    public FixTextWidth(doc: jsPDF, x: number, x_counter: number, y: number, data: string): number {
        let len = data.length;
        let strArray: string[] = [];
        let start = 0;
        for (let i: number = 0; i <= len; (i = i + x_counter)) {
            let testData = data.substr(i, x_counter);
            doc.text(x, y, testData);
            y = y + 5;
        }
        return y;
    }

