  public convertSystemDateToReportDate(date: string): string {
    var t_date = date.split('-');
    var final_date = t_date[2].split('T')[0] + " " + this.getCurrentMonth((parseInt(t_date[1])) - 1) + " " + t_date[0];
    return final_date;
  }

  public getTodaysDate(): string {
    var date = new Date();
    return date.toDateString() + " " + date.getHours() + ":" + date.getMinutes();
  }
  //Method to return the Month in the Text.
  public getCurrentMonth(i: number): string {
    var month = "";
    switch (i) {
      case 0:
        month = "JAN";
        break;
      case 1:
        month = "FEB";
        break;
      case 2:
        month = "MAR";
        break;
      case 3:
        month = "APR";
        break;
      case 4:
        month = "MAY";
        break;
      case 5:
        month = "JUN";
        break;
      case 6:
        month = "JUL";
        break;
      case 7:
        month = "AUG";
        break;
      case 8:
        month = "SEPT";
        break;
      case 9:
        month = "OCT";
        break;
      case 10:
        month = "NOV";
        break;
      case 11:
        month = "DEC";
        break;
    }
    return month;
  }


