
export class OtherIncomeMasterModel{
    public Id: number;
    public  VOId: number;
    public Amount: number;
    public SHGId : number;
    public PaidDate: any;
    public PayeeName: any;
    public IsSHGMember : boolean;
    public CreatedBy: any;
    public CreatedDate : any;
    public UpdatedBy : any
    public  UpdatedDate : any
    public IsDeleted : number
    public IncomeSource : any;
    public Reason : any;
    public BankPhotoUrl: any;
    public CheckPhotoUrl : any;
    public OtherIncomeSourceBankReceipt : number;
    public OtherIncomeSourceCheckReceipt : number;

    constructor(){
        this.Id = 0;
        this.VOId = 0;
        this.Amount = 0;
        this.SHGId = 0;
        this.PaidDate = '';
        this.PayeeName = '';
        this.IsSHGMember = false;
        this.CreatedBy = '';
        this.CreatedDate = '';
        this.UpdatedBy = '';
        this.UpdatedDate = '';
        this.IsDeleted = 0;
        this.IncomeSource = 0;
        this.Reason = '';
        this.BankPhotoUrl = '';
        this.CheckPhotoUrl = '';
        this.OtherIncomeSourceBankReceipt = 0;
        this.OtherIncomeSourceCheckReceipt = 0;

    }

}


