    // Create New Member
    public memberform: NgForm;
    create(data: NgForm, metadata: any) {
        // alert("add called");
        debugger;
        this.memberform = data;
        let age: number = this.calculateAge(data.value.DateOfBirth);
        this._globalURL.isLoading = true;
        data.value.Code = this.nextCode;
        data.value.SHGId = sessionStorage.getItem("shgId");
        data.value.Code = this.nextCode;
        data.value.ParentCode = this.globalVariable.shgBaseCode;
        data.value.Age = age;
        this._voService.post(this._globalURL.POST_CREATE_MEMBER,
            data.value, sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.status === 401) {
                    sessionStorage.clear();
                    this.showErrorMessage = true;
                    this.message = this._common_message.error401message;
                    setInterval(() => {
                        this.showErrorMessage = false;
                    }, 5000);
                    this._globalURL.isLoading = false;
                    this._router.navigate(['../login']);
                }
                if (res.Status == undefined) {
                    if (res.status == 200) {
                        var data = JSON.parse(res._body);
                        // sessionStorage.getItem("access_token") = data.access_token;
                        var formData = new FormData();
                        formData.append("RefId", data.Data);
                        formData.append("file", metadata);
                        this._fileUploadService.post((this._globalURL.ADD_PHOTO + "?folderName=Member" + "&type=" + this._globalURL.MEM_PHOTO + "&RefId=" + data.Data),
                            formData, sessionStorage.getItem("access_token")).then((res) => {
                                if (res.Status == undefined) {
                                    if (res.status == 200) {
                                        this.memberform.control.reset();
                                        this.showMessage = true;
                                        this.message = this._common_message.MemberCreatedMsg;
                                        setInterval(() => {
                                            this.showMessage = false;
                                        }, 5000);
                                    }
                                }
                                else {
                                    this.showWarningMessage = true;
                                    this.message = this._common_message.WarningMsg;
                                    setInterval(() => {
                                        this.showWarningMessage = false;
                                    }, 5000);
                                    this._globalURL.isLoading = false;
                                }
                            }).catch((res) => {
                                this.showErrorMessage = true;
                                console.log(res.error);
                                this.message = this._common_message.CatchBlockMsg;
                                setInterval(() => {
                                    this.showErrorMessage = false;
                                }, 5000);
                                this._globalURL.isLoading = false;
                            })
                        this.loadSHGNameAndId();
                        this.getMemberDetails(sessionStorage.getItem("shgId"));
                        this._globalURL.isLoading = false;

                    }
                }
                else {
                    if (res.Status == true) {
                        this.loadSHGNameAndId();
                        this.getMemberDetails(sessionStorage.getItem("shgId"));
                        this._globalURL.isLoading = false;
                    }
                    else {
                        this.showWarningMessage = true;
                        this.message = this._common_message.WarningMsg;
                        setInterval(() => {
                            this.showWarningMessage = false;
                        }, 5000);
                        this._globalURL.isLoading = false;
                    }
                }
            }).catch((res) => {
                this.showErrorMessage = true;
                console.log(res.error);
                this.message = this._common_message.CatchBlockMsg;
                setInterval(() => {
                    this.showErrorMessage = false;
                }, 5000);
                this._globalURL.isLoading = false;
            })
    }

    calculateAge(dob: string): number {
        let getCurrentYear: number = new Date().getFullYear();
        let dateObj = new Date();
        let dobYear = dob.split('-')[0];
        let age = getCurrentYear - parseInt(dobYear);
        return age;
    }

