

import { Component, OnInit } from '@angular/core';
import { constructor } from 'events';
import { Router } from '@angular/router';
import { AccessToken } from '../../../environments/access_token';
import { GlobalAssets } from '../../../environments/Global';
import { CMRCService } from '../../../services/cmrc.services';
import { ExportToPDF } from '../../../services/reports/exportpdf';
import { ExportToExcel } from '../../../services/reports/exportexcel';

@Component({
    templateUrl: '../reports/shg_report.component.html',
})

export class SHGReportsComponent implements OnInit {
    public shgsKvp: any;
    public shgs: any;
    public ShgsByCmrc: any;
    public Shgs: any;
    public Vos: any;
    public AllVos: any;
    public voId: number;
    public shgByCmrcId: boolean;
    public shgByVoId: boolean;
    public selectedVoId: number;
    constructor(public _cmrcservice: CMRCService,
        public _router: Router,
        public _access_token: AccessToken,
        public exportToPDF: ExportToPDF,
        public  exportToExcel: ExportToExcel,
        public _globalURL: GlobalAssets) {
        _globalURL.isLoading = true;

        if (sessionStorage.getItem("access_token") == ""){
            _router.navigate(['../login']);
        }
        this.shgByCmrcId = true;
        this.shgByVoId = false;
    }
    ngOnInit() {
        // this.voId;
        this.getShgsByCmrc();
        this.getVoIdAndName();
    }

    getVoIdAndName(): void {
        this._globalURL.isLoading = true;
        debugger;
        this._cmrcservice.get(this._globalURL.GET_VO_CMRCID, sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.Status == true) {
                    this.Vos = res.Data;
                    this._globalURL.isLoading = false;
                }
                else {
                    setInterval(() => {
                        this._globalURL.isSaveSuccessMessage = false;
                        // this.ShowErrormsg = false;
                    }, 5000);
                }
            });
    }

    getShgsByCmrc(): void {
        this.shgByCmrcId = true;
        this.shgByVoId = false;
        debugger;
        this._globalURL.isLoading = true;
        this._cmrcservice.get(this._globalURL.GET_SHG_BYCMRC, sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.Status == true) {
                    this.ShgsByCmrc = res.Data;
                    this._globalURL.isLoading = false;
                }
                else {
                    setInterval(() => {
                        this._globalURL.isSaveSuccessMessage = false;
                    }, 5000);
                }
            });
    }

    getShgsByVo(voId: number): void {
        this.shgByVoId = true;
        this.shgByCmrcId = false;
        debugger;
        this._globalURL.isLoading = true;
        debugger;
        this._cmrcservice.get(this._globalURL.GET_SHGS_BYVO + voId, sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.Status == true) {
                    this.Shgs = res.Data;
                    this._globalURL.isLoading = false;
                }
                else {
                    setInterval(() => {
                        this._globalURL.isSaveSuccessMessage = false;
                    }, 5000);
                }
            });
    }

    getShgNameAndId(voID: any): void {
        //  let id = data.voId;
        this._globalURL.isLoading = true;
        debugger;
        this._cmrcservice.get((this._globalURL.GET_SHGNAME_ID_VOID + "" + voID), sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.Status == true) {
                    this.shgsKvp = res.Data;
                    this._globalURL.isLoading = false;
                }
                else {
                    this._globalURL.isLoading = false;
                }
            });
    }

    R33_shgByCmrcDetailsExportToPdf() {
        this._globalURL.isLoading = true;
        this.exportToPDF.R33_shgByCmrcDetailsExportToPdf(this.ShgsByCmrc);
        this._globalURL.isLoading = false;
    }
    R34_shgByVoExportTopdf() {
        this._globalURL.isLoading = true;
        debugger;
        this.exportToPDF.R34_shgByVoExportTopdf(this.Shgs);      
        this._globalURL.isLoading = false;
    }

    public R34_ShgViewExportTopdf(data: any){
        this._globalURL.isLoading = true;
        this.exportToPDF.R34_ShgViewExportTopdf(data);
        this._globalURL.isLoading = false;
    }
     
    public R34_ShgByVoViewExportTopdf(data: any){
        this._globalURL.isLoading = true;
       debugger;
        this.exportToPDF.R34_ShgByVoViewExportTopdf(data);        
        this._globalURL.isLoading = false;
    }

    public shgByCmrcExcel(){
        this._globalURL.isLoading = true;
        this.exportToExcel.shgByCmrcExcel(this.ShgsByCmrc);
        this._globalURL.isLoading = false;
    }
    
    public shgByVoExcel(){
        debugger;
        this._globalURL.isLoading = true;
        this.exportToExcel.shgByVoExcel(this.Shgs);
    }
    
}







































































