import { Component, OnInit, ViewChild, AfterViewInit } from '@angular/core';
import { constructor } from 'events';
import { Router } from '@angular/router';
import { AccessToken } from '../../../environments/access_token';
import { GlobalAssets } from '../../../environments/Global';
import { CMRCService } from '../../../services/cmrc.services';
import { GlobalVariable } from '../../../environments/GlobalVeriables';
import { LangulageConf } from '../../../environments/language.config';
import { Subject } from 'rxjs';
import { DataTableDirective } from 'angular-datatables';
import { CommonMessageComponent } from '../../../environments/CommanMssage';

@Component({
    templateUrl: '../ration_card/ration_card.component.html',
    styleUrls: ['../../../components/app.css']
})

export class RationcardComponent implements OnInit, AfterViewInit {
    showErrorMessage: boolean;
    @ViewChild(DataTableDirective)
    dtElement: DataTableDirective;
    dtInstance: DataTables.Api;
    public dtTrigger: Subject<any> = new Subject();
    public ShowErrormsg: boolean;
    public RationCardModel: any;
    public getRationCardDetails: any;
    public message: string;
    public showMessage: boolean;

    constructor(public _cmrcservice: CMRCService,
        public _router: Router,
        public lang: LangulageConf,
        public _access_token: AccessToken,
        public _globalURL: GlobalAssets,
        public _globalVariable: GlobalVariable,
        public _common_message: CommonMessageComponent
    ) {
        _globalURL.isLoading = true;
        if (sessionStorage.getItem("access_token") == "") {
            _router.navigate(['../login']);
        }
    }

    ngOnInit() {
        this.getRationCard();
    }
    ngAfterViewInit(): void {
        this.dtTrigger.next();
        this.getRationCard();
    }

    //GET AREA AND ...
    getRationCard(): void {
        this._globalURL.isLoading = true;
        debugger;
        this._cmrcservice.get(this._globalURL.GET_RETION_CARD_DETAILS, sessionStorage.getItem("access_token"))
            .then((res) => {
                this.reRenderDT();
                if (res.Status == true) {
                    this.getRationCardDetails = res.Data;
                    this._globalURL.isLoading = false;
                }
                else {
                    this.ShowErrormsg = true;
                    this.message = "Record is not get";
                    setInterval(() => {
                        this._globalURL.isSaveSuccessMessage = false;
                        this.ShowErrormsg = false;
                    }, 5000);
                }              
            }).catch((res) => {
                this._globalURL.isLoading = false;
                this.message = this._common_message.CatchBlockMsg;
                this.showErrorMessage = true;
                setInterval(() => {
                    this.showErrorMessage = false;
                }, 5000);
            })
    }

    createRationCard(data: any): void {
        this._globalURL.isLoading = true;
        debugger;
        let rationCard = data.RationCardName;
        this._cmrcservice.post((this._globalURL.CREATE_RETION_CARD + rationCard), data, sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.Status == undefined)
                    if (res.status == 200) {
                        let data_ = JSON.parse(res._body);
                        if (data_.Status == true) {
                            this._globalURL.isLoading = false;
                            this.showMessage = true;
                            this.message = this._common_message.AddRationCardMsg;
                            setInterval(() => {
                                this.showMessage = false;
                            }, 5000);
                            this.getRationCard();
                        }
                        else {
                            this._globalURL.isLoading = false;
                            this.showErrorMessage = true;
                            this.message = this._common_message.ErrorAddRationCardMsg;
                            setInterval(() => {
                                this.showErrorMessage = false;
                            }, 5000);
                            this.getRationCard();
                        }
                    }
                    else {
                        this._globalURL.isLoading = false;
                        this.showErrorMessage = true;
                        this.message = this._common_message.ErrorAddRationCardMsg;
                        setInterval(() => {
                            this.showErrorMessage = false;
                        }, 5000);
                    }
                else {
                    if (res.status == 200) {
                        this._globalURL.isLoading = false;
                        this.showMessage = true;
                        this.message = this._common_message.AddRationCardMsg;
                        setInterval(() => {
                            this.showMessage = false;
                        }, 5000);
                        this.getRationCard();
                    }
                    else {
                        this._globalURL.isLoading = false;
                        this.showErrorMessage = true;
                        this.message = this._common_message.ErrorAddRationCardMsg;
                        setInterval(() => {
                            this.showErrorMessage = false;
                        }, 5000);
                    }
                }
            }).catch((err) => {
                this._globalURL.isLoading = false;
                this.message = this._common_message.CatchBlockMsg;
                this.showErrorMessage = true;
                setInterval(() => {
                    this.showErrorMessage = false;
                }, 5000);
            })
    }

    delete(data: any) {
        debugger;
        this.RationCardModel = data;
        debugger;
    }

    deleteRationCard(data: any): void {
        this._globalURL.isLoading = true;
        debugger;
        let id = data.Id;
        let final = this._globalURL.DELET_RETION_CARD + id;
        this._cmrcservice.post(final, "data", sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.Status == undefined) {
                    var data = JSON.parse(res._body);
                    if (data.Status == true) {
                        this._globalURL.isLoading = false;
                        this.showMessage = true;
                        this.message = this._common_message.DeleteRationCardMsg;
                        setInterval(() => {
                            this.showMessage = false;
                        }, 5000);
                        this.getRationCard();
                    }
                    else {
                        this._globalURL.isLoading = false;
                        this.showErrorMessage = true;
                        this.message = this._common_message.ErrorDeleteRationCardMsg;
                        setInterval(() => {
                            this.showErrorMessage = false;
                        }, 5000);
                        this.getRationCard();
                    }
                }
                else {
                    if (res.Status == true) {
                        this._globalURL.isLoading = false;
                        this.showMessage = true;
                        this.message = this._common_message.DeleteRationCardMsg;
                        setInterval(() => {
                            this.showMessage = false;
                        }, 5000);
                        this.getRationCard();
                    }
                    else {
                        this._globalURL.isLoading = false;
                        this.showErrorMessage = true;
                        this.message = this._common_message.ErrorDeleteRationCardMsg;
                        setInterval(() => {
                            this.showErrorMessage = false;
                        }, 5000);
                        this.getRationCard();
                    }
                }
            }).catch((err) => {
                this._globalURL.isLoading = false;
                this.message = this._common_message.CatchBlockMsg;
                this.showErrorMessage = true;
                setInterval(() => {
                    this.showErrorMessage = false;
                }, 5000);
            })
    }

    reRenderDT() {
        this.dtElement.dtInstance.then((dtInstance: DataTables.Api) => {
            dtInstance.destroy();
            this.dtTrigger.next();
        });
    }
}













