import { Component, OnInit, ViewChild, AfterViewInit } from '@angular/core';
import { CurrencyPipe } from '@angular/common';
import { constructor } from 'events';
import { Router } from '@angular/router';
import { AccessToken } from '../../../environments/access_token';
import { GlobalAssets } from '../../../environments/Global';
import { CMRCService } from '../../../services/cmrc.services';
import { GlobalVariable } from '../../../environments/GlobalVeriables';
import { LangulageConf } from '../../../environments/language.config';
import { Subject } from 'rxjs';
import { DataTableDirective } from 'angular-datatables';

@Component({
    templateUrl: '../memShare/memShare.component.html',
    styleUrls: ['../../../components/app.css']


})

export class MemShareComponent implements OnInit, AfterViewInit {
    // @ViewChild(DataTableDirective)
    // dtElement: DataTableDirective;
    // dtInstance: DataTables.Api;
    // public dtTrigger: Subject<any> = new Subject();
    public MemberId: string;
    public shareAmount: string;
    public memberId: string;
    public memberShareModel: any;
    public showMessage: boolean;
    public message: string;
    public ShowErrormsg: boolean;
    public selectedShgId: number;
    public selectedVoId: number;
    public shgs: any;
    public Vos: any;
    public memShares: any;

    // public voId: any;
    constructor(public _cmrcservice: CMRCService,
        public _router: Router,
        public lang: LangulageConf,
        public _access_token: AccessToken,
        public _globalURL: GlobalAssets,
        public _globalVariable: GlobalVariable,
        public _cmrcService: CMRCService,
    ) {
        // this.selectedVoId = ;
        _globalURL.isLoading = true;
        if (sessionStorage.getItem("access_token") == "") {
            _router.navigate(['../login']);
        }
        this.memberShareModel = null;
    }

    ngOnInit() {
        this.getVoIdAndName();
    }

    ngAfterViewInit(): void {
        //this.dtTrigger.next();
        this.getMemShareDetails();
    }

    //Get member details ...
    public shgId = this.selectedShgId;
    public voId = this.selectedVoId;
    getMemShareDetails(): void {
        this._globalURL.isLoading = true;
        debugger;
        this.memShares = null;
        this._cmrcservice.get((this._globalURL.GET_SHARE_AMOUNT + "?voId=" + this.selectedVoId + "&shgId=" + this.selectedShgId),
            sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.Status == true) {
                    this.memShares = res.Data;
                    this._globalURL.isLoading = false;
                }
                else {
                    this.ShowErrormsg = true;
                    this.message = "THERE IS NO MEMBER IN SELECTED SHG WHO HAS SHARE AMOUNT PENDING";
                    setInterval(() => {
                        //this._globalURL.isSaveSuccessMessage = false;
                        this.ShowErrormsg = false;
                    }, 5000);
                }
                //this.reRenderDT();
            });
    }

    //GET vo in dropdown ...
    getVoIdAndName(): void {
        this._globalURL.isLoading = true;
        debugger;
        this._cmrcservice.get(this._globalURL.GET_VO_CMRCID, sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.Status == true) {
                    this.Vos = res.Data;
                    this._globalURL.isLoading = false;
                }
                else {
                    this.ShowErrormsg = true;
                    this.message = "Vo is not get";
                    setInterval(() => {
                        this._globalURL.isSaveSuccessMessage = false;
                        this.ShowErrormsg = false;
                    }, 5000);
                }
            });
    }

    //get shg dropdown..
    getShgNameAndId(voID: any): void {
        //  let id = data.voId;
        this._globalURL.isLoading = true;
        debugger;
        // let finalUrl = this._globalURL.GET_SHGNAME_ID_VOID + " " + id
        this._cmrcservice.get((this._globalURL.GET_SHGNAME_ID_VOID + "" + voID), sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.Status == true) {
                    this.shgs = res.Data;
                    this._globalURL.isLoading = false;
                }
                else {
                    this._globalURL.isLoading = false;
                }
            });
    }

    delete(data: any) {
        debugger;
        this.memberShareModel = data;
        debugger;
    }

    returnMemberShare(data: any): void {
        data.Status = 2;
        this._globalURL.isLoading = true;
        debugger;
        //let id = data.Id;
        let final = this._globalURL.RETURN_SHARE_AMOUNT + "?memberId=" + data.Id + "&shareAmount=" + data.ShareAmount + "&Status=" + data.Status;
        this._cmrcservice.post(final, "data", sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.Status == undefined) {
                    if (res.status == 200) {
                        this._globalURL.isLoading = false;
                        this.showMessage = true;
                        this.message = "Record is Delete succesfully";
                        setInterval(() => {
                            this.showMessage = false;
                        }, 5000);
                        this.getMemShareDetails();
                    }
                    else {
                        this.ShowErrormsg = true;
                        this.message = "Record is not delete";
                        setInterval(() => {
                            this._globalURL.isSaveSuccessMessage = false;
                            this.ShowErrormsg = false;
                        }, 5000);
                        this.getMemShareDetails();
                    }
                }
                else {
                    if (res.Status == true) {
                        this._globalURL.isLoading = false;
                        this.showMessage = true;
                        this.message = "Record is Delete succesfully";
                        setInterval(() => {
                            this.showMessage = false;
                        }, 5000);
                        this.getMemShareDetails();
                    }
                    else {
                        this.ShowErrormsg = true;
                        this.message = "Record is not delete";
                        setInterval(() => {
                            this._globalURL.isSaveSuccessMessage = false;
                            this.ShowErrormsg = false;
                        }, 5000);
                        this.getMemShareDetails();
                    }
                }
                this._globalURL.isLoading = false;
            })
            .catch((err) => {
                // alert("sooory");
                setInterval(() => {
                }, 5000);
                this.getMemShareDetails();
            })
    }
    // reRenderDT() {
    //     this.dtElement.dtInstance.then((dtInstance: DataTables.Api) => {
    //         dtInstance.destroy();
    //         this.dtTrigger.next();
    //     });
    // }
}


