import { Component, Input } from '@angular/core';

//import{} from '../../'

@Component({
    selector: 'loading-selector',
    template: `<div  class="loader-wp text-center">
    <div class="loader-inner">                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                   
    <img src="/assets/pdf/loadwait.gif" class="image-set" style="position: fixed;top:50%;height:120px;margin:0 auto;left:47%;width:124px;z-index:99999">
    </div>    
</div>`
})
export class LoadingComponent {
    constructor() {
    
    }
}


@Component({
    selector: 'message-selector',
    styleUrls: ['../components/app.css'],
    template: `<div class="Registration_Success">
                <p class="SuccessMessage">
                <i class="fa fa-check-square-o" aria-hidden="true"></i> {{message}}
                </p>
               </div>`
})
export class MessageComponent {
    // public msg: any;
    @Input() message: string;
    constructor() {
    }
} 

@Component({
    selector: 'error-message-selector',
    styleUrls: ['../components/app.css'],
    template: `<div class="Error-Message ">
                <p class="Error_message">
                    {{message}}
                </p>
               </div>`
})
export class ErrorMessageComponent {
    // public msg: any;
    @Input() message: string;
    constructor() {
    }
} 

@Component({
    selector: 'warning-message-selector',
    styleUrls: ['../components/app.css'],
    template: `<div class="Warning-Message">
                <p class="Warning_message">
                    {{message}}
                </p>
               </div>`
})
export class WarningMessageComponent {
    // public msg: any;
    @Input() message: string;
    constructor() {
    }
} 


