import { Component, OnInit, ViewChild, AfterViewInit } from '@angular/core';
import { constructor } from 'events';
import { Router } from '@angular/router';
import { AccessToken } from '../../../environments/access_token';
import { GlobalAssets } from '../../../environments/Global';
import { CMRCService } from '../../../services/cmrc.services';
import { GlobalVariable } from '../../../environments/GlobalVeriables';
import { LangulageConf } from '../../../environments/language.config';
import { DataTableDirective } from 'angular-datatables';
import { Subject } from 'rxjs';
import { CommonMessageComponent } from '../../../environments/CommanMssage';
import { NgForm } from '@angular/forms';

@Component({
    templateUrl: '../area/area.component.html'
})

export class AreaComponent implements OnInit, AfterViewInit {
    public showWarningMessage: boolean;
    public showErrorMessage: boolean;
    @ViewChild(DataTableDirective)
    dtElement: DataTableDirective;
    dtInstance: DataTables.Api;
    public dtTrigger: Subject<any> = new Subject();
    public ShowErrormsg: boolean;
    public areaModel: any;
    public message: string;
    public showMessage: boolean;
    public getAreas: any;
    constructor(public _cmrcservice: CMRCService,
        public _router: Router,
        public _access_token: AccessToken,
        public lang: LangulageConf,
        public _globalURL: GlobalAssets,
        public _globalVariable: GlobalVariable,
        public _common_message: CommonMessageComponent,
    ) {
        _globalURL.isLoading = true;
        if (sessionStorage.getItem("access_token") == "") {
            _router.navigate(['../login']);
        }
    }

    ngOnInit() {
        //this.getArea();
    }
    ngAfterViewInit() {
        this.dtTrigger.next();      
        this.getArea();
    }

    //GET AREA  ...
    getArea(): void {
        this._globalURL.isLoading = true;
        debugger;
        this._cmrcservice.get(this._globalURL.GET_AREA_DETAILS, sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.status === 401) {
                    sessionStorage.clear();
                    this.showErrorMessage = true;
                    this.message = this._common_message.error401message;
                    setInterval(() => {
                        this.showErrorMessage = false;
                    }, 5000);
                    this._globalURL.isLoading = false;
                    this._router.navigate(['../login']);
                }
                if (res.Status == true) {
                    this.getAreas = res.Data;
                    this._globalURL.isLoading = false;
                }
                else {
                    this.showWarningMessage = true;
                    this.message = this._common_message.WarningMsg;
                    setInterval(() => {
                        this.showWarningMessage = false;
                    }, 5000);
                    this._globalURL.isLoading = false;
                }
                this.reRenderDT();
            }).catch((res) => {
                this._globalURL.isLoading = false;
                this.message = this._common_message.CatchBlockMsg;
                this.showErrorMessage = true;
                setInterval(() => {
                    this.showErrorMessage = false;
                }, 5000);
            });
    }

    //create area...
    createArea(Data1: NgForm): void {
        this._globalURL.isLoading = true;
        this._cmrcservice.post((this._globalURL.CREATE_AREA + Data1.value.AreaName), null, sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.status === 401) {
                    sessionStorage.clear();
                    this.showErrorMessage = true;
                    this.message = this._common_message.error401message;
                    setInterval(() => {
                        this.showErrorMessage = false;
                    }, 5000);
                    this._globalURL.isLoading = false;
                    this._router.navigate(['../login']);
                }
                if (res.Status == undefined) {
                    if (res.status == 200) {
                        let data_ = JSON.parse(res._body);
                        if (data_.Status == true) {
                            this._globalURL.isLoading = false;
                            this.showMessage = true;
                            this.message = this._common_message.AddAreaMsg;
                            setInterval(() => {
                                this.showMessage = false;
                            }, 5000);
                            this.getArea();
                            Data1.control.reset();
                        }
                        else {
                            this.showErrorMessage = true;
                            this.message = this._common_message.NotAreaAddMsg;
                            setInterval(() => {
                                this.showErrorMessage = false;
                            }, 5000);
                            this._globalURL.isLoading = false;

                        }
                    }
                }
                else {
                    if (res.status == 200) {
                        this._globalURL.isLoading = false;
                        this.showMessage = true;
                        this.message = this._common_message.AddAreaMsg;
                        setInterval(() => {
                            this.showMessage = false;
                        }, 5000);
                        this.getArea();
                    }
                    else {
                        this.showErrorMessage = true;
                        this.message = this._common_message.NotAreaAddMsg;
                        setInterval(() => {
                            this.showErrorMessage = false;
                        }, 5000);
                        this._globalURL.isLoading = false;
                    }
                }
            })
            .catch((err) => {
                this._globalURL.isLoading = false;
                this.message = this._common_message.CatchBlockMsg;
                this.showErrorMessage = true;
                setInterval(() => {
                    this.showErrorMessage = false;
                }, 5000);
            })
    }
    //Delete Area
    delete(data: any) {
        debugger;
        this.areaModel = data;
        debugger;
    }

    deleteArea(data: any): void {
        this._globalURL.isLoading = true;
        debugger;
        let id = data.Id;
        let final = this._globalURL.DELETE_AREA + id;
        this._cmrcservice.post(final, "data", sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.status === 401) {
                    sessionStorage.clear();
                    this.showErrorMessage = true;
                    this.message = this._common_message.error401message;
                    setInterval(() => {
                        this.showErrorMessage = false;
                    }, 5000);
                    this._globalURL.isLoading = false;
                    this._router.navigate(['../login']);
                }
                if (res.Status == undefined) {
                    var data = JSON.parse(res._body);
                    if (data.Status == true) {
                        this.message = this._common_message.DeleteAreaMsg;
                        this.showMessage = true;
                        setInterval(() => {
                            this.showMessage = false;
                        }, 5000);
                        this._globalURL.isLoading = false;
                        this.getArea();
                    }
                    else {
                        this.showErrorMessage = true;
                        this.message = this._common_message.AreaNotDeleteMsg;
                        setInterval(() => {
                            this.showErrorMessage = false;
                        }, 5000);
                        this._globalURL.isLoading = false;
                    }
                }
                else {
                    if (res.Status == true) {
                        this.message = this._common_message.DeleteAreaMsg;
                        this.showMessage = true;
                        setInterval(() => {
                            this.showMessage = false;
                        }, 5000);
                        this._globalURL.isLoading = false;
                        this.getArea();
                    }
                    else {
                        this.showErrorMessage = true;
                        this.message = this._common_message.AreaNotDeleteMsg;
                        setInterval(() => {
                            this.showErrorMessage = false;
                        }, 5000);
                        this._globalURL.isLoading = false;
                    }
                }
            }).catch((err) => {
                this._globalURL.isLoading = false;
                this.message = this._common_message.CatchBlockMsg;
                this.showErrorMessage = true;
                setInterval(() => {
                    this.showErrorMessage = false;
                }, 5000);
            })
    }
    reRenderDT() {
        this.dtElement.dtInstance.then((dtInstance: DataTables.Api) => {
            dtInstance.destroy();
            this.dtTrigger.next();
        });
    }
}
