import { Component, OnInit } from '@angular/core';
import { VOService } from '../../services/vo.services';
import { EmpModel } from '../../services/empModel';
import { GlobalAssets } from '../../environments/Global';
import { LangulageConf } from '../../environments/language.config';
//import {} from '../vo'
@Component({
    templateUrl: '../sc/index.html'
})

export class SCBaseComponent implements OnInit {
    public today :any;
    public selectLang: string = "en";
    constructor(public _voService: VOService
        , public _globalURL: GlobalAssets,
        public lang: LangulageConf) {
            this.today = new Date();
    }
    ngOnInit() {

    }
    setLang(lang_) {
        if (lang_ === "en") {
            this.lang.en = true;
            this.lang.mr = false;
        }
        if (lang_ === "mr") {
            this.lang.mr = true;
            this.lang.en = false;
        }
        debugger;
    }

    SignOut(){
       
        sessionStorage.clear();
    }
}


