

export class MemberShareMasterModel{

    public Id : number;
    public DistrictUserId : number;
    public DistrictUserName : any;
    public CMRCId : number;
    public VOId : number;
    public SHGId : number;
    public MemberId : number;
    public MemberFullName : any;
    public ShareAmount :number;
    public ShareAmountDate : any;
    public ShareReturnDate : any;
    public Status : any;

    constructor(){
        this.Id = 0;
        this.DistrictUserId = 0;
        this.DistrictUserName = '';
        this.CMRCId = 0;
        this.VOId = 0;
        this.SHGId = 0;
        this.MemberId = 0;
        this.MemberFullName = '';
        this.ShareAmount = 0;
        this.ShareAmountDate = '';
        this.ShareReturnDate = '';
        this.Status = '';
    }

}


