import { Component } from "@angular/core";
import { GlobalAssets } from "../../../environments/Global";
import { LangulageConf } from '../../../environments/language.config';
import { DISTRICTService } from "../../../services/district.services";
import { AccessToken } from "../../../environments/access_token";
import { Router } from "@angular/router";
import { CommonService } from "../../../services/common.service";
import { GlobalVariable } from "../../../environments/GlobalVeriables";
import { FundMasterModel } from "./fund.model.component";
import { DatepickerOptions } from 'ng2-datepicker';
import * as frLocale from 'date-fns/locale/en';
import { CommonMessageComponent } from "../../../environments/CommanMssage";
import { NgForm } from "@angular/forms";


@Component({
    templateUrl: '../fund/fund.component.html',
    styleUrls: ['../../../components/app.css']
})

export class DistrictFundComponent {
    fundDetail: any;
    public showType1: boolean = true;
    public showType2: boolean = true;
    public updateapprove: boolean;
    public fundTypeId: number;
    public fundModel: any;
    public message: string;
    public showMessage: boolean;  
    public ApprovedDate : Date = new Date(); 
    options: DatepickerOptions = {
        minYear: 2016,
        maxYear: (new Date().getFullYear() + 1),
        displayFormat: 'MMM D[,] YYYY',
        barTitleFormat: 'MMMM YYYY',
        dayNamesFormat: 'dd',
        firstCalendarDay: 0, // 0 - Sunday, 1 - Monday
        locale: frLocale,
        //minDate: new Date(Date.now()), // Minimal selectable date
        minDate: new Date("2016-03-03"),
        //maxDate: new Date(Date.now()),  // Maximal selectable date
        barTitleIfEmpty: 'Click to select a date'
    };
   
    constructor(public districtService: DISTRICTService,
        public _globalURL: GlobalAssets,
        public _access_Token: AccessToken,
        public _router: Router,
        public lang: LangulageConf,
        public _commonService: CommonService,
        public _commonMessage: CommonMessageComponent,
        public _globalVariable: GlobalVariable,
        public fundData: FundMasterModel
    ) {
        this.fundTypeId = 1;
        if (sessionStorage.getItem("access_token") == "")
        _router.navigate(['../login']);
    }

    ngOnInit() {
        this.getFund(1);
    }

    getFund(status: number) {
        debugger;
        this.fundTypeId = status;
        if (status == 1) {
            this.showType1 = true;
            this.showType2 = false;
        }
        if (status == 2) {

            this.showType2 = true;
            this.showType1 = false;
        }
        this.districtService.get((this._globalURL.GET_DISTRICT_FUND_DETAILS + status), sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.Status == true) {
                    this.fundModel = res.Data;
                    this._globalURL.isLoading = false;
                }
                else {
                    this._globalURL.isLoading = false
                }
            });
    }


    public setUpdate(fundDetail: any) {
        this.fundData = fundDetail;
        if(this.fundTypeId == 1){
            this.showType1 = true;
            this.showType2 = false;
        }
        if(this.fundTypeId == 2){
            this.showType2 = true;
            this.showType1 = false;
        }

    }
    public confirmUpdate(Data: NgForm) {     
       // let data = this.fundData;         
      debugger;
      Data.value.Id = this.fundData.Id;
      var updata={
      } 
    // data.ApprovedDate = this._commonService.convertToDate(frDt); 
    //   let ApprovedDate = this._commonService.convertToDate(frDt);
        this.districtService.post(this._globalURL.UPDATE_DISTRICT_FUND_DETAILS, Data.value, sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.Status == 401) {     
                }
                if ((res.status) == undefined) {
                  
                }
                else {
                    if (res.status === 200) {    
                        Data.control.reset()                 
                        this._globalURL.isLoading = false;
                        this.message = this._commonMessage.UpdateMsg;
                        this.showMessage = true;
                        setInterval(() => {
                            this.showMessage = false;
                        }, 5000);
                        this.getFund(1);
                    }
                    else {

                    }
                }
                   
            }).catch((res) => {

            })

    }
}


