import { Component, OnInit, ViewChild, AfterViewInit } from '@angular/core';
import { constructor } from 'events';
import { Router } from '@angular/router';
import { AccessToken } from '../../../environments/access_token';
import { GlobalAssets } from '../../../environments/Global';
import { CMRCService } from '../../../services/cmrc.services';
import { GlobalVariable } from '../../../environments/GlobalVeriables';
import { LangulageConf } from '../../../environments/language.config';
import { DataTableDirective } from 'angular-datatables';
import { Subject } from 'rxjs';
import { CommonMessageComponent } from '../../../environments/CommanMssage';
import { NgForm } from '@angular/forms';

@Component({
    templateUrl: '../economic_category/economic_category.component.html',
    styleUrls: ['../../../components/app.css']
})

export class EconomiccategoryComponent implements OnInit, AfterViewInit {
    showWarningMessage: boolean;
    public showErrorMessage: boolean;
    @ViewChild(DataTableDirective)
    dtElement: DataTableDirective;
    dtInstance: DataTables.Api;
    public dtTrigger: Subject<any> = new Subject();
    public ShowErrormsg: boolean;
    public EconomicCategoryModel: any;
    public getEconomicCategorys: any;
    public getSocialCategorys: any;
    public SocialCategoryModel: any;
    public getOccupations: any;
    public message: string;
    public showMessage: boolean;

    constructor(public _cmrcservice: CMRCService,
        public _router: Router,
        public lang: LangulageConf,
        public _access_token: AccessToken,
        public _globalURL: GlobalAssets,
        public _globalVariable: GlobalVariable,
        public _common_message: CommonMessageComponent
    ) {
        _globalURL.isLoading = true;
        if (sessionStorage.getItem("access_token") == "") {
            _router.navigate(['../login']);
        }
    }
    ngOnInit() {
        // this.getEconomicCategory();
    }

    ngAfterViewInit(): void {
        this.dtTrigger.next();
        this.getEconomicCategory();

    }
    //GET AREA AND ...
    getEconomicCategory(): void {
        this._globalURL.isLoading = true;
        debugger;
        this._cmrcservice.get(this._globalURL.GET_ECONOMIC_CATEGORY_DETAILS, sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.status === 401) {
                    sessionStorage.clear();
                    this.showErrorMessage = true;
                    this.message = this._common_message.error401message;
                    setInterval(() => {
                        this.showErrorMessage = false;
                    }, 5000);
                    this._globalURL.isLoading = false;
                    this._router.navigate(['../login']);
                }
                if (res.Status == true) {
                    this.getEconomicCategorys = res.Data;
                    this._globalURL.isLoading = false;
                }
                else {
                    this.showWarningMessage = true;
                    this.message = this._common_message.WarningMsg;
                    setInterval(() => {
                        this.showWarningMessage = false;
                    }, 5000);
                    this._globalURL.isLoading = false;
                }
                this.reRenderDT();
            }).catch((res) => {
                this._globalURL.isLoading = false;
                this.message = this._common_message.CatchBlockMsg;
                this.showErrorMessage = true;
                setInterval(() => {
                    this.showErrorMessage = false;
                }, 5000);
            })
    }

    createEconomicCategory(data1: NgForm): void {
        this._globalURL.isLoading = true;
        debugger;
        let economicCategory = data1.value.Name;
        //alert('designationModel');
        this._cmrcservice.post((this._globalURL.CREATE_ECONOMIC_CATEGORY + economicCategory), data1.value, sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.status === 401) {
                    sessionStorage.clear();
                    this.showErrorMessage = true;
                    this.message = this._common_message.error401message;
                    setInterval(() => {
                        this.showErrorMessage = false;
                    }, 5000);
                    this._globalURL.isLoading = false;
                    this._router.navigate(['../login']);
                }
                if (res.Status == undefined)
                    if (res.status == 200) {
                        let data_ = JSON.parse(res._body);
                        if (data_.Status == true) {
                            data1.control.reset();
                            this._globalURL.isLoading = false;
                            this.showMessage = true;
                            this.message = this._common_message.AddEconomicCategoryMsg;
                            setInterval(() => {
                                this.showMessage = false;
                            }, 5000);
                            this.getEconomicCategory();
                        }
                        else {
                            this._globalURL.isLoading = false;;
                            this.showErrorMessage = true;
                            this.message = this._common_message.ErrorAddEconomicCategoryMsg;
                            setInterval(() => {
                                this.showErrorMessage = false;
                            }, 5000);
                        }
                    }
            })
            .catch((err) => {
                this._globalURL.isLoading = false;
                this.message = this._common_message.CatchBlockMsg;
                this.showErrorMessage = true;
                setInterval(() => {
                    this.showErrorMessage = false;
                }, 5000);
            })
    }

    delete(data: any) {
        debugger;
        this.EconomicCategoryModel = data;
        debugger;
    }

    deleteEconomicCategory(data: any): void {
        this._globalURL.isLoading = true;
        debugger;
        let id = data.Id;
        let final = this._globalURL.DELETE_ECONOMIC_CATEGORY + id;
        this._cmrcservice.post(final, "data", sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.status === 401) {
                    sessionStorage.clear();
                    this.showErrorMessage = true;
                    this.message = this._common_message.error401message;
                    setInterval(() => {
                        this.showErrorMessage = false;
                    }, 5000);
                    this._globalURL.isLoading = false;
                    this._router.navigate(['../login']);
                }
                if (res.Status == undefined) {
                    var data = JSON.parse(res._body);
                    if (data.Status == true) {
                        this._globalURL.isLoading = false;
                        this.showMessage = true;
                        this.message = this._common_message.DeleteEconomicCaytegoryMsg;
                        setInterval(() => {
                            this.showMessage = false;
                        }, 5000);
                        this.getEconomicCategory();
                    }
                    else {
                        this.showErrorMessage = true;
                        this.message = this._common_message.ErrorDeleteEconomicCategoryMsg;
                        setInterval(() => {
                            this.showErrorMessage = false;
                        }, 5000);
                        this._globalURL.isLoading = false;;
                    }
                }
                else {
                    if (res.Status == true) {
                        this._globalURL.isLoading = false;
                        this.showMessage = true;
                        this.message = this._common_message.DeleteEconomicCaytegoryMsg;
                        setInterval(() => {
                            this.showMessage = false;
                        }, 5000);
                        this.getEconomicCategory();
                    }
                    else {
                        this.showErrorMessage = true;
                        this.message = this._common_message.ErrorDeleteEconomicCategoryMsg;
                        setInterval(() => {
                            this.showErrorMessage = false;
                        }, 5000);
                        this._globalURL.isLoading = false;;
                    }
                }
            }).catch((err) => {
                this._globalURL.isLoading = false;
                this.message = this._common_message.CatchBlockMsg;
                this.showErrorMessage = true;
                setInterval(() => {
                    this.showErrorMessage = false;
                }, 5000);
            })
    }
    reRenderDT() {
        this.dtElement.dtInstance.then((dtInstance: DataTables.Api) => {
            dtInstance.destroy();
            this.dtTrigger.next();
        });
    }
}













