import { Injectable } from '@angular/core';
@Injectable()
export class FundMasterModel {
    public Id: number;
    public DistrictUserId: number;
    public DistrictUserName: any;
    public FromCMRCId: number;
    public FromCMRCName: any;
    public ToVOId: number;
    public ToVOName: any;
    public PaymentMode: any;
    public RequestAmount: any;
    public InterestRate: any;
    public Tenure: number;
    public FundType: any;
    public RequestDate: any;
    public ApproveDate: any;
    public CreatedBy: any;
    public CreatedDate: any;
    public UpdatedBy: any;
    public UpdatedDate: any;
    public ApproveAmount: any;
    public Status: any;
    public FundTypeId: any;
    public BankReceiptPhotoURL: any;
    public CheckReceiptPhotoURL: any;
    public FundReason: any;
    public PrincipleAmountPaidTillDate: any;
    public PrincipleRemainingAmtToPay: any;
    public InterestAmountPaidTillDate: any;
    public InterestRemainingAmtToPay: any;



    constructor() {
        this.Id = 0;
        this.DistrictUserId = 0;
        this.DistrictUserName = '';
        this.FromCMRCId = 0;
        this.FromCMRCName = '';
        this.ToVOId = 0;
        this.ToVOName = '';
        this.PaymentMode = '';
        this.RequestAmount = 0;
        this.InterestRate = '';
        this.Tenure = 0;
        this.FundType = '';
        this.RequestDate = '';
        this.ApproveDate = '';
        this.CreatedBy = '';
        this.CreatedDate = '';
        this.UpdatedBy = '';
        this.UpdatedDate = '';
        this.ApproveAmount = '';
        this.Status = '';
        this.FundTypeId = '';
        this.BankReceiptPhotoURL = '';
        this.CheckReceiptPhotoURL = '';
        this.FundReason = '';
        this.PrincipleAmountPaidTillDate = '';
        this.PrincipleRemainingAmtToPay = '';
        this.InterestAmountPaidTillDate = '';
        this.InterestRemainingAmtToPay = '';

    }





}





