import { Component, OnInit, ViewChild, AfterViewInit } from '@angular/core';
import { GlobalAssets } from '../../../environments/Global';
import { Router } from '@angular/router';
import { AccessToken } from '../../../environments/access_token';
import { CMRCService } from '../../../services/cmrc.services';
import { LangulageConf } from '../../../environments/language.config';
import { DataTableDirective } from 'angular-datatables';
import { Subject } from 'rxjs';
import { NgForm } from '@angular/forms';
import { CommonMessageComponent } from '../../../environments/CommanMssage';
@Component({
    templateUrl: '../village/village.component.html',
})
export class VillageComponent implements OnInit, AfterViewInit {
    showErrorMessage: boolean;
    @ViewChild(DataTableDirective)
    dtElement: DataTableDirective;
    dtInstance: DataTables.Api;
    public dtTrigger: Subject<any> = new Subject();
    public VillageModel: any;
    public showMessage: boolean;
    public message: string;
    public ShowErrormsg: boolean;
    public Villages: any;
    constructor(
        public _globalUrl: GlobalAssets,
        public _cmrcservice: CMRCService,
        public _access_token: AccessToken,
        public _router: Router,
        public lang: LangulageConf,
        public _common_message: CommonMessageComponent
    ) {
        _globalUrl.isLoading = true;
        if (sessionStorage.getItem("access_token") == "") {
            _router.navigate(['../login']);
        }
    }

    ngOnInit() {
        this.getVillage();
    }

    ngAfterViewInit(): void {
        this.dtTrigger.next();
        this.getVillage();
    }

    //get village
    getVillage(): void {
        debugger;
        this._globalUrl.isLoading = true;
        this._cmrcservice.get(this._globalUrl.GET_VILLAGE, sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.Status == true) {
                    this.Villages = res.Data;
                    this._globalUrl.isLoading = false;
                }
                else {
                    this.ShowErrormsg = true;
                    this.message = "Record is not get";
                    setInterval(() => {
                        this._globalUrl.isSaveSuccessMessage = false;
                        this.ShowErrormsg = false;
                    }, 5000);
                }
                this.reRenderDT();
            });
    }

    //crceate village 

    createVillage(Data: NgForm): void {
        debugger;
        // let villageName = data;
        this._globalUrl.isLoading = true;
        this._cmrcservice.post((this._globalUrl.CREATE_VILLAGE + Data.value.Name), null, sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.Status == undefined)
                    if (res.status == 200) {
                        let data_ = JSON.parse(res._body);
                        if (data_.Status == true) {
                            Data.control.reset();
                            this._globalUrl.isLoading = false;
                            this.showMessage = true;
                            this.message = this._common_message.AddVillageMsg;
                            setInterval(() => {
                                this._globalUrl.isSaveSuccessMessage = false;
                                this.showMessage = false;
                            }, 5000);
                            this.getVillage();
                        }
                        else {
                            this._globalUrl.isLoading = false;
                            this.showErrorMessage = true;
                            this.message = this._common_message.ErrorAddVillageMsg;
                            setInterval(() => {
                                this.showErrorMessage = false;
                            }, 5000);
                        }
                    }
                    else {
                        this.ShowErrormsg = true;
                        this.message = "Village is not created";
                        setInterval(() => {
                            this._globalUrl.isSaveSuccessMessage = false;
                            this.ShowErrormsg = false;
                        }, 5000);
                    }
                else {
                    if (res.Status == 1) {
                        this._globalUrl.isLoading = false;
                        this.showMessage = true;
                        this.message = "Your Record is Create succesfully";
                        setInterval(() => {
                            this._globalUrl.isSaveSuccessMessage = false;
                            this.showMessage = false;
                        }, 5000);
                        this.getVillage();
                    }
                }

            })
            .catch((err) => {
                this.ShowErrormsg = true;
                this.message = "Record is not created";
                setInterval(() => {
                }, 5000);
            })
    }

    delete(data: any) {
        debugger;
        this.VillageModel = data;
        debugger;
    }

    deleteVillage(data: any): void {
        this._globalUrl.isLoading = true;
        debugger;
        let id = data.Id;
        let final = this._globalUrl.DELETE_VILLAGE + id;
        this._cmrcservice.post(final, "data", sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.Status == undefined) {
                    var data = JSON.parse(res._body);
                    if (data.Status == true) {
                        this._globalUrl.isLoading = false;
                        this._common_message.DeleteVillageMsg;
                        this.getVillage();
                    }
                    else {
                        this.ShowErrormsg = true;
                        this.message = "Village is not delete";
                        setInterval(() => {
                            this._globalUrl.isSaveSuccessMessage = false;
                            this.ShowErrormsg = false;
                        }, 5000);
                    }
                }
                else {
                    if (res.Status == true) {
                        this.getVillage();
                    }
                    else {
                        this.ShowErrormsg = true;
                        this.message = "Village is not delete";
                        setInterval(() => {
                            this._globalUrl.isSaveSuccessMessage = false;
                            this.ShowErrormsg = false;
                        }, 5000);
                    }
                }
                this._globalUrl.isLoading = false;
                this.showMessage = true;
                this.message = "Village is Delete succesfully";
                setInterval(() => {
                    this.showMessage = false;
                }, 5000);
            })
            .catch((err) => {
                this.ShowErrormsg = true;
                this.message = "Village is not created";
                setInterval(() => {
                }, 5000);
            })
    }

    reRenderDT() {
        this.dtElement.dtInstance.then((dtInstance: DataTables.Api) => {
            dtInstance.destroy();
            this.dtTrigger.next();
        });
    }
}





