import { Component, AfterViewInit, OnInit, ViewChild } from '@angular/core';
import { VOService } from '../../../services/vo.services';
import { CommonService } from '../../../services/common.service';
import { AccessToken } from '../../../environments/access_token';
import { GlobalAssets } from '../../../environments/Global';
import { Router } from '@angular/router';
import { GlobalVariable } from '../../../environments/GlobalVeriables';
import { ExportToPDF } from '../../../services/reports/exportpdf';
import { LangulageConf } from '../../../environments/language.config';
import { ExportToExcel } from '../../../services/reports/exportexcel';
import { DataTableDirective } from 'angular-datatables';
import { Subject } from 'rxjs';
import { DatepickerOptions } from 'ng2-datepicker';
import * as frLocale from 'date-fns/locale/en';
import { CommonMessageComponent } from '../../../environments/CommanMssage';
import { NgForm } from '@angular/forms';
@Component({
    templateUrl: 'ledgerentries.component.html'
})
export class SLEntryComponent implements OnInit, AfterViewInit {
    showMessage: boolean;
    showWarningMessage: boolean;
    // @ViewChild(DataTableDirective)
    // dtElement: DataTableDirective;
    // dtInstance: DataTables.Api;
    // public dtTrigger: Subject<any> = new Subject();
    public AvailableAccounts: any;
    public slEntriesDetails: any;
    public showSuccMessage: boolean = false;
    public showErrorMessage: boolean = false;
    public message: any;
    public Amount: number = 0;
    public TransactionDate: Date = new Date();
    options: DatepickerOptions = {
        minYear: 2016,
        maxYear: (new Date().getFullYear() + 1),
        displayFormat: 'MMM D[,] YYYY',
        barTitleFormat: 'MMMM YYYY',
        dayNamesFormat: 'dd',
        firstCalendarDay: 0, // 0 - Sunday, 1 - Monday
        locale: frLocale,
        //minDate: new Date(Date.now()), // Minimal selectable date
        minDate: new Date("2016-03-03"),
        //maxDate: new Date(Date.now()),  // Maximal selectable date
        barTitleIfEmpty: 'Click to select a date'
    };
    constructor(
        public voservice: VOService,
        public globalURL: GlobalAssets,
        public accesstoken: AccessToken,
        public globalVariable: GlobalVariable,
        public exportToExcel: ExportToExcel,
        public exportToPDF: ExportToPDF,
        public lang: LangulageConf,
        public router: Router,
        public _commonService: CommonService,
        public _common_message: CommonMessageComponent
    ) {
        if (sessionStorage.getItem("access_token") == "")
            router.navigate(['../login']);

    }

    ngOnInit() {
        this.getSLEntriesDetails();
        //this.slDetailExportTopdf();
    }
    ngAfterViewInit(): void {
        //this.dtTrigger.next();
        //this.getSLEntriesDetails();

    }
    getSLEntriesDetails() {
        this.globalURL.isLoading = true; debugger;
        this.voservice.get(this.globalURL.GET_SL_ENTRIES_DETAILS + 3, sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.status === 401) {
                    this.accesstoken.access_token = "";
                    this.showErrorMessage = true;
                    this.message = this._common_message.error401message;
                    setInterval(() => {
                        this.showErrorMessage = false;
                    }, 5000);
                    this.globalURL.isLoading = false;
                    this.router.navigate(['../login']);
                }
                if (res.Status == undefined)
                    if (res.status == 200) {
                        if ((JSON.parse(res._body).Data.Status == 200))
                            this.slEntriesDetails = JSON.parse(res._body).Data;
                        else {
                            this.showWarningMessage = true;
                            this.message = this._common_message.WarningMsg;
                            setInterval(() => {
                                this.showWarningMessage = false;
                            }, 5000);
                            this.globalURL.isLoading = false;
                        }
                    }
                    else {
                        this.showWarningMessage = true;
                        this.message = this._common_message.WarningMsg;
                        setInterval(() => {
                            this.showWarningMessage = false;
                        }, 5000);
                        this.globalURL.isLoading = false;
                    }

                else {
                    if (res.Status == true) {
                        this.slEntriesDetails = res.Data;
                        this.globalURL.isLoading = false;
                    }
                    else {
                        this.showWarningMessage = true;
                        this.message = this._common_message.WarningMsg;
                        setInterval(() => {
                            this.showWarningMessage = false;
                        }, 5000);
                        this.globalURL.isLoading = false;
                    }
                }
                //this.reRenderDT(); 
            })
            .catch((res) => {
                this.showErrorMessage = true;
                this.message = this._common_message.CatchBlockMsg;
                setInterval(() => {
                    this.showErrorMessage = false;
                }, 5000);
                this.globalURL.isLoading = false;
            })
    }
    public clearData: NgForm;
    saveSLEntry(Data: NgForm, frDt: Date, ) {
        debugger;
        this.globalURL.isLoading = true;
        Data.value.TransactionDate = this._commonService.convertToDate(frDt);
        Data.value.pmode = Data.value.pmode == undefined ? 0 : Data.value.pmode;
        this.voservice.get((this.globalURL.SAVE_SL_ENTRY + "?slId=" + Data.value.slId + "&amount=" + Data.value.Amount + "&pmode=" + Data.value.pmode + "&TransactionDate=" + Data.value.TransactionDate), sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.status === 401) {
                    this.accesstoken.access_token = "";
                    this.showErrorMessage = true;
                    this.message = this._common_message.error401message;
                    setInterval(() => {
                        this.showErrorMessage = false;
                    }, 5000);
                    this.globalURL.isLoading = false;
                    this.router.navigate(['../login']);
                }
                if (res.Status == true) {
                    Data.control.reset();
                    this.globalURL.isLoading = false;
                    this.message = this._common_message.sladdmsg;
                    this.showMessage = true;
                    setInterval(() => {
                        this.showMessage = false;
                    }, 5000);
                    this.getSLEntriesDetails();

                }
                else {
                    this.showWarningMessage = true;
                    this.message = this._common_message.WarningMsg;
                    setInterval(() => {
                        this.showWarningMessage = false;
                    }, 5000);
                    this.globalURL.isLoading = false;
                    this.getSLEntriesDetails();
                }
            })
            .catch((res) => {
                this.showErrorMessage = true;
                this.message = this._common_message.CatchBlockMsg;
                setInterval(() => {
                    this.showErrorMessage = false;
                }, 5000);
                this.globalURL.isLoading = false;
                this.getSLEntriesDetails();
            })
    }

    public isPMApplicable: boolean = false;
    checkIfPMIsApplicable(slId: number): void {
        debugger;
        this.voservice.get(this.globalURL.CHECKIF_PM_APPLICABLE + slId, sessionStorage.getItem("access_token"))
            .then((res) => {
                debugger;
                if (res == null) {
                    this.isPMApplicable = false;
                    return;                    
                }
                if (res.status === 401) {
                    this.accesstoken.access_token = "";
                    this.showErrorMessage = true;
                    this.message = this._common_message.error401message;
                    setInterval(() => {
                        this.showErrorMessage = false;
                    }, 5000);
                    this.globalURL.isLoading = false;
                    this.router.navigate(['../login']);
                }
                this.isPMApplicable = res;
            }).catch((res) => {
                this.showErrorMessage = true;
                this.message = this._common_message.CatchBlockMsg;
                setInterval(() => {
                    this.showErrorMessage = false;
                }, 5000);
                this.globalURL.isLoading = false;
            });
        if (slId == this.globalVariable.CASH_IN_HAND_TO_CASH_AT_BANK) {
            this.Amount = this.globalVariable.CashInHand;
        }
        else {
            this.Amount = 0;
        }
    }
    slDetailExportTopdf() {
        this.voservice.get(this.globalURL.GET_AVAILABLEACCOUNTSS, sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.status === 401) {
                    this.accesstoken.access_token = "";
                    this.showErrorMessage = true;
                    this.message = this._common_message.error401message;
                    setInterval(() => {
                        this.showErrorMessage = false;
                    }, 5000);
                    this.globalURL.isLoading = false;
                    this.router.navigate(['../login']);
                }
                this.AvailableAccounts = res.Data;
            }).catch((res) => {
                this.showErrorMessage = true;
                this.message = this._common_message.CatchBlockMsg;
                setInterval(() => {
                    this.showErrorMessage = false;
                }, 5000);
                this.globalURL.isLoading = false;
            });
    }

    R143_slDetailExportTopdf() {
        this.exportToPDF.R143_slDetailExportTopdf(this.AvailableAccounts);
    }

    subLedgerExcel() {
        this.exportToExcel.subLedgerExcel(this.AvailableAccounts);
    }
    // reRenderDT() {
    //     this.dtElement.dtInstance.then((dtInstance: DataTables.Api) => {
    //         dtInstance.destroy();
    //         this.dtTrigger.next();
    //     });
    // }
}



