import { Component, OnInit } from '@angular/core'
import { VOService } from '../../../services/vo.services';
import { AccessToken } from '../../../environments/access_token';
import { GlobalAssets } from '../../../environments/Global';
import { Router } from '@angular/router';
import { GlobalVariable } from '../../../environments/GlobalVeriables';
import { CommonService } from '../../../services/common.service';
import { ExportToPDF } from '../../../services/reports/exportpdf';
import { LangulageConf } from '../../../environments/language.config';
import { CommonMessageComponent } from '../../../environments/CommanMssage';

@Component({
    templateUrl: 'accounttransaction.component.html'
})

export class VOViewAccountTransctionComponent implements OnInit {
    public message: string;
    public showErrorMessage: boolean = false;
    public showSuccessMessage: boolean = false;
    public showWarningMessage: boolean = false;
    public glTypeAccTransactionDetails: any;
    public totalAmt: number;
    public selectGLType: number;
    public lastFinanceYearOrFromDt: string;
    public todaysOrToDt: string;
    constructor(public _voservice: VOService,
        public _access_token: AccessToken,
        public _globalURL: GlobalAssets,
        public router: Router,
        public lang: LangulageConf,
        public globalVariable: GlobalVariable,
        public _common_message: CommonMessageComponent,
        public commonService: CommonService,
        public exportToPDF: ExportToPDF) {
        if (sessionStorage.getItem("access_token") == "")
            router.navigate(['../login']);
    }

    ngOnInit() {
        this.selectGLType = this.globalVariable.selectedGLTypeId;
        this.getSelectedGLTypeDetails(this.globalVariable.selectedGLTypeId);
    }

    getSelectedGLTypeDetails(glType: number) {
        this._globalURL.isLoading = true;
        this.lastFinanceYearOrFromDt = "1 APR " + (new Date().getFullYear() - 1);
        this.todaysOrToDt = this.commonService.getTodaysDate();
        this.totalAmt = 0;
        this.selectGLType = glType;
        this._voservice.get(this._globalURL.GET_SELECTED_GL_TYPE_DETAILS + 3 + "&glType=" + glType + "&frDt=null&toDt=null", sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.status === 401) {
                  sessionStorage.clear();
                    this.showErrorMessage = true;
                    this.message = this._common_message.error401message;
                    setInterval(() => {
                        this.showErrorMessage = false;
                    }, 5000);
                    this._globalURL.isLoading = false;
                    this.router.navigate(['../login']);
                }
                if (res.Status == undefined) {
                    if (res.status == 200) {
                        this.glTypeAccTransactionDetails = JSON.parse(res._body).Data;
                        for (var item in this.glTypeAccTransactionDetails)
                            this.totalAmt = this.totalAmt + this.glTypeAccTransactionDetails[item].Amount;
                    }
                    else {
                        this.message = this._common_message.WarningMsg;
                        this.showWarningMessage = true;
                        setInterval(() => {
                            this.showWarningMessage = false;
                        }, 5000);
                        this._globalURL.isLoading = false;
                    }
                }
                else {
                    if (res.Status == true) {
                        this.glTypeAccTransactionDetails = res.Data;
                        for (var item in this.glTypeAccTransactionDetails)
                            this.totalAmt = this.totalAmt + this.glTypeAccTransactionDetails[item].Amount;
                    }
                    else {
                        this.message = this._common_message.WarningMsg;
                        this.showWarningMessage = true;
                        setInterval(() => {
                            this.showWarningMessage = false;
                        }, 5000);
                        this._globalURL.isLoading = false;
                    }
                }
                this._globalURL.isLoading = false;
            }).catch((res) => {
                this.showErrorMessage = true;
                this.message = this._common_message.CatchBlockMsg;
                setInterval(() => {
                    this.showErrorMessage = false;
                }, 5000);
                this._globalURL.isLoading = false;
            })
    }

    loadGlTypeDetails(data: any) {
        this._globalURL.isLoading = true;
        debugger;
        // this.lastFinanceYearOrFromDt = frDt;
        // this.todaysOrToDt = toDt;
        this.totalAmt = 0;
        this._voservice.get(this._globalURL.GET_SELECTED_GL_TYPE_DETAILS + 3 + "&glType=" + this.selectGLType + "&frDt=" + data.lastFinanceYearOrFromDt + "&toDt=" + data.todaysOrToDt, sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.status === 401) {
                  sessionStorage.clear();
                    this.showErrorMessage = true;
                    this.message = this._common_message.error401message;
                    setInterval(() => {
                        this.showErrorMessage = false;
                    }, 5000);
                    this._globalURL.isLoading = false;
                    this.router.navigate(['../login']);
                }
                if (res.Status == undefined) {
                    if (res.status == 200) {
                        this.glTypeAccTransactionDetails = JSON.parse(res._body).Data;
                        for (var item in this.glTypeAccTransactionDetails)
                            this.totalAmt = this.totalAmt + this.glTypeAccTransactionDetails[item].Amount;
                    }
                    else {
                        this.message = this._common_message.WarningMsg;
                        this.showWarningMessage = true;
                        setInterval(() => {
                            this.showWarningMessage = false;
                        }, 5000);
                        this._globalURL.isLoading = false;
                    }
                }
                else {
                    if (res.Status == true) {
                        this.glTypeAccTransactionDetails = res.Data;
                        for (var item in this.glTypeAccTransactionDetails)
                            this.totalAmt = this.totalAmt + this.glTypeAccTransactionDetails[item].Amount;
                    }
                    else {
                        this.message = this._common_message.WarningMsg;
                        this.showWarningMessage = true;
                        setInterval(() => {
                            this.showWarningMessage = false;
                        }, 5000);
                        this._globalURL.isLoading = false;
                    }
                }
                this._globalURL.isLoading = false;
            }).catch((res) => {
                this.showErrorMessage = true;
                this.message = this._common_message.CatchBlockMsg;
                setInterval(() => {
                    this.showErrorMessage = false;
                }, 5000);
                this._globalURL.isLoading = false;
            })
    }

    ExportGlTransactionDetailsToPdf() {
        this._globalURL.isLoading = true;
        let reportName = this.selectGLType == 1 ? "ASSETS" : this.selectGLType == 2 ? "LIABILITIES" : this.selectGLType == 3 ? "INCOME" : "EXPENSES"
        debugger;
        var reportData = {
            "TransactionDetails": this.glTypeAccTransactionDetails,
            "Total": this.totalAmt
        };
        var headerData = {
            "VOName": this.globalVariable.VOName,
            "Code": this.globalVariable.voBaseCode,
            "frDt": this.lastFinanceYearOrFromDt,
            "toDt": this.todaysOrToDt,
            "reportName": reportName
        };
        this.exportToPDF.RExportGLTransactionDetailsToPdf(reportData, headerData);
        this._globalURL.isLoading = false;
    }

    ExportDetailedBalancesheettoPdf() {
        this._globalURL.isLoading = true;
        this._voservice.get(this._globalURL.GET_DETAILED_BALANCESHEET + 3, sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.status === 401) {
                  sessionStorage.clear();
                    this.showErrorMessage = true;
                    this.message = this._common_message.error401message;
                    setInterval(() => {
                        this.showErrorMessage = false;
                    }, 5000);
                    this._globalURL.isLoading = false;
                    this.router.navigate(['../login']);
                }
                if (res.Status == true) {
                    var headerData = {
                        "VOName": this.globalVariable.VOName,
                        "Code": this.globalVariable.voBaseCode,
                        "frDt": this.lastFinanceYearOrFromDt,
                        "toDt": this.todaysOrToDt
                    };
                    this.exportToPDF.ExportDetailedbalancesheetToPDF(res.Data, headerData);
                    this._globalURL.isLoading = false;
                }
                else {
                    this.message = this._common_message.WarningMsg;
                    this.showWarningMessage = true;
                    setInterval(() => {
                        this.showWarningMessage = false;
                    }, 5000);
                    this._globalURL.isLoading = false;
                }
            }).
            catch((res) => {
                this.showErrorMessage = true;
                this.message = this._common_message.CatchBlockMsg;
                setInterval(() => {
                    this.showErrorMessage = false;
                }, 5000);
                this._globalURL.isLoading = false;
            })
    }

    //interface for r_type,
    // r_type = 1: Profit and Loss Hisgh Level Report
    // r_type = 2: Profit Detailed Transactin reports
    // r_type = 3: Loss Detailed Transactin reports
    public ExportProfitAndLoss(r_type: number) {
        debugger;
        this._globalURL.isLoading = true;
        let final_URL;

        //        if (this.todaysOrToDt == null && this.lastFinanceYearOrFromDt)
        final_URL = this._globalURL.GET_PROFIT_AND_LOSS + 3 + "&fromDt=null&toDt=null";
        // else
        //     final_URL = this._globalURL.GET_PROFIT_AND_LOSS + 3 + "&fromDt=" + this.lastFinanceYearOrFromDt + "&toDt=" + this.todaysOrToDt;
        this._voservice.get(this._globalURL.GET_PROFIT_AND_LOSS + 3 + "&fromDt=null&toDt=null", sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.status === 401) {
                  sessionStorage.clear();
                    this.showErrorMessage = true;
                    this.message = this._common_message.error401message;
                    setInterval(() => {
                        this.showErrorMessage = false;
                    }, 5000);
                    this._globalURL.isLoading = false;
                    this.router.navigate(['../login']);
                }
                if (res.Status == true) {
                    var headerData = {
                        "VOName": this.globalVariable.VOName,
                        "Code": this.globalVariable.voBaseCode,
                        "frDt": this.lastFinanceYearOrFromDt,
                        "toDt": this.todaysOrToDt
                    };
                    this.exportToPDF.ExportProfitAndLossToPDF(res.Data, headerData, r_type);
                    this._globalURL.isLoading = false;
                }
                else {
                    this.message = this._common_message.WarningMsg;
                    this.showWarningMessage = true;
                    setInterval(() => {
                        this.showWarningMessage = false;
                    }, 5000);
                    this._globalURL.isLoading = false;
                }
            }).catch((res) => {
                this.showErrorMessage = true;
                this.message = this._common_message.CatchBlockMsg;
                setInterval(() => {
                    this.showErrorMessage = false;
                }, 5000);
                this._globalURL.isLoading = false;
            })
    }
}


