import { Component, Input, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { LoginService } from '../../../services/login.service';
import { GlobalAssets } from '../../../environments/Global';
import { AccessToken } from '../../../environments/access_token';
import *as jsPDF from 'jspdf';

@Component({
    templateUrl: '../login/login.component.html',
    styleUrls: ['../login/login.component.css']
})

export class LoginComponent implements OnInit {
     public Cmrcs : any;
    public showMessage: boolean = false;
    public message: string;
    public ShowErrormsg: boolean;
    @Input() _message: string;
    public userName: string;
    public password: string;
    public parentData: string;
    constructor(public _loginService: LoginService,
        public _globalURL: GlobalAssets,
        public _access_token: AccessToken,
        public router: Router) {
        this._globalURL.isLoading = false;
        debugger;
        //this.router.navigate(['../login']);
        sessionStorage.clear();
    }

    ngOnInit() {
    }
    abc() {
    }
    login(loginCredentials: any) {
        debugger;
        this._globalURL.isLoading = true;
        this._loginService.post(this._globalURL.TOKEN, loginCredentials)
            .then((res) => {
                if (res.status == 200) {
                    var data = JSON.parse(res._body);
                    sessionStorage.setItem("access_token", data.access_token);
                    sessionStorage.setItem("roleId", data.userRoleId);
                    this._access_token.roleId = data.userRoleId;
                    this._globalURL.isLoading = false;
                    console.log(sessionStorage.getItem("access_token"));
                    if (data.userRole == "VO")
                        this.router.navigate(['../vo']);
                    if (data.userRole == "CMRC")
                        this.router.navigate(['../cmrc']);
                    if (data.userRole == "SC")
                        this.router.navigate(['../sc']);
                    if (data.userRole == "DISTRICT")
                        this.router.navigate(['../district']);
                }
                else {
                    alert("username failed");
                    //  this.ShowErrormsg = true;
                    this.message = "Invalid Username or Password";
                    this.ShowErrormsg = true;
                    this._globalURL.isLoading = false;
                }
            }).catch((res) => {
                this.ShowErrormsg = true;
                this._globalURL.isLoading = false;
            })
    }

    getAllCMRCKVP(): void {
        debugger;
        this._globalURL.isLoading = true;
        this._loginService.get(this._globalURL.GET_ALL_CMRC_KPV, sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.Status == true) {
                    this.Cmrcs = res.Data;                    
                    this._globalURL.isLoading = false;
                }
                else {
                    this._globalURL.isLoading = false;
                }
            });
    }
}


