import { Component, OnInit } from '@angular/core';
import { CurrencyPipe } from '@angular/common';
import { VOService } from '../../../services/vo.services';
import { AccessToken } from '../../../environments/access_token';
import { GlobalAssets } from '../../../environments/Global';
import { Router } from '@angular/router';
import { FileUploadService } from '../../../services/FileService/file_upload.service';
import { CommonService } from '../../../services/common.service';
import { LangulageConf } from '../../../environments/language.config';
import { ExportToPDF } from '../../../services/reports/exportpdf';
import { DatepickerOptions } from 'ng2-datepicker';
import * as frLocale from 'date-fns/locale/en';
import { CommonMessageComponent } from '../../../environments/CommanMssage';
import { NgForm } from '@angular/forms';

@Component({
    templateUrl: '../loan/loan.repayment.component.html',
    styleUrls: ['../../../components/app.css']
})
export class LoanRepaymentComponent implements OnInit {
    showWarningMessage: boolean;
    public showErrorMessage: boolean;
    public message: string;
    public showSuccessMessage: boolean;
    public recordNotCreated: any;
    public recordCreated: any;
    public showErrorMsg: any;
    public convertedWordsInEnglishForIntrest: string;
    public convertedWordsInHindiForIntrest: string;
    public convertedWordsInMarathiForIntrest: string;
    public convertedWordsInMarathi: any;
    public convertedWordsInHindi: any;
    public convertedWordsInEnglish: any;
    public amount: any;
    public memLoanRepaymentDetails: any;
    public memberLoanDetails: any;
    public SHGNameAndId: any;
    public selectedShgId: any;
    public membersNameAndId: any;
    public selectedMemberId: any;
    public totalLoanAmt: any;
    public totalPrinciple; any;
    public totalPayable: any;
    public totalFineAmt: any;
    public amtPaidDate: Date = new Date;
    options: DatepickerOptions = {
        minYear: 2016,
        maxYear: (new Date().getFullYear() + 1),
        displayFormat: 'MMM D[,] YYYY',
        barTitleFormat: 'MMMM YYYY',
        dayNamesFormat: 'dd',
        firstCalendarDay: 0, // 0 - Sunday, 1 - Monday
        locale: frLocale,
        //minDate: new Date(Date.now()), // Minimal selectable date
        minDate: new Date("2016-03-03"),
        //maxDate: new Date(Date.now()),  // Maximal selectable date
        barTitleIfEmpty: 'Click to select a date'
    };
    constructor(public _voservice: VOService,
        private _fileUploadService: FileUploadService,
        public _access_token: AccessToken,
        public _commonService: CommonService,
        public _globalURL: GlobalAssets,
        public _exportToPdf: ExportToPDF,
        public lang: LangulageConf,
        public router: Router,
        public _common_message: CommonMessageComponent) {
        this._globalURL.isLoading = false;
        this.memLoanRepaymentDetails = null;
        this.memberLoanDetails = null;
        this.SHGNameAndId = null;

        this.selectedShgId = null;
        this.membersNameAndId = null;
        this.selectedMemberId = null;
        this.setLoanAmountCountToZero();
        if (sessionStorage.getItem("access_token") == "")
            router.navigate(['../login']);

    }

    ngOnInit() {
        this.amount = this._globalURL.loanAmount;
        //alert(this._globalURL.loanAmount);
        // this.loanSHGLoanData()
        this._globalURL.isLoading = true;
        this.loadLoanRepayment();
        this.loanLoanData(this._globalURL.selectedLoanId);
    }
    public remainingLoamAmount: number = 0;
    loanLoanData(loanId: number) {
        debugger;
        this._globalURL.isLoading = true;
        this._voservice.get(this._globalURL.GET_LOAN_DETAILS_BY_LOAN_ID + loanId, sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.Status == true) {
                    this.memberLoanDetails = res.Data;
                    this.remainingLoamAmount = this.memberLoanDetails.LoanAmount - this.memberLoanDetails.LoanApprovedAmount;
                }
                this._globalURL.isLoading = false;
            })
    }

    public loanId: any = 0;
    setLoanAmountCountToZero() {
        this.totalLoanAmt = 0;
        this.totalPayable = 0;
        this.totalFineAmt = 0;
        this.totalPrinciple = 0;
        this.totalFineAmt = 0;
        this.totalAmtPaid = 0;
    }

    public repaymentId: number = 0;
    loadLoanRepayment() {
        debugger;        
        this.totalLoanAmt = 0;
        this.totalPayable = 0;
        this.totalPrinciple = 0;
        this.totalFineAmt = 0;
        this.totalAmtPaid = 0;
        this.setLoanAmountCountToZero();
        this._voservice.get((this._globalURL.GET_REPAYMENT + this._globalURL.selectedLoanId), sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.status === 401) {
                    sessionStorage.clear();
                    this.showErrorMessage = true;
                    this.message = this._common_message.error401message;
                    setInterval(() => {
                        this.showErrorMessage = false;
                    }, 5000);
                    this._globalURL.isLoading = false;
                    this.router.navigate(['../login']);
                }
                if (res.Status == true) {
                    this.memLoanRepaymentDetails = res.Data;
                    this.loanId = res.Data[0].LoanId;
                    for (var item in res.Data) {
                        if (!res.Data[item].RepaymentDone) {
                            this.repaymentId = res.Data[item].RepaymentId;
                            break;
                        }
                    }

                    for (var item in res.Data) {
                        debugger;
                        this.memLoanRepaymentDetails[item].PerTotalAmountPaid = this.memLoanRepaymentDetails[item].PerTotalAmountPaid != null ? this.memLoanRepaymentDetails[item].PerTotalAmountPaid.toString().split('.')[0] : 0;
                        this.totalLoanAmt = this.totalLoanAmt + res.Data[item].FixedPayableAmt;
                        this.totalPayable = this.totalPayable + res.Data[item].TotalPayableAmt;
                        this.totalPrinciple = this.totalPrinciple + res.Data[item].InterestAmt;
                        this.totalFineAmt = this.totalFineAmt + res.Data[item].FineAmount;
                        this.totalAmtPaid = this.totalAmtPaid + res.Data[item].PaidTotalAmt + res.Data[item].PaidIntrestAmount;
                        debugger;
                    }
                    this._globalURL.isLoading = false;
                }
                else {
                    this.showWarningMessage = true;
                    this.message = this._common_message.WarningMsg;
                    setInterval(() => {
                        this.showWarningMessage = false;
                    }, 5000);
                    this._globalURL.isLoading = false;
                }
            }).catch((res) => {
                this.showErrorMessage = true;
                console.log(res.error);
                alert("catch block");
                this.message = this._common_message.CatchBlockMsg;
                setInterval(() => {
                    this.showErrorMessage = false;
                }, 5000);
                this._globalURL.isLoading = false;
            })
    }

    convertToWords(num) {
        this.convertedWordsInEnglish = this._commonService.convertDigitToWordsInEnglish(num);
        this.convertedWordsInHindi = this._commonService.convertDigitToWordsInHindi(num);
        this.convertedWordsInMarathi = this._commonService.convertDigitToWordsInMarathi(num);
    }

    convertToWordsForIntrest(num) {
        this.convertedWordsInEnglishForIntrest = this._commonService.convertDigitToWordsInEnglish(num);
        this.convertedWordsInHindiForIntrest = this._commonService.convertDigitToWordsInHindi(num);
        this.convertedWordsInMarathiForIntrest = this._commonService.convertDigitToWordsInMarathi(num);
    }

    save(data: any) {
        this.setLoanAmountCountToZero();
        // this.memLoanRepaymentDetails.length = -1;
        this._globalURL.isLoading = true;
        this._voservice.get(
            (this._globalURL.CALCULATE_REPAYMENT + "?amount=" + this.amount + "&interest=" + data.interest + "&tenure=" + data.tenure + "&effectiveDate=" + data.effectiveDate),
            sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.status === 401) {
                    sessionStorage.clear();
                    this.showErrorMessage = true;
                    this.message = this._common_message.error401message;
                    setInterval(() => {
                        this.showErrorMessage = false;
                    }, 5000);
                    this._globalURL.isLoading = false;
                    this.router.navigate(['../login']);
                }
                if (res.status == 200) {
                    this.memLoanRepaymentDetails = res.Data;
                    for (var item in res.Data) {
                        this.totalLoanAmt = this.totalLoanAmt + res.Data[item].FixedPayableAmt;
                        this.totalPayable = this.totalPayable + res.Data[item].TotalPayableAmt;
                        this.totalPrinciple = this.totalPrinciple + res.Data[item].InterestAmt;
                        debugger;
                    }
                }
                else {
                    this.showWarningMessage = true;
                    this.message = this._common_message.LoanAppSaveErrorMsg;
                    setInterval(() => {
                        this.showWarningMessage = false;
                    }, 5000);
                    this._globalURL.isLoading = false;
                }
            }).catch((res) => {
                this.showErrorMessage = true;
                console.log(res.error);
                this.message = this._common_message.CatchBlockMsg;
                setInterval(() => {
                    this.showErrorMessage = false;
                }, 5000);
                this._globalURL.isLoading = false;
            })
    }

    public loanRepaymentdata = {
        'PaidTotalAmt': null, 'FixedPayableAmt': null, 'InterestAmt': null, 'TotalPayableAmt': null,
        'PaidIntrestAmount': null, 'FineAmount': null,
        'LoanId': null, 'RepaymentId': null, 'Tenure': null, 'InterestRate': null,
        'PaymentMode': 0,
        'PaidDate': null
    };
    setRepaymentData(data: any, currentInstallment: number) {
        this.loanRepaymentdata.FixedPayableAmt = data.FixedPayableAmt;
        this.loanRepaymentdata.InterestAmt = data.InterestAmt;
        this.loanRepaymentdata.TotalPayableAmt = data.TotalPayableAmt;
        this.loanRepaymentdata.LoanId = data.LoanId;
        this.loanRepaymentdata.RepaymentId = data.RepaymentId;
        this.loanRepaymentdata.Tenure = (currentInstallment + 1);
        debugger
    }
    public Loanform : NgForm;
    MarkRepayment(Data: NgForm, metadata: any) {
        debugger;
        let _RepaymentId = this.loanRepaymentdata.RepaymentId;
        this.loanRepaymentdata.PaidTotalAmt = Data.value.actualPrincipleAmt;
        this.loanRepaymentdata.PaidIntrestAmount = Data.value.actualIntrestAmt;
        this.loanRepaymentdata.FineAmount = Data.value.fineAmt == undefined ? 0 : Data.value.fineAmt;
        this.loanRepaymentdata.PaymentMode = Data.value.selectedPaymentModeId;
        this.loanRepaymentdata.PaidDate = Data.value.amtPaidDate;
        this._globalURL.isLoading = true;
        this.Loanform = Data.value;
        this._voservice.post(this._globalURL.MARK_REPAYMENT, this.loanRepaymentdata, sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.status === 401) {
                    sessionStorage.clear();
                    this.showErrorMessage = true;
                    this.message = this._common_message.error401message;
                    setInterval(() => {
                        this.showErrorMessage = false;
                    }, 5000);
                    this._globalURL.isLoading = false;
                    this.router.navigate(['../login']);
                }
                if (res.Status == undefined) {
                    if (res.status === 200) {
                        var formData = new FormData();
                        formData.append('files', metadata);
                        formData.append('RefId', _RepaymentId);
                        this._fileUploadService.post((this._globalURL.ADD_PHOTO + "?folderName=Loan" + "&type=" + this._globalURL.LOAN_REPAYMENT_PHOTO + "&RefId=" + _RepaymentId),
                            formData, sessionStorage.getItem("access_token")).then((res) => {
                                if (res.status === 401) {
                                    sessionStorage.clear();
                                    this.showErrorMessage = true;
                                    this.message = this._common_message.error401message;
                                    setInterval(() => {
                                        this.showErrorMessage = false;
                                    }, 5000);
                                    this._globalURL.isLoading = false;
                                    this.router.navigate(['../login']);
                                }
                                if (res.Status == undefined) {
                                    if (res.status == 200) {
                                        Data.control.reset();
                                        this.loadLoanRepayment();
                                        this._globalURL.isLoading = false;
                                    }
                                    else {
                                        this.showWarningMessage = true;
                                        this.message = this._common_message.MarkRepaymentErrMsg;
                                        setInterval(() => {
                                            this.showWarningMessage = false;
                                        }, 5000);
                                        this.loadLoanRepayment();
                                        this._globalURL.isLoading = false;
                                    }
                                }
                                else {
                                    this.showWarningMessage = true;
                                    this.message = this._common_message.LoanAppSaveErrorMsg;
                                    setInterval(() => {
                                        this.showWarningMessage = false;
                                    }, 5000);
                                    this.loadLoanRepayment();
                                    this._globalURL.isLoading = false;
                                }
                            }).catch((err) => {
                                this.showErrorMessage = true;
                                console.log(res.error);
                                this.message = this._common_message.CatchBlockMsg;
                                setInterval(() => {
                                    this.showErrorMessage = false;
                                }, 5000);
                                this.loadLoanRepayment();
                                this._globalURL.isLoading = false;
                            });
                        this._globalURL.isLoading = false;
                        this.message = this._common_message.MarkRepaymentMsg;
                        this.showSuccessMessage = true;
                        setInterval(() => {
                            this.showSuccessMessage = false;
                        }, 5000);
                        this.loadLoanRepayment();
                    }
                    else {
                        this.showWarningMessage = true;
                        this.message = this._common_message.MarkRepaymentErrMsg;
                        setInterval(() => {
                            this.showWarningMessage = false;
                        }, 5000);
                        this.loadLoanRepayment();
                        this._globalURL.isLoading = false;
                    }
                }
                else {
                    this.showWarningMessage = true;
                    this.message = this._common_message.MarkRepaymentErrMsg;
                    setInterval(() => {
                        this.showWarningMessage = false;
                    }, 5000);
                    this.loadLoanRepayment();
                    this._globalURL.isLoading = false;
                }
            }).catch((res) => {
                this.showErrorMessage = true;
                console.log(res.error);
                this.message = this._common_message.CatchBlockMsg;
                setInterval(() => {
                    this.showErrorMessage = false;
                }, 5000);
                this.loadLoanRepayment();
                this._globalURL.isLoading = false;
            })
    }

    public totalAmtPaid: number;
    UpdateLoan(remainingAmt: number, loanId: number) {
        debugger;
        this.setLoanAmountCountToZero();
        //        this.LoanDetails.LoanAmount = data.amount;
        let loanData = {
            'LoanApprovedAmount': remainingAmt,
            'LoanId': loanId
        };
        // this.Action;
        // this.LoanId;
        debugger;
        this._globalURL.isLoading = true;
        this._voservice.post(this._globalURL.UPDATE_LOAN_AMT, loanData, sessionStorage.getItem("access_token"))
            .then((response) => {
                if (res.status === 401) {
                    sessionStorage.clear();
                    this.showErrorMessage = true;
                    this.message = this._common_message.error401message;
                    setInterval(() => {
                        this.showErrorMessage = false;
                    }, 5000);
                    this._globalURL.isLoading = false;
                    this.router.navigate(['../login']);
                }
                if (response.Status == undefined) {
                    if (response.status == 200) {
                        var res = JSON.parse(response._body);
                        if (res.Status == true) {
                            this.memLoanRepaymentDetails = res.Data;
                            this.loanLoanData(loanId);
                            this._globalURL.isLoading = false;
                            for (var item in res.Data) {
                                if (!res.Data[item].RepaymentDone) {
                                    this.repaymentId = res.Data[item].RepaymentId;
                                    break;
                                }
                            }
                            for (var item in res.Data) {
                                this.totalLoanAmt = this.totalLoanAmt + res.Data[item].FixedPayableAmt;
                                this.totalPayable = this.totalPayable + res.Data[item].TotalPayableAmt;
                                this.totalPrinciple = this.totalPrinciple + res.Data[item].InterestAmt;
                                this.totalFineAmt = this.totalFineAmt + res.Data[item].FineAmount;
                                this.totalAmtPaid = this.totalAmtPaid + res.Data[item].PaidTotalAmt + res.Data[item].PaidIntrestAmount;
                                debugger;
                            }
                            this._globalURL.isLoading = false;
                            this.message = this._common_message.LoanUpdateMsg;
                            this.showSuccessMessage = true;
                            setInterval(() => {
                                this.showSuccessMessage = false;
                            }, 6000);
                        }
                        else {
                            this.showWarningMessage = true;
                            this.message = this._common_message.WarningMsg;
                            setInterval(() => {
                                this.showWarningMessage = false;
                            }, 5000);
                            this._globalURL.isLoading = false;
                        }
                    }
                }
                //this.loanSHGLoanData(this.selectedLoanStatus);
            })
            .catch((res) => {
                this.showErrorMessage = true;
                console.log(res.error);
                this.message = this._common_message.CatchBlockMsg;
                setInterval(() => {
                    this.showErrorMessage = false;
                }, 5000);
                this._globalURL.isLoading = false;
            })
    }

    SHGLoanRepaymentExportToPdf() {
        this._globalURL.isLoading == true;
        let totalamountData = {
            "totalLoanAmount": this.totalLoanAmt.toString(),
            "totalPaybleAmount": this.totalPayable.toString(),
            "totalPrincipleAmount": this.totalPrinciple.toString(),
            "totalFineAmount": this.totalFineAmt.toString(),
            "totalPaidAmount": this.totalAmtPaid.toString()
        };
        this._exportToPdf.SHGLoanRepaymentExportToPdf(this.memLoanRepaymentDetails, totalamountData, this.memberLoanDetails);
        this._globalURL.isLoading == false;
    }

}


