import { Component, OnInit, ViewChild, AfterViewInit } from '@angular/core';
import { CurrencyPipe } from '@angular/common';
import { VOService } from '../../../services/vo.services';
import { Router } from '@angular/router';
import { AccessToken } from '../../../environments/access_token';
import { GlobalAssets } from '../../../environments/Global';
import { CMRCService } from '../../../services/cmrc.services';
import { CommonService } from '../../../services/common.service';
import { DataTableDirective } from 'angular-datatables';
import { GlobalVariable } from '../../../environments/GlobalVeriables';
import { LangulageConf } from '../../../environments/language.config';
import { DatepickerOptions } from 'ng2-datepicker';
import * as frLocale from 'date-fns/locale/en';
import { CommonMessageComponent } from '../../../environments/CommanMssage';
import { NgForm } from '@angular/forms';

@Component({
    templateUrl: '../fund/fundrepayment.component.html',
    styleUrls: ['../../../components/app.css'],
})

export class VOFundRepaymentComponent implements OnInit, AfterViewInit {
    public showMessage: boolean;
    public showWarningMessage: boolean;
    public showErrorMessage: boolean;
    public message: string;
    public ShowErrormsg: boolean;
    public convertedWordsInEnglishForInterest: string;
    public convertedWordsInHindiForInterest: string;
    public convertedWordsInMarathiForInterest: string;
    // @ViewChild(DataTableDirective)
    // dtElement: DataTableDirective;
    // dtInstance: DataTables.Api;
    public fundgroups: any;
    public fundRepaymentDetails: any;
    public fundDetails: any;
    public totalRequestAmt: any;
    public totalApprovedAmt: any;
    public fundTypeId: number;
    public calculatedFundRepaymentData: any;
    public RequestAmount: string;
    public convertedWordsInEnglish: string;
    public convertedWordsInMarathi: string;
    public convertedWordsInHindi: string;
    public totalInterestAmtPaid: number;
    public totalPrincipalAmtPaid: number;
    public PaidDate: Date = new Date();
    options: DatepickerOptions = {
        minYear: 2016,
        maxYear: (new Date().getFullYear() + 1),
        displayFormat: 'MMM D[,] YYYY',
        barTitleFormat: 'MMMM YYYY',
        dayNamesFormat: 'dd',
        firstCalendarDay: 0, // 0 - Sunday, 1 - Monday
        locale: frLocale,
        //minDate: new Date(Date.now()), // Minimal selectable date
        minDate: new Date("2016-03-03"),
        //maxDate: new Date(Date.now()),  // Maximal selectable date
        barTitleIfEmpty: 'Click to select a date'
    };
    constructor(public _cmrcService: CMRCService,
        public _voservice: VOService,
        public _globalURL: GlobalAssets,
        public _access_token: AccessToken,
        public _router: Router,
        public lang: LangulageConf,
        public _commonService: CommonService,
        public _globalVariable: GlobalVariable,
        public _common_message: CommonMessageComponent,
    ) {
        if (sessionStorage.getItem("access_token") == "")
            this._router.navigate(['../login']);

        this.fundDetails = null;
        this.fundRepaymentDetails = null;
        this.totalRequestAmt = null;
        this.totalApprovedAmt = null;
        this.fundTypeId = 1;
        this.RequestAmount = "";
        this.convertedWordsInEnglish = "";
        this.convertedWordsInHindi = "";
        this.convertedWordsInMarathi = "";
    }
    ngOnInit(): void {
        this.getFundRepaymentDetails(this._globalVariable.SelectedFundId);
        this.calculatedFundRepaymentData = {};
    }
    ngAfterViewInit(): void {

    }

    getFundRepaymentDetails(id: number): void {
        this.fundTypeId = id;
        this.totalInterestAmtPaid = 0;
        this._globalURL.isLoading = true;
        this.totalRequestAmt = 0;
        this.totalApprovedAmt = 0;
        this.totalPrincipalAmtPaid = 0;
        this._voservice.get(this._globalURL.GET_FUND_REPAYMENT_DETAILS_VO + id, sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.status === 401) {
                  sessionStorage.clear();
                    this.showErrorMessage = true;
                    this.message = this._common_message.error401message;
                    setInterval(() => {
                        this.showErrorMessage = false;
                    }, 5000);
                    this._globalURL.isLoading = false;
                    this._router.navigate(['../login']);
                }
                if (res.Status == true) {
                    this.fundDetails = res.Data;
                    this.fundRepaymentDetails = res.Data.fundRepaymentModelList;
                    for (var item in res.Data.fundRepaymentModelList) {
                        this.totalInterestAmtPaid = this.totalInterestAmtPaid + res.Data.fundRepaymentModelList[item].ActualInterestAmtPaid;
                        this.totalPrincipalAmtPaid = this.totalPrincipalAmtPaid + res.Data.fundRepaymentModelList[item].ActualPrincipleAmtPaid;

                    }
                    this._globalURL.isLoading = false;
                }
                else {
                    this.showWarningMessage = true;
                    this.message = this._common_message.WarningMsg;
                    setInterval(() => {
                        this.showWarningMessage = false;
                    }, 5000);
                    this._globalURL.isLoading = false;
                }
            }).catch((res) => {
                this.showErrorMessage = true;
                this.message = this._common_message.CatchBlockMsg;
                setInterval(() => {
                    this.showErrorMessage = false;
                }, 5000);
                this._globalURL.isLoading = false;
            })
    }

    getCalculatedFundRepaymentDetails() {
        this._globalURL.isLoading = true;
        this._voservice.get(this._globalURL.GET_CALCULATED_FUND_DETAILS + this._globalVariable.SelectedFundId, sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.status === 401) {
                  sessionStorage.clear();
                    this.showErrorMessage = true;
                    this.message = this._common_message.error401message;
                    setInterval(() => {
                        this.showErrorMessage = false;
                    }, 5000);
                    this._globalURL.isLoading = false;
                    this._router.navigate(['../login']);
                }
                if (res.Status == true) {
                    this._globalURL.isLoading = false
                    this.calculatedFundRepaymentData = res.Data;
                }
                else {
                    this.showWarningMessage = true;
                    this.message = this._common_message.WarningMsg;
                    setInterval(() => {
                        this.showWarningMessage = false;
                    }, 5000);
                    this._globalURL.isLoading = false;
                }
            }).catch((res) => {
                this.showErrorMessage = true;
                this.message = this._common_message.CatchBlockMsg;
                setInterval(() => {
                    this.showErrorMessage = false;
                }, 5000);
                this._globalURL.isLoading = false;
            })
    }
    public ngformdata: NgForm;
    MarkRepayment(data: NgForm, frDt: Date) {
        debugger;
        this.ngformdata = data;
        data.value.PaidDate = this._commonService.convertToDate(frDt); 
        var fundRepaymentSaveData = {
            "FundId": this._globalVariable.SelectedFundId,
            "InterestRate": this.calculatedFundRepaymentData.InterestRate,
            "ExpectedPrincipleAmt": this.calculatedFundRepaymentData.ExpectedPrincipleAmt,
            "ExpectedInterestAmt": this.calculatedFundRepaymentData.ExpectedInterestAmt,
            "ActualPrincipleAmtPaid": data.value.actualPrincipleAmt,
            "ActualInterestAmtPaid": data.value.actualIntrestAmt == null ? this.calculatedFundRepaymentData.ExpectedInterestAmt : data.value.actualIntrestAmt,
            "PaymentMode": data.value.selectedPaymentModeId,
            "PaidDate": data.value.PaidDate
        }
        this._voservice.post(this._globalURL.SAVE_FUND_REPAYMENT, fundRepaymentSaveData, sessionStorage.getItem("access_token"))
            .then((res) => {
                debugger;
                if (res.status === 401) {
                  sessionStorage.clear();
                    this.showErrorMessage = true;
                    this.message = this._common_message.error401message;
                    setInterval(() => {
                        this.showErrorMessage = false;
                    }, 5000);
                    this._globalURL.isLoading = false;
                    this._router.navigate(['../login']);
                }
                if (res.Status == undefined) {
                    if (res.status == 200) {                     
                        // data.control.reset();
                        this._globalURL.isLoading = false;
                        this.message = this._common_message.FundRepaymentMsg;
                        this.showMessage = true;
                        setInterval(() => {
                            this.showMessage = false;
                        }, 5000);
                        this.getFundRepaymentDetails(this._globalVariable.SelectedFundId);
                        this.message = "LOAN APPLICATION CREATED AND SENT TO SCRUTINY COMMITTEE FOR APPROVAL ";
                        this.showMessage = true;
                        setInterval(() => {
                            this.showMessage = false;
                        }, 6000);
                    }
                    else {
                        this.showWarningMessage = true;
                        this.message = this._common_message.WarningMsg;
                        setInterval(() => {
                            this.showWarningMessage = false;
                        }, 5000);
                        this._globalURL.isLoading = false;
                    }
                }
                else {
                    if (res.Status == true) {
                        data.control.reset();
                        this._globalURL.isLoading = false;
                        this.message = this._common_message.FundRepaymentMsg;
                        this.showMessage = true;
                        setInterval(() => {
                            this.showMessage = false;
                        }, 5000);
                        this.getFundRepaymentDetails(this._globalVariable.SelectedFundId);
                    }
                    else {
                        this.showWarningMessage = true;
                        this.message = this._common_message.WarningMsg;
                        setInterval(() => {
                            this.showWarningMessage = false;
                        }, 5000);
                        this._globalURL.isLoading = false;
                    }
                }
            }).catch((res) => {
                this.showErrorMessage = true;
                this.message = this._common_message.CatchBlockMsg;
                setInterval(() => {
                    this.showErrorMessage = false;
                }, 5000);
                this._globalURL.isLoading = false;
            })
    }


    convertToWords(num) {
        this.convertedWordsInEnglish = this._commonService.convertDigitToWordsInEnglish(num);
        this.convertedWordsInHindi = this._commonService.convertDigitToWordsInHindi(num);
        this.convertedWordsInMarathi = this._commonService.convertDigitToWordsInMarathi(num);
    }
    convertToWordsForInterest(num) {
        this.convertedWordsInEnglishForInterest = this._commonService.convertDigitToWordsInEnglish(num);
        this.convertedWordsInHindiForInterest = this._commonService.convertDigitToWordsInHindi(num);
        this.convertedWordsInMarathiForInterest = this._commonService.convertDigitToWordsInMarathi(num);
    }
    // reRenderDT() {
    //     this.dtElement.dtInstance.then((dtInstance: DataTables.Api) => {
    //         dtInstance.destroy();
    //     });
    // }
}


