import { Component, OnInit, ViewChild, AfterViewInit } from '@angular/core';
import { Router } from '@angular/router';
import { GlobalAssets } from '../../../environments/Global';
import { AccessToken } from '../../../environments/access_token';
import { VOService } from '../../../services/vo.services';
import { GlobalVariable } from '../../../environments/GlobalVeriables';
import { LangulageConf } from '../../../environments/language.config';
import { DataTableDirective } from 'angular-datatables';
import { Subject } from 'rxjs';
import { DatepickerOptions } from 'ng2-datepicker';
import * as frLocale from 'date-fns/locale/en';
import { CommonService } from '../../../services/common.service';
import { CommonMessageComponent } from '../../../environments/CommanMssage';

@Component({
    templateUrl: '../attendance/attendance.component.html'
})

export class AttendanceComponent implements OnInit, AfterViewInit {
    // @ViewChild(DataTableDirective)
    // dtElement: DataTableDirective;
    // dtInstance: DataTables.Api;
    // public dtTrigger: Subject<any> = new Subject();
    public editisMeetingListData: boolean = false;
    public meetingDetails: any;
    public memberNames: any;
    public isMeetingListData: boolean = true;
    public showWarningMessage: boolean;
    public showSuccessMessage: boolean;
    public message: string;
    public voMeetingDt: number | null;
    public showErrorMessage: boolean;
    public ActualMeetingDate: Date = new Date ();
    options: DatepickerOptions = {
        minYear: 2016,
        maxYear: (new Date().getFullYear() + 1),
        displayFormat: 'MMM D[,] YYYY',
        barTitleFormat: 'MMMM YYYY',
        dayNamesFormat: 'dd',
        firstCalendarDay: 0, // 0 - Sunday, 1 - Monday
        locale: frLocale,
        //minDate: new Date(Date.now()), // Minimal selectable date
        minDate: new Date("2016-03-03"),
        //maxDate: new Date(Date.now()),  // Maximal selectable date
        barTitleIfEmpty: 'Click to select a date'
    };

    constructor(public _voservice: VOService,
        public _access_token: AccessToken,
        public globalvariable: GlobalVariable,
        public _globalURL: GlobalAssets,
        public lang: LangulageConf,
        public _common_message: CommonMessageComponent,
        public router: Router,
        public commonService: CommonService) {    
        if (sessionStorage.getItem("access_token") == "")
        router.navigate(['../login']);
    }

    ngOnInit() {
        this.getAllVOMembers();
        this.getVOMeetingDate();
        this.getAllVOMembers();
    }
    ngAfterViewInit(): void {
        //this.dtTrigger.next();

    }

    public getVOMeetingDate(): void {
        this._globalURL.isLoading = true;
        this._voservice.get(this._globalURL.GET_VO_MEETING_DT, sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.Status == true)
                    this.voMeetingDt = res.Data;
            }).catch((res) => {
                console.log(res);
            })
    }

    getAllVOMembers() {
        this._globalURL.isLoading = true;
        this._voservice.get(this._globalURL.GET_ALL_VO_SC_MEMEBER, sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.status === 401) {
                    sessionStorage.clear();
                    this.showErrorMessage = true;
                    this.message = this._common_message.error401message;
                    setInterval(() => {
                        this.showErrorMessage = false;
                    }, 5000);
                    this._globalURL.isLoading = false;
                    this.router.navigate(['../login']);
                }
                if (res.Status == true) {
                    this.memberNames = res.Data;
                    this._globalURL.isLoading = false;
                }
                else {
                    this.showWarningMessage = true;
                    this.message = this._common_message.WarningMsg;
                    setInterval(() => {
                        this.showWarningMessage = false;
                    })
                    this._globalURL.isLoading = false;
                }
            }).catch((res) => {
                this.showErrorMessage = true;
                this.message = this._common_message.CatchBlockMsg;
                setInterval(() => {
                    this.showErrorMessage = false;
                }, 5000);
                this._globalURL.isLoading = false;
            })
        //this.reRenderDT();
        this._globalURL.isLoading = false;
    }

    loanMeetingDetails(date: any) {
        date = this.commonService.convertToDate(date);
        this._globalURL.isLoading = true;
        this.meetingDetails = null;
        this._voservice.get(this._globalURL.GET_ATTENDANCE_DETAILS + "?datetime=" + date, sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.status === 401) {
                  sessionStorage.clear();
                    this.showErrorMessage = true;
                    this.message = this._common_message.error401message;
                    setInterval(() => {
                        this.showErrorMessage = false;
                    }, 5000);
                    this._globalURL.isLoading = false;
                    this.router.navigate(['../login']);
                }
                if (res.Status == undefined) {
                    if (res.status == 200) {
                        this.meetingDetails = JSON.parse(res._body).Data;
                        this._globalURL.isLoading = false;
                    }
                    else {
                        this.showWarningMessage = true;
                        this.message = this._common_message.WarningMsg;
                        setInterval(() => {
                            this.showWarningMessage = false;
                        }, 5000);
                        this._globalURL.isLoading = false;
                    }
                }
                else {
                    if (res.Status == true) {
                        this.meetingDetails = res.Data;
                        this._globalURL.isLoading = false;
                    }
                    else {
                        this.showWarningMessage = true;
                        this.message = this._common_message.WarningMsg;
                        setInterval(() => {
                            this.showWarningMessage = false;
                        }, 5000);
                        this._globalURL.isLoading = false;
                    }
                }
            }).catch((res) => {
                this.showErrorMessage = true;
                this.message = this._common_message.CatchBlockMsg;
                setInterval(() => {
                    this.showErrorMessage = false;
                }, 5000);
                this._globalURL.isLoading = false;
            })

    }
    getMeetingDetails() {
        this._globalURL.isLoading = true;
        this._voservice.get(this._globalURL.GET_ATTENDANCE_DETAILS, sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.status === 401) {
                  sessionStorage.clear();
                    this.showErrorMessage = true;
                    this.message = this._common_message.error401message;
                    setInterval(() => {
                        this.showErrorMessage = false;
                    }, 5000);
                    this._globalURL.isLoading = false;
                    this.router.navigate(['../login']);
                }
                if (res.Status == undefined) {
                    if (res.status == 200) {
                        this.meetingDetails = JSON.parse(res._body).Data;
                        this._globalURL.isLoading = false;
                    }
                    else {
                        this.showWarningMessage = true;
                        this.message = this._common_message.WarningMsg;
                        setInterval(() => {
                            this.showWarningMessage = false;
                        }, 5000);
                        this._globalURL.isLoading = false;
                    }
                }
                else {
                    if (res.Status == true) {
                        this.meetingDetails = res.Data;
                        this._globalURL.isLoading = true;
                    }
                    else {
                        this.showWarningMessage = true;
                        this.message = this._common_message.WarningMsg;
                        setInterval(() => {
                            this.showWarningMessage = false;
                        }, 5000);
                        this._globalURL.isLoading = false;
                    }
                }
            }).catch((res) => {
                this.showErrorMessage = true;
                this.message = this._common_message.CatchBlockMsg;
                setInterval(() => {
                    this.showErrorMessage = false;
                }, 5000);
                this._globalURL.isLoading = false;
            })
    }

    showOrAttendanceForm(id: number) {
        if (id == 1) {
            this.getAllVOMembers();
            this.isMeetingListData = false;
            this.editisMeetingListData = true;
        }
        else {
            this.isMeetingListData = true;
        }
    }

    SaveMeetingAttendance(data: any) {
        this._globalURL.isLoading = true;
        var memberIdAndPresentStatus = new Array({ Key: 0, Value: "" });
        memberIdAndPresentStatus.pop();
        for (var item in data) {
            for (var memAttItem in this.memberNames) {
                if (item == this.memberNames[memAttItem].MemberId) {
                    memberIdAndPresentStatus.push({ Key: parseInt(item), Value: data[item] == "p" ? "1" : data[item + "r"] });
                }
            }
        }

        var meetingAttendanceData = {
            "ActualMeetingDate": data.ActualMeetingDate,
            "Address": data.Address,
            "Comment": data.Comment,
            "MeetingDecision": data.MeetingDecision,
            "MeetingReason": data.MeetingReason,
            "MemberIdAndPresentStatus": memberIdAndPresentStatus
        };

        this._voservice.post(this._globalURL.MARK_MEETING_ATTENDANCE, meetingAttendanceData, sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.status === 401) {
                  sessionStorage.clear();
                    this.showErrorMessage = true;
                    this.message = this._common_message.error401message;
                    setInterval(() => {
                        this.showErrorMessage = false;
                    }, 5000);
                    this._globalURL.isLoading = false;
                    this.router.navigate(['../login']);
                }
                if (res.status == 1) {
                    this._globalURL.isLoading = false;
                    this.showSuccessMessage = true;
                    this.message = this._common_message.SaveMeetingAttendenceMsg;
                    setInterval(() => {
                        this.showSuccessMessage = false;
                    }, 5000);
                }
                if (res.status == 2) {
                    this._globalURL.isLoading = false;
                    this.showErrorMessage = true;
                    this.message = this._common_message.SaveErrorAttendenceMsg;
                    setInterval(() => {
                        this.showErrorMessage = false;
                    });
                }
                if (res.status == 3) {
                    this._globalURL.isLoading = false;
                    this.showErrorMessage = true;
                    this.message = this._common_message.SaveErrorAttendenceMsg;
                    setInterval(() => {
                        this.showErrorMessage = false;
                    });
                }
                this._globalURL.isLoading = false;
            })
            .catch((res) => {
                this.showErrorMessage = true;
                this.message = this._common_message.CatchBlockMsg;
                setInterval(() => {
                    this.showErrorMessage = false;
                }, 5000);
                this._globalURL.isLoading = false;
            })
    }


    naviagateToAttendance() {
        this.editisMeetingListData = false;
        this.isMeetingListData = true;
    }
    // reRenderDT() {
    //     this.dtElement.dtInstance.then((dtInstance: DataTables.Api) => {
    //         dtInstance.destroy();
    //         this.dtTrigger.next();
    //     });
    // }
}


