import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { LoginComponent } from '../components/public/login/login.component';
import { PublicComponent } from '../components/public/Public.Component';
import { VOBaseComponent } from '../components/vo/VO.BaseComponent';
import { VoDashboardComponent } from '../components/vo/dashboard/dashboard.component';
import { MemberComponent } from '../components/vo/member/member.component';
import { FixDepositeComponent } from '../components/vo/fix_deposite/fixdeposite.component';
import { LoanComponent } from '../components/vo/loan/loan.component';
import { ShgMasterComponent } from '../components/vo/shg_master/shg_master.component';
import { LoanRepaymentComponent } from '../components/vo/loan/loanrepayment.component';
import { SCBaseComponent } from '../components/sc/base.component';
import { SCDashboardComponent } from '../components/sc/dashboard/dashboard.component';
import { SCLoanComponent } from '../components/sc/loan/loan.component';
import { CMRCBaseComponent } from '../components/cmrc/CMRC.BaseComponent';
import { CMRCDashboardComponent } from '../components/cmrc/dashboard/dashboard.component';
import { VoComponent } from '../components/cmrc/vo/vo.component';
import { CmrcReportComponent } from '../components/cmrc/reports/reports.component';
import { CmrcdistrictComponent } from '../components/cmrc/district/district.component';
import { VOCMembersComponent } from '../components/vo/vo_members/vo_members.component';
import { SCMemberComponent } from '../components/vo/sc_member/sc_member.component';
import { CMRCFundComponent } from '../components/cmrc/fund/fund.component';
import { AttendanceComponent } from '../components/vo/attendance/attendance.component';
import { ALLSCMembers } from '../components/sc/members/member.component';
import { GenderComponent } from '../components/cmrc/gender/gender.component';
import { TalukaComponent } from '../components/cmrc/taluka/taluka.component';
import { EducationComponent } from '../components/cmrc/education/education.component';
import { OccupationComponent } from '../components/cmrc/occupation/occupation.component';
import { SocialcategoryComponent } from '../components/cmrc/social_category/social_category.component';
import { EconomiccategoryComponent } from '../components/cmrc/economic_category/economic_category.component';
import { RationcardComponent } from '../components/cmrc/ration_card/ration_card.component';
import { DesignationComponent } from '../components/cmrc/designation/designation.component';
import { SchemeComponent } from '../components/cmrc/scheme/scheme.component';
import { AreaComponent } from '../components/cmrc/area/area.component';
import { LoanreasonComponent } from '../components/cmrc/loan_reason/loan_reason.component';
import { FundTypeComponent } from '../components/cmrc/fund_types/fund_types.component';
import { MemShareComponent } from '../components/cmrc/memShare/memShare.component';
import { MaritalStatusComponent } from '../components/cmrc/maritalStatus/maritalStatus.component';
import { MinorityComponent } from '../components/cmrc/minority/minority.component';
import { FDComponent } from '../components/cmrc/fd/fd.component';
import { VOFundComponent } from '../components/vo/fund/fund.component';
import { VOFundRepaymentComponent } from '../components/vo/fund/fundrepayment.component';
import { VOOtherIncome } from '../components/vo/income/other_income.component';
import { LedgerComponent } from '../components/cmrc/ledger/ledger.component';
import { CMRCComponent } from '../components/public/cmrc/cmrc.component';
import { MavimComponent } from '../components/public/mavim/mavim.component';
import { WorkComponent } from '../components/public/work/work.component';
import { SLEntryComponent } from '../components/vo/ledgerentries/ledgerentries.component';
import { VOViewAccountTransctionComponent } from '../components/vo/AccountTransaction/accounttransaction.component';
import { VOCMRCAccountTransctionComponent } from '../components/cmrc/AccountTransaction/accounttransaction.component';
import { CMRCFundRepaymentComponent } from '../components/cmrc/fund/fundrepayment.component';
import { CMRCSLEntryComponent } from '../components/cmrc/ledgerentries/ledgerentries.component';
import { VillageComponent } from '../components/cmrc/village/village.component';
import { ActiveAndWithdrawlMembersComponent } from '../components/cmrc/reports/members_report.component';
import { SHGReportsComponent } from '../components/cmrc/reports/shg_report.component';
//import { MemberPerformanceComponent } from '../components/cmrc/reports/memberperformance/memberperfromance';
import { scheme_wise_shg_Component } from '../components/cmrc/reports/scheme_wise_shg_component';
import { SHGByVoComponent } from '../components/cmrc/reports/shgbyvo.component';
import { DISTRICTBaseComponent } from '../components/district/DISTRICT.BaseComponent';
import { MemberPerformanceComponent } from '../components/cmrc/reports/memberperformance/memberperfromance';
import { DistrictDashboardComponent } from '../components/district/dashboard/dashboard.component';
import { VOFinanceComponent } from '../components/vo/finance/finance.component';
import { DistrictFundComponent } from '../components/district/fund/fund.component';
import { DistrictFDComponent } from '../components/district/fd/fd.component';
import { DistrictLoanComponent } from '../components/district/loan/loan.component';
import { ContactComponent } from '../components/public/contact/contact.component';
import { DistrictMemberShareComponent } from '../components/district/memShare/memShare.component';
import { DistrictOtherIncomeComponent } from '../components/district/otherIncome/otherIncome.component';
import { DistrictSHGComponent } from '../components/district/shg/shg.component';
import { DistrictMemberComponent } from '../components/district/member/member.component';
import { LoanTakenOrNotMemberComponent } from '../components/cmrc/reports/loantakenmemornot/loantakenmemornot';
const publicroutes: Routes = [
    {
        path: '', component: PublicComponent, children: [
            { path: '', component: MavimComponent },
            { path: 'public', component: PublicComponent },
            { path: 'mavim', component: MavimComponent },
            { path: 'cmrcp', component: CMRCComponent },
            { path: 'login', component: LoginComponent },
            { path: 'contact', component: ContactComponent },
            { path: 'work', component: WorkComponent }
        ]
    },
    {
        path: 'login', component: PublicComponent, children: [
            { path: '', redirectTo: '/login', pathMatch: 'full' },
            { path: 'login', component: LoginComponent }
        ]
    },
    {
        path: 'vo', component: VOBaseComponent, children: [
            { path: '', component: VoDashboardComponent },
            { path: 'dashbord', component: VoDashboardComponent },
            { path: 'shg_master', component: ShgMasterComponent },
            { path: 'shg_master/member', component: MemberComponent },
            { path: 'member', component: MemberComponent },
            { path: 'dashboad', component: VoDashboardComponent },
            { path: 'fixdeposite', component: FixDepositeComponent },
            { path: 'loan', component: LoanComponent },
            { path: 'loanrepayment', component: LoanRepaymentComponent },
            { path: 'vo_members', component: VOCMembersComponent },
            { path: 'sc_member/vo_members', component: VOCMembersComponent },
            { path: 'fund', component: VOFundComponent },
            { path: 'fundrepayment', component: VOFundRepaymentComponent },
            { path: 'vo_members/sc_member', component: SCMemberComponent },
            { path: 'attendance', component: AttendanceComponent },
            { path: 'income', component: VOOtherIncome },
            { path: 'AccTransaction', component: VOViewAccountTransctionComponent },
            { path: 'SLEntry', component: SLEntryComponent },
            { path: 'Finance', component: VOFinanceComponent }
        ]
    },
    {
        path: 'sc', component: SCBaseComponent, children: [
            { path: '', component: SCLoanComponent },
            { path: 'loan', component: SCLoanComponent },
            { path: 'members', component: ALLSCMembers }
        ]
    },
    {
        path: 'district', component: DISTRICTBaseComponent, children: [
            { path: '', component: DistrictDashboardComponent },
            { path: '', component: DistrictDashboardComponent },
            { path: 'dashbord', component: DistrictDashboardComponent },
            { path: 'fund', component: DistrictFundComponent},
            { path: 'fd', component: DistrictFDComponent},
            { path: 'loan', component:DistrictLoanComponent},
            { path:'memShare' , component:DistrictMemberShareComponent},
            { path:'otherIncome' , component:DistrictOtherIncomeComponent},
            { path:'shg' , component:DistrictSHGComponent},
            { path:'member' ,component:DistrictMemberComponent}
        ]
    },
    {
        path: 'cmrc', component: CMRCBaseComponent, children: [
            { path: '', component: CMRCDashboardComponent },
            { path: 'dashboard', component: CMRCDashboardComponent },
            { path: 'vo', component: VoComponent },
            { path: 'district', component: CmrcdistrictComponent },
            { path: 'fund', component: CMRCFundComponent },
            { path: 'fundRepayment', component: CMRCFundRepaymentComponent },
            { path: 'reports', component: CmrcReportComponent },
            { path: 'reports/scheme-wise-shg', component: scheme_wise_shg_Component },
            { path: 'reports/shg-by-vo', component: SHGByVoComponent },
            { path: 'gender', component: GenderComponent },
            { path: 'ledger', component: LedgerComponent },
            { path: 'taluka', component: TalukaComponent },
            { path: 'education', component: EducationComponent },
            { path: 'occupation', component: OccupationComponent },
            { path: 'social_category', component: SocialcategoryComponent },
            { path: 'economic_category', component: EconomiccategoryComponent },
            { path: 'ration_card', component: RationcardComponent },
            { path: 'designation', component: DesignationComponent },
            { path: 'scheme', component: SchemeComponent },
            { path: 'area', component: AreaComponent },
            { path: 'loan_reason', component: LoanreasonComponent },
            { path: 'reports/member-performance', component: MemberPerformanceComponent },
            {path: 'reports/loantakenmemornot', component: LoanTakenOrNotMemberComponent},
            { path: 'fund_types', component: FundTypeComponent },
            { path: 'memShare', component: MemShareComponent },
            { path: 'maritalStatus', component: MaritalStatusComponent },
            { path: 'minority', component: MinorityComponent },
            { path: 'fd', component: FDComponent },
            { path: 'memShare', component: MemShareComponent },
            { path: 'cmrcAccTransaction', component: VOCMRCAccountTransctionComponent },
            // { path: 'LedgerEntry', component: CMRCSLEntryComponent },
            { path: 'reports/shg-report', component: SHGReportsComponent },
            { path: 'reports/members-report', component: ActiveAndWithdrawlMembersComponent },
            { path: 'LedgerEntry', component: CMRCSLEntryComponent },
            { path: 'village', component: VillageComponent }
            //LoanTakenOrNotMemberComponent
        ]
    }
]

@NgModule({
    imports: [RouterModule.forRoot(publicroutes)],
    exports: [RouterModule]
})

export class PublicRoutes { }



