import { Component, OnInit } from '@angular/core';
import { AccessToken } from '../../environments/access_token';
import { GlobalAssets } from '../../environments/Global';
import { VOService } from '../../services/vo.services';
import { CMRCService } from '../../services/cmrc.services';
import { Router } from '@angular/router';
//import { TreeModel, NodeEvent } from 'ng2-tree';
import { GlobalVariable } from '../../environments/GlobalVeriables';
import { LangulageConf } from '../../environments/language.config';
import { CommonMessageComponent } from '../../environments/CommanMssage';
import { Title } from '@angular/platform-browser';
// import { } from '../../../../node_modules/ng2-tree/styles.css'

//import {} from '../vo'
@Component({
    templateUrl: '../cmrc/index.html',
    styleUrls: ['../../components/app.css']
})

export class CMRCBaseComponent implements OnInit {
    showErrorMessage: boolean;
    public cmrcDetails: any;
    public message: string;
    public showMessage: boolean;
    public today: any;
    constructor(public _cmrcservice: CMRCService,
        public _router: Router,
        public _access_token: AccessToken,
        public _globalURL: GlobalAssets,
        public _globalVariable: GlobalVariable,
        public lang: LangulageConf,
        public _common_message: CommonMessageComponent,
        public titleService: Title) {
        this.today = new Date();
        this.setTitle("CMRC");
    }

    public setTitle(newTitle: string) {
        this.titleService.setTitle(newTitle);
    }

    ngOnInit() {
        this.getCMRCDetail();
    }

    setLang(lang_) {
        if (lang_ === "en") {
            this.lang.en = true;
            this.lang.mr = false;
        }
        if (lang_ === "mr") {
            this.lang.en = false;
        }
        debugger;
    }

    public roleId: any;
    getCMRCDetail(): void {
        debugger;
        let finalUrl = "";
        if (sessionStorage.getItem("roleId") == "2") {
            this._globalURL.get_GET_USER_PROFILE_DETAIL_VO = "";
            finalUrl = this._globalURL.GET_USER_PROFILE_DETAIL + (sessionStorage.getItem("roleId") == null ? "2" : sessionStorage.getItem("roleId"));
        }
        else
            finalUrl = this._globalURL.GET_USER_PROFILE_DETAIL
        this._cmrcservice.get(finalUrl, sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.status === 401) {
                    sessionStorage.clear();
                    this.showErrorMessage = true;
                    this.message = this._common_message.error401message;
                    setInterval(() => {
                        this.showErrorMessage = false;
                    }, 5000);
                    this._globalURL.isLoading = false;
                    this._router.navigate(['../login']);
                }
                if (res.Status == true) {
                    this.cmrcDetails = res.Data;
                    this._globalVariable.cmrcBaseCode = res.Data.Code;
                    this._globalVariable.CMRCName = res.Data.Name
                }
                else {
                    alert("else");
                }
            })
            .catch((err) => {
                alert("catch block");
            })
    }

    changePassword(changePassword: any): void {
        debugger;
        this._globalURL.isLoading = true;
        this._cmrcservice.post(this._globalURL.CHANGEPASSWORD, changePassword, sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.status == 200) {
                    this.showMessage = true;
                    this.message = "Password changed successfully";
                    setInterval(() => {
                    }, 5000);
                }
                else {
                    //    this._router.navigate(['../login']);
                    setInterval(() => {

                        this._globalURL.isLoading = false;
                    }, 5000);
                }
            }).catch((res) => {
                console.log(res.error);
                alert("error");
            })
    }

    SignOut() {
        sessionStorage.clear();
        // sessionStorage.setItem("roleId", null);
    }
}


