import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/Observable';
import { Headers, Response, RequestOptions } from '@angular/http';
import { Http } from '@angular/http'

import 'rxjs/add/operator/map';
import 'rxjs/add/operator/catch';
import 'rxjs/add/operator/toPromise';

@Injectable()

export class FileUploadService {
    options: RequestOptions;
    constructor(private _http: Http) {
    }

    post(url: string, modeldata: any, token: string): Promise<any> {
        debugger;
        return this._http
            .post(url, modeldata, this.getPutHeaders(token))
            .toPromise()
            .catch();
    }


    //To Define the Header information
    private getPutHeaders(_token: string) {
        let headers = new Headers();
        var __token = 'Bearer ' +_token.replace(/['"]+/g, '');     
        // headers.append('Content-Type', undefined);
        // headers.append('Accept', 'application/json');
//        headers.append('Authorization',__token);
        return new RequestOptions({
            headers: headers
        });
    }

    private extractData(res: Response) {
        let body = res.json();
        return body || {};
    }
    // handleError(error: Response) {
    //     console.error(error);
    //     return Observable.throw(error);
    // }
}



