import { Component, OnInit, ViewChild, AfterViewInit } from '@angular/core';
import { constructor } from 'events';
import { Router } from '@angular/router';
import { AccessToken } from '../../../environments/access_token';
import { GlobalAssets } from '../../../environments/Global';
import { CMRCService } from '../../../services/cmrc.services';
import { GlobalVariable } from '../../../environments/GlobalVeriables';
import { LangulageConf } from '../../../environments/language.config';
import { DataTableDirective } from 'angular-datatables';
import { Subject } from 'rxjs';
import { CommonMessageComponent } from '../../../environments/CommanMssage';

@Component({
    templateUrl: '../social_category/social_category.component.html',
    styleUrls: ['../../../components/app.css']
})

export class SocialcategoryComponent implements OnInit, AfterViewInit {
    public showWarningMessage: boolean;
    public showErrorMessage: boolean;
    @ViewChild(DataTableDirective)
    dtElement: DataTableDirective;
    dtInstance: DataTables.Api;
    public dtTrigger: Subject<any> = new Subject();
    public ShowErrormsg: boolean;
    public getSocialCategorys: any;
    public SocialCategoryModel: any;
    public getOccupations: any;
    public message: string;
    public showMessage: boolean;

    constructor(public _cmrcservice: CMRCService,
        public _router: Router,
        public lang: LangulageConf,
        public _access_token: AccessToken,
        public _globalURL: GlobalAssets,
        public _globalVariable: GlobalVariable,
        public _common_message: CommonMessageComponent
    ) {
        _globalURL.isLoading = true;
        if (sessionStorage.getItem("access_token") == ""){
            _router.navigate(['../login']);
        }
    }

    ngOnInit() {
        this.getSocialCategory();
    }

    ngAfterViewInit(): void {
        this.dtTrigger.next();
        this.getSocialCategory();
      
    }
    //GET AREA AND ...
    getSocialCategory(): void {
        this._globalURL.isLoading = true;
        debugger;
        this._cmrcservice.get(this._globalURL.GET_SOCIAL_CATEGORY, sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.status === 401) {
                    sessionStorage.clear();
                    this.showErrorMessage = true;
                    this.message = this._common_message.error401message;
                    setInterval(() => {
                        this.showErrorMessage = false;
                    }, 5000);
                    this._globalURL.isLoading = false;
                    this._router.navigate(['../login']);
                }
                if (res.Status == true) {
                    this.getSocialCategorys = res.Data;
                    this._globalURL.isLoading = false;
                }
                else {
                    this.showWarningMessage = true;
                    this.message = this._common_message.WarningMsg;
                    setInterval(() => {
                        this.showWarningMessage = false;
                    }, 5000);
                    this._globalURL.isLoading = false;
                }
                this.reRenderDT();
            });
    }

    createSocialCategory(data: any): void {
        this._globalURL.isLoading = true;
        debugger;
        let socialCategory = data.Name;
        this._cmrcservice.post((this._globalURL.CREATE_SOCIAL_CATEGORY + socialCategory), data, sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.status === 401) {
                    sessionStorage.clear();
                    this.showErrorMessage = true;
                    this.message = this._common_message.error401message;
                    setInterval(() => {
                        this.showErrorMessage = false;
                    }, 5000);
                    this._globalURL.isLoading = false;
                    this._router.navigate(['../login']);
                }
                if (res.Status == undefined)
                    if (res.status == 200) {
                        let data_ = JSON.parse(res._body);
                        if (data_.Status == true) {
                            this._globalURL.isLoading = false;
                            this.showMessage = true;
                            this.message = "Record is Create succesfully";
                            setInterval(() => {
                                this._globalURL.isSaveSuccessMessage = false;
                                this.showMessage = false;
                            }, 5000);
                            this.getSocialCategory();
                        }
                        else {
                            this.ShowErrormsg = true;
                            this.message = "Record is not created";
                            setInterval(() => {
                                this._globalURL.isSaveSuccessMessage = false;
                                this.ShowErrormsg = false;
                            }, 5000);
                        }
                    }
                    else {
                        this.ShowErrormsg = true;
                        this.message = "Record is not created";
                        setInterval(() => {
                            this._globalURL.isSaveSuccessMessage = false;
                            this.ShowErrormsg = false;
                        }, 5000);
                    }
                else {
                    if (res.Status == 1) {
                        this._globalURL.isLoading = false;
                        this.showMessage = true;
                        this.message = "Record is Create succesfully";
                        setInterval(() => {
                            this._globalURL.isSaveSuccessMessage = false;
                            this.showMessage = false;
                        }, 5000);
                        this.getSocialCategory();
                    }
                }
            })
            .catch((err) => {
                this.ShowErrormsg = true;
                this.message = "Record is not created";
                setInterval(() => {
                }, 5000);
            })
    }

    delete(data: any) {
        debugger;
        this.SocialCategoryModel = data;
        debugger;
    }

    deleteSocialCategory(data: any): void {
        this._globalURL.isLoading = true;
        debugger;
        let id = data.Id;
        let final = this._globalURL.DELETE_SOCIAL_CATEGORY + id;
        this._cmrcservice.post(final, null, sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.status === 401) {
                    sessionStorage.clear();
                    this.showErrorMessage = true;
                    this.message = this._common_message.error401message;
                    setInterval(() => {
                        this.showErrorMessage = false;
                    }, 5000);
                    this._globalURL.isLoading = false;
                    this._router.navigate(['../login']);
                }
                if (res.Status == undefined) {
                    var data = JSON.parse(res._body);
                    if (data.Status == true) {
                        this.getSocialCategory();
                    }
                    else {
                        this.ShowErrormsg = true;
                        this.message = "Record is not delete";
                        setInterval(() => {
                            this._globalURL.isSaveSuccessMessage = false;
                            this.ShowErrormsg = false;
                        }, 5000);
                    }
                }
                else {
                    if (res.Status == true) {
                        this.getSocialCategory();
                    }
                    else {
                        this.ShowErrormsg = true;
                        this.message = "Record is not delete";
                        setInterval(() => {
                            this._globalURL.isSaveSuccessMessage = false;
                            this.ShowErrormsg = false;
                        }, 5000);
                    }
                }
                this.getSocialCategory();
                this._globalURL.isLoading = false;
                this.showMessage = true;
                this.message = "Record is Delete succesfully";
                setInterval(() => {
                    this.showMessage = false;
                }, 5000);
            })
            .catch((err) => {
                this.ShowErrormsg = true;
                this.message = "Record is not deleted";
                setInterval(() => {
                }, 5000);
            })
    }
    reRenderDT() {
        this.dtElement.dtInstance.then((dtInstance: DataTables.Api) => {
            dtInstance.destroy();
            this.dtTrigger.next();
        });
    }
}













