import { Component, OnInit, ViewChild, AfterViewInit } from '@angular/core';
import { constructor } from 'events';
import { Router } from '@angular/router';
import { AccessToken } from '../../../environments/access_token';
import { GlobalAssets } from '../../../environments/Global';
import { CMRCService } from '../../../services/cmrc.services';
import { GlobalVariable } from '../../../environments/GlobalVeriables';
import { LangulageConf } from '../../../environments/language.config';
import { DataTableDirective } from 'angular-datatables';
import { Subject } from 'rxjs';
import { CommonMessageComponent } from '../../../environments/CommanMssage';
import { NgForm } from '@angular/forms';


@Component({
    templateUrl: '../education/education.component.html',
    styleUrls: ['../../../components/app.css']
})

export class EducationComponent implements OnInit, AfterViewInit {
    public showWarningMessage: boolean;
    public showErrorMessage: boolean;
    public showMessage: boolean;
    @ViewChild(DataTableDirective)
    dtElement: DataTableDirective;
    dtInstance: DataTables.Api;
    public dtTrigger: Subject<any> = new Subject();
    public ShowErrormsg: boolean;
    public educationModel: any;
    public getEducationDetails: any;
    public message: string;
    
    constructor(public _cmrcservice: CMRCService,
        public _router: Router,
        public lang: LangulageConf,
        public _access_token: AccessToken,
        public _globalURL: GlobalAssets,
        public _globalVariable: GlobalVariable,
        public _common_message: CommonMessageComponent
    ) {
        _globalURL.isLoading = true;
        if (sessionStorage.getItem("access_token") == "") {
            _router.navigate(['../login']);
        }

    }

    ngOnInit() {
        //this.getEducation();
    }

    ngAfterViewInit(): void {
        this.dtTrigger.next();
        this.getEducation();

    }

    //GET AREA AND ...
    getEducation(): void {
        this._globalURL.isLoading = true;
        debugger;
        this._cmrcservice.get(this._globalURL.GET_EDUCATION, sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.status === 401) {
                    sessionStorage.clear();
                    this.showErrorMessage = true;
                    this.message = this._common_message.error401message;
                    setInterval(() => {
                        this.showErrorMessage = false;
                    }, 5000);
                    this._globalURL.isLoading = false;
                    this._router.navigate(['../login']);
                }
                if (res.Status == true) {
                    this.getEducationDetails = res.Data;
                    this._globalURL.isLoading = false;
                    // this.showMessage = true;
                    // this.message = this._common_message.AddEducationMsg;
                    // setInterval(() => {
                    //     this.showMessage = false;
                    // }, 5000);
                    
                }
                else {
                    this._globalURL.isLoading = false;
                    this.showErrorMessage = true;
                    this.message = this._common_message.AddEducationMsg;
                    setInterval(() => {
                        this.showErrorMessage = false;
                    }, 5000);
        
                }
                this.reRenderDT();
            }).catch((res) =>{
                this._globalURL.isLoading = false;
                this.message = this._common_message.CatchBlockMsg;
                this.showErrorMessage = true;
                setInterval(() => {
                    this.showErrorMessage = false;
                }, 5000);
            })
    }

    createEducation(data1: NgForm): void {
        this._globalURL.isLoading = true;
        debugger;
        var educationName = data1.value.EducationName;
        this._cmrcservice.post((this._globalURL.ADD_EDUCATION + educationName), "data", sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.status === 401) {
                    sessionStorage.clear();
                    this.showErrorMessage = true;
                    this.message = this._common_message.error401message;
                    setInterval(() => {
                        this.showErrorMessage = false;
                    }, 5000);
                    this._globalURL.isLoading = false;
                    this._router.navigate(['../login']);
                }
                if (res.Status == undefined)
                    if (res.status == 200) {
                        let data_ = JSON.parse(res._body);
                        if (data_.Status == true) {
                            data1.control.reset();
                            this._globalURL.isLoading = false;
                            this.showMessage = true;
                            this.message = this._common_message.AddEducationMsg;
                            setInterval(() => {
                                this.showMessage = false;
                            }, 5000);
                            this.getEducation();
                        }
                        else {
                            this._globalURL.isLoading = false;
                            this.message = this._common_message.EducationNotAddMsg;
                            this.showErrorMessage = true;
                            setInterval(() => {
                                this.showErrorMessage = false;
                            }, 5000);
                            this._globalURL.isLoading = false;
                        }
                    }
                    else {
                        this.message = this._common_message.EducationNotAddMsg;
                        this.showErrorMessage = true;
                        setInterval(() => {
                            this.showErrorMessage = false;
                        }, 5000);
                        this._globalURL.isLoading = false;
                    }
            })
            .catch((err) => {
                this._globalURL.isLoading = false;
                this.message = this._common_message.CatchBlockMsg;
                this.showErrorMessage = true;
                setInterval(() => {
                    this.showErrorMessage = false;
                }, 5000);
            })
    }

    delete(data: any) {
        debugger;
        this.educationModel = data;
        debugger;
    }

    deleteEducation(data: any): void {
        this._globalURL.isLoading = true;
        debugger;
        let id = data.Id;
        let final = this._globalURL.DELETE_EDUCATION + id;
        this._cmrcservice.post(final, null, sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.status === 401) {
                    sessionStorage.clear();
                    this.showErrorMessage = true;
                    this.message = this._common_message.error401message;
                    setInterval(() => {
                        this.showErrorMessage = false;
                    }, 5000);
                    this._globalURL.isLoading = false;
                    this._router.navigate(['../login']);
                }
                if (res.Status == undefined) {
                    var data = JSON.parse(res._body);
                    if (data.Status == true) {
                        this._globalURL.isLoading = false;
                        this.showMessage = true;
                        this.message = this._common_message.DeleteEducationMsg;
                        setInterval(() => {
                            this.showMessage = false;
                        }, 5000);
                        this.getEducation();
                    }
                    else {
                        this.message = this._common_message.ErrorDeleteEducationMsg;
                        this.showErrorMessage = true;
                        setInterval(() => {
                            this.showErrorMessage = false;
                        }, 5000);
                        this._globalURL.isLoading = false;
                    }
                }
            }).catch((err) => {
                this._globalURL.isLoading = false;
                this.message = this._common_message.CatchBlockMsg;
                this.showErrorMessage = true;
                setInterval(() => {
                    this.showErrorMessage = false;
                }, 5000);
            })
    }
    reRenderDT() {
        this.dtElement.dtInstance.then((dtInstance: DataTables.Api) => {
            dtInstance.destroy();
            this.dtTrigger.next();
        });
    }
}













