import { Component, ViewChild, OnInit, ElementRef } from '@angular/core';
import { Router } from '@angular/router';
import { GlobalAssets } from "../../environments/Global";
import { FileUploadService } from '../../services/FileService/file_upload.service';
import { LoginComponent } from './login/login.component';
import *as jsPDF from 'jspdf';
// import { DatepickerOptions } from 'ng2-datepicker';
// import * as frLocale from 'date-fns/locale/en';
// import { DatepickerOptions } from 'ng2-datepicker';
import '../../../../src/config.js'
import { Title } from '@angular/platform-browser';
import { LoginService } from '../../services/login.service';
import { GlobalVariable } from '../../environments/GlobalVeriables';

@Component({
    templateUrl: '../public/index.html',
    styleUrls: ['../../components/app.css']
})
export class PublicComponent {
    public date: Date = new Date();
    //    @ViewChild('closeBtn') closeBtn_: ElementRef;
    _message = "Hey from parent";
    uploadFile: any;
    public Cmrcs: any;
    constructor(private router: Router,
        private _globalAssets: GlobalAssets,
        public globalVariables: GlobalVariable,
        private _fileUploadService: FileUploadService,
        public titleService: Title,
        public loginService: LoginService) {
        this.setTitle("MAVIM - CSG");
        //this.getAllCMRCKVP();
    }

    public setTitle(newTitle: string) {
        this.titleService.setTitle(newTitle);
    }

    handleUpload(data: any): void {
        debugger;
        if (data && data.response) {
            data = JSON.parse(data.response);
            this.uploadFile = data;
        }
    }

    fileChange(metadata: any) {
        debugger;
        var formData = new FormData();
        formData.append("RefId", "2023");
        formData.append("file", metadata);
        this._fileUploadService.post("http://localhost:59611/api/Account/ChangeProfileImage?RefId=2012"
            , formData, "").then((res) => {
                alert("Success");
            })
    }

    getAllCMRCKVP(): void {
        debugger;
        this._globalAssets.isLoading = true;
        this.loginService.get(this._globalAssets.GET_ALL_CMRC_KPV, sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.Status == true) {
                    this.Cmrcs = res.Data;
                    this._globalAssets.isLoading = false;
                }
                else {
                    this._globalAssets.isLoading = false;
                }
            });
    }
    public saveCMRCId(cmrcId: number) {
        this.globalVariables.cmrcIdforPublicPage = cmrcId;
    }
}



