import { Component, OnInit } from '@angular/core';
import { VOService } from '../../services/vo.services';
import { EmpModel } from '../../services/empModel';
import { GlobalAssets } from '../../environments/Global';
import { LangulageConf } from '../../environments/language.config';
import { Router } from '@angular/router';
@Component({
    templateUrl: '../district/index.html'
})

export class DISTRICTBaseComponent implements OnInit {
    public today: any;;
    constructor(public _voService: VOService
        , public _globalURL: GlobalAssets,
        public lang: LangulageConf,
        public router: Router) {
        this.today = new Date();
    }
    ngOnInit() {

    }

    setLang(lang_) {
        debugger;
        if (lang_ === "en") {
            this.lang.en = true;
            this.lang.mr = false;
        }
        if (lang_ === "mr") {
            this.lang.mr = true;
            this.lang.en = false;
        }

    }

    SignOut() {
        debugger;
        // sessionStorage.setItem("roleId", null);
    }
}


