import { Component, OnInit } from '@angular/core';
import { VOService } from '../../services/vo.services';
import { EmpModel } from '../../services/empModel';
import { GlobalAssets } from '../../environments/Global';
import { AccessToken } from '../../environments/access_token';
import { GlobalVariable } from '../../environments/GlobalVeriables';
import { LangulageConf } from '../../environments/language.config';
import { CommonMessageComponent } from '../../environments/CommanMssage';
import { Router } from '@angular/router';
import { Title } from '@angular/platform-browser';
//import {} from '../vo'
//import {translate} from 'google-translate-api';
@Component({
    templateUrl: '../vo/index.html',
    styleUrls: ['../../components/app.css']
})

export class VOBaseComponent implements OnInit {
    showWarningMessage: boolean;
    message: any;
    showErrorMessage: boolean;
    public today: Date;
    public voBaseDetails: any;
    public OldPassword: any;
    public NewPassword: any;
    public ConfirmPassword: any;
    public dist: string;
    public VillageName: string;
    public selectLang: string = "en";
    constructor(public _voService: VOService,
        public _globalURL: GlobalAssets,
        public _access_token: AccessToken,
        public lang: LangulageConf,
        public globalVariable: GlobalVariable,
        public _router: Router,
        public _common_message: CommonMessageComponent,
        public titleService: Title)
         {
         this.setTitle("VILLAGE ORGANIZATION");
        this.voBaseDetails = null;
        this.today = new Date;
    }
    public setTitle(newTitle: string) {
        this.titleService.setTitle(newTitle);
    }
    ngOnInit() {
        this.getVODetail();
    }

    setLang(lang_) {
        if (lang_ === "en") {
            this.lang.en = true;
            this.lang.mr = false;
        }
        if (lang_ === "mr") {
            this.lang.mr = true;
            this.lang.en = false;
        }

    }

    changePassword(changePasswordCredentials: any): void {
        debugger
        //this._voService.post(this._globalURL.CHANGE_PASSWORD, changePasswordCredentials)
        this._voService.post(this._globalURL.CHANGE_PASSWORD, changePasswordCredentials, sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.Status == 200) {
                    alert("Password");
                }
                else {

                }
            }).catch((err) => {
                alert("Failed");
            })
    }
    public roleId: any;
    getVODetail(): void {
        debugger;

        debugger;
        let finalUrl = "";
        if (sessionStorage.getItem("roleId") == "3") {
            this._globalURL.get_GET_USER_PROFILE_DETAIL = "";
            finalUrl = this._globalURL.GET_USER_PROFILE_DETAIL + (sessionStorage.getItem("roleId") == null ? "3" : sessionStorage.getItem("roleId"));
        }
        else
            finalUrl = this._globalURL.GET_USER_PROFILE_DETAIL
        this._voService.get(finalUrl, sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.status === 401) {
                    sessionStorage.clear();
                    this.showErrorMessage = true;
                    this.message = this._common_message.error401message;
                    setInterval(() => {
                        this.showErrorMessage = false;
                    }, 5000);
                    this._globalURL.isLoading = false;
                    this._router.navigate(['../login']);
                }
                if (res.Status == true) {
                    this.voBaseDetails = res.Data;
                    this.dist = this.voBaseDetails.District;
                    this.VillageName = this.voBaseDetails.Village;
                    this.globalVariable.voBaseCode = res.Data.Code;
                    this.globalVariable.cmrcBaseCode = res.Data.ParentCode;
                    this.globalVariable.VOName = res.Data.Name;
                    this.globalVariable.CMRCName = res.Data.CMRCName;
                }
                else {
                    this.showWarningMessage = true;
                    this.message = this._common_message.WarningMsg;
                    setInterval(() => {
                        this.showWarningMessage = false;
                    }, 5000);
                    this._globalURL.isLoading = false;
                }
            })
            .catch((res) => {
                this.showErrorMessage = true;
                console.log(res.error);
                this.message = this._common_message.CatchBlockMsg;
                setInterval(() => {
                    this.showErrorMessage = false;
                }, 5000);
                this._globalURL.isLoading = false;
            })
    }

    SignOut() {
        sessionStorage.clear();
        sessionStorage.setItem("roleId", null);
    }
}


