import { Component, OnInit } from '@angular/core';
import { CurrencyPipe } from '@angular/common';
import { VOService } from '../../../services/vo.services';
import { AccessToken } from '../../../environments/access_token';
import { GlobalAssets } from '../../../environments/Global';
import { Router } from '@angular/router';
import { LoanModel } from './loan.model';
import { LangulageConf } from '../../../environments/language.config';

@Component({
    templateUrl: '../loan/loan.component.html'
})
export class SCLoanComponent implements OnInit {
    message: string;
    showMessage: boolean;
    public shgLoanDetails: any;
    public memberLoanDetails: any;
    public SHGNameAndId: any;
    public selectedShgId: any;
    public membersNameAndId: any;
    public memberId: number;
    public selectedMemberId: any;
    public memLoanRepaymentDetails: string;
    public Action: string;
    public LoanId: string;
    public repaymentDetails: string;
    public amount: any;
    public shareAmount: any;
    public totalLoanAmt: any;
    public totalPrinciple; any;
    public totalPayable: any;
    public selectedLoanStatus: number;
    public LoanDetails: any;
    public calculateButtonValue: string;
    public memberDetailsCallBack: string;
    public isSharedPaid: boolean = false;
    public totalLoanAmount: number;
    constructor(public _voservice: VOService,
        public _access_token: AccessToken,
        public _globalURL: GlobalAssets,
        public lang: LangulageConf,
        public router: Router) {
        _globalURL.isLoading = false;
        this.shgLoanDetails = null;
        this.memberLoanDetails = null;
        this.SHGNameAndId = null;
        this.selectedShgId = null;
        this.membersNameAndId = null;
        this.selectedMemberId = null;
        this.memLoanRepaymentDetails = null;
        this.LoanId = null;
        this.Action = null;
        this.amount = null;
        this.shareAmount = null;
        this.LoanDetails = null;
        this.totalLoanAmt = 0;
        this.totalPayable = 0;
        this.totalPrinciple = 0;
        this.selectedLoanStatus = 0;
        this.totalLoanAmount = 0;
        this.disable = false;
        this.calculateButtonValue = "SAVE ACTION";
        if (sessionStorage.getItem("access_token") == "")
            router.navigate(['../login']);
        //s this._globalURL.isSaveSuccessMessage =  ;
    }

    ngOnInit() {
        this._globalURL.isLoading = true;
        this.selectedLoanStatus = 2;
        this.loanSHGLoanData(2);
        this.getFund();
        this.showComment = false;
    }

    loanSHGLoanData(loanStatus: number) {
        debugger;
        this.selectedLoanStatus = loanStatus;
        this._globalURL.isLoading = true;
        this._voservice.get((this._globalURL.MEMBER_LOAN_DETAILS + "" + loanStatus), sessionStorage.getItem("access_token"))
            .then((res) => {
                debugger;
                this.memberLoanDetails = res.Data;
                for (var item in this.memberLoanDetails)
                    this.totalLoanAmount = this.totalLoanAmount + this.memberLoanDetails[item].LoanAmount;
                this.memLoanRepaymentDetails = null;
                this._globalURL.isLoading = false;
            })
            .catch((res) => {
            })
    }

    clear() {
        this.memLoanRepaymentDetails = null;
        this.disable = false;
    }
    public disable: boolean = false;
    calculateRepayment(data: any) {
        debugger;
        this.disable = true;
        this.calculateButtonValue = "calculating ...";

        //this.memLoanRepaymentDetails = null;
        this._globalURL.isLoading = true;
        this.totalLoanAmt = 0;
        this.totalPayable = 0;
        this.totalPrinciple = 0;
        this._voservice.get(
            (this._globalURL.CALCULATE_REPAYMENT + "?amount=" + data.approvedLoanAmt +
                "&interest=" + data.interest + "&tenure=" + data.tenure + "&effectiveDate=" + data.effectiveDate),
            sessionStorage.getItem("access_token"))
            .then((res) => {
                this.memLoanRepaymentDetails = res.Data;
                for (var item in res.Data) {
                    this.totalLoanAmt = this.totalLoanAmt + res.Data[item].FixedPayableAmt;
                    this.totalPayable = this.totalPayable + res.Data[item].TotalPayableAmt;
                    this.totalPrinciple = this.totalPrinciple + res.Data[item].InterestAmt;
                }
                this._globalURL.isLoading = false;
                //this.loadLoanRepayment();
            })
    }

    public shareAmr: any = null;
    public isShareAmt: boolean = false;
    public shgId: number = 0;
    setLoanId(loanData: any) {
        debugger;
        this.disable = false;
        this.memLoanRepaymentDetails = null;
        this.LoanId = loanData.LoanId;
        this.amount = loanData.LoanAmount;
        this.memberDetailsCallBack = loanData;
        // if (loanData.ShareAmount != null || loanData.ShareAmount != 0)
        //     this.isShareAmt = true;
        this.shareAmount = loanData.ShareAmount;
        this.isSharedPaid = this.shareAmount == 0 || this.shareAmount == null ? false : true;
        this.memberId = loanData.MemberId;
        this.shgId = loanData.SHGId;
    }

    public showComment: boolean;
    setStatus(id: any) {
        this.Action = id;
        this.showComment = id == 1 ? false : true;
    }

    UpdateStatus(data: any) {
        this.disable = false;
        let loanData = {
            'LoanAmount': this.amount, 'LoanApprovedAmount': data.approvedLoanAmt,
            'Status': this.Action, 'EffectiveStartDate': data.effectiveDate,
            'LoanId': this.LoanId, 'IntrestRate': data.interest, 'Tenure': data.tenure,
            'ShareAmount': this.isSharedPaid ? null : data.shareAmount, 'MemberId': this.memberId,
            'PaymentMode': data.selectedPaymentModeId, 'RejectReason': data.RejectReason,
            'ApprovedDate': data.ApprovedDate, 'SHGId': this.shgId
        };
        // this.Action;
        // this.LoanId;
        debugger;
        this._globalURL.isLoading = true;
        this._voservice.post(this._globalURL.UPDATE_STATUS, loanData, sessionStorage.getItem("access_token"))
            .then((res) => {
                this._globalURL.isLoading = true;
                this._globalURL.isSaveSuccessMessage = true;
                this.showMessage = true;
                if (this.Action == "1")
                    this.message = "LOAN APPLICATION APPROVED SUCCESSFULLY";
                else
                    this.message = "LOAN APPLICATION REJECTED SUCCESSFULLY";
                setInterval(() => {
                    this.showMessage = false;
                }, 6000);
                this.loanSHGLoanData(this.selectedLoanStatus);
            })
    }

    public totalApprovedAmt: number;
    getFund(): void {
        this._globalURL.isLoading = true;
        this.totalApprovedAmt = 0;
        this._voservice.get(this._globalURL.GET_VO_AVAILABLE_FUND, sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.Status == true) {
                    //this.reRenderDT();
                    this.totalApprovedAmt = res.Data;
                    // for (var item in res.Data) {
                    //     this.totalApprovedAmt = this.totalApprovedAmt + res.Data[item].ApproveAmount;
                    // }
                    this._globalURL.isLoading = false;
                }
                else {
                    this._globalURL.isLoading = false;
                }
            })
            .catch((res) => {
                this._globalURL.isLoading = false;
            });
    }

    public ifAmountExceed: boolean = false;
    checkIfAmountExceed(enteredAmt: number) {
        debugger;
        if (this.amount < enteredAmt)
            this.ifAmountExceed = true
        else this.ifAmountExceed = false;
    }
}


