import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { CMRCService } from '../../../../services/cmrc.services';
import { GlobalVariable } from '../../../../environments/GlobalVeriables';
import { ExportToPDF } from '../../../../services/reports/exportpdf';
import { GlobalAssets } from '../../../../environments/Global';
import { AccessToken } from '../../../../environments/access_token';

@Component({
    templateUrl: 'memberperformance.html'
})

export class MemberPerformanceComponent implements OnInit {
    schemes: any;
    public memberLoanPerformanceData: any;
    public MemberLoanDetails: any;
    public VOKVP: any;
    constructor(public _cmrcservice: CMRCService,
        public _router: Router,
        public _access_token: AccessToken,
        public globalVariable: GlobalVariable,
        public _globalURL: GlobalAssets,
        public exportToPDF: ExportToPDF) {
        _globalURL.isLoading = false;
        this.memberLoanPerformanceData = null;
        if (sessionStorage.getItem("access_token") == ""){
            _router.navigate(['../../login']);
        }
    }

    ngOnInit() {
        this.getVoIdAndName();
    }

    getVoIdAndName(): void {
        this._globalURL.isLoading = true;
        this._cmrcservice.get(this._globalURL.GET_VO_CMRCID, sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.Status == true) {
                    this.VOKVP = res.Data;
                    this._globalURL.isLoading = false;
                }
                else {
                    this._globalURL.isLoading = false;
                }
            });
    }

    public LoanMembersLoanPerformance(voId: number, fromDt: any, toDt: any) {
        this._globalURL.isLoading = true;
        fromDt = fromDt == 0 ? null : fromDt;
        toDt = toDt == 0 ? null : toDt;
        this._cmrcservice.get((this._globalURL.MEMBER_LOAN_PERFORMANCE + "?voId=" + voId + "&fromDt=" + fromDt + "&toDt=" + toDt + "&Grade=0"), sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.Status == true) {
                    debugger;
                    this.memberLoanPerformanceData = res.Data;
                    this.MemberLoanDetails = this.memberLoanPerformanceData.LoanMemberData;
                    this._globalURL.isLoading = false;
                }
            }).catch((res) => {
                console.log(res);
                this._globalURL.isLoading = false;
            })
    }

    public ExportMemberPerformance() {
        this._globalURL.isLoading = true;
        var headerData = {
            "VOName": this.globalVariable.VOName,
            "Code": this.globalVariable.voBaseCode,
        };
        this.exportToPDF.ExportMemberPerformance(this.memberLoanPerformanceData, headerData);
        this._globalURL.isLoading = false;
    }
}


