import { Component, OnInit, ViewChild, AfterViewInit } from '@angular/core';
import { constructor } from 'events';
import { Router } from '@angular/router';
import { AccessToken } from '../../../environments/access_token';
import { GlobalAssets } from '../../../environments/Global';
import { CMRCService } from '../../../services/cmrc.services';
import { GlobalVariable } from '../../../environments/GlobalVeriables';
import { LangulageConf } from '../../../environments/language.config';
import { DataTableDirective } from 'angular-datatables';
import { Subject } from 'rxjs';
import { CommonMessageComponent } from '../../../environments/CommanMssage';


@Component({
    templateUrl: '../scheme/scheme.component.html',
    styleUrls: ['../../../components/app.css'],
})


export class SchemeComponent implements OnInit, AfterViewInit {
    showErrorMessage: boolean;
    @ViewChild(DataTableDirective)
    dtElement: DataTableDirective;
    dtInstance: DataTables.Api;
    public dtTrigger: Subject<any> = new Subject();
    public ShowErrormsg: boolean;
    public SchemeModel: any;
    public getSchemes: any;
    public message: string;
    public showMessage: boolean;

    constructor(public _cmrcservice: CMRCService,
        public _router: Router,
        public lang: LangulageConf,
        public _access_token: AccessToken,
        public _globalURL: GlobalAssets,
        public _globalVariable: GlobalVariable,
        public _common_message: CommonMessageComponent
    ) {
        _globalURL.isLoading = true;
        if (sessionStorage.getItem("access_token") == "") {
            _router.navigate(['../login']);
        }

    }

    ngOnInit() {
        this.getScheme();
    }
    ngAfterViewInit(): void {
        this.dtTrigger.next();
        this.getScheme();

    }

    //GET AREA AND ...
    getScheme(): void {
        this._globalURL.isLoading = true;
        debugger;
        this._cmrcservice.get(this._globalURL.GET_SCHEME_DETAILS, sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.Status == true) {
                    this.getSchemes = res.Data;
                    this._globalURL.isLoading = false;
                }
                else {
                    this._globalURL.isLoading = false;
                    this.message = this._common_message.CatchBlockMsg;
                    this.showErrorMessage = true;
                    setInterval(() => {
                        this.showErrorMessage = false;
                    }, 5000);
                }
                this.reRenderDT();
            });
    }

    createScheme(data: any): void {
        this._globalURL.isLoading = true;
        debugger;
        let schemeName = data.Name;
        this._cmrcservice.post((this._globalURL.CREATE_SCHEME + schemeName), data, sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.Status == undefined)
                    if (res.status == 200) {
                        let data_ = JSON.parse(res._body);
                        if (data_.Status == true) {
                            this._globalURL.isLoading = false;
                            this.showMessage = true;
                            this.message = this._common_message.AddSchemeMsg;
                            setInterval(() => {
                                this.showMessage = false;
                            }, 5000);
                            this.getScheme();
                        }
                        else {
                            this._globalURL.isLoading = false;
                            this.showErrorMessage = true;
                            this.message = this._common_message.ErrorAddSchemeMsg;
                            setInterval(() => {
                                this.showErrorMessage = false;
                            }, 5000);
                            this.getScheme();
                        }
                    }
                    else {
                        this._globalURL.isLoading = false;
                        this.showErrorMessage = true;
                        this.message = this._common_message.ErrorAddSchemeMsg;
                        setInterval(() => {
                            this.showErrorMessage = false;
                        }, 5000);
                    }
                else {
                    if (res.status == 200) {
                        this._globalURL.isLoading = false;
                        this.showMessage = true;
                        this.message = this._common_message.AddSchemeMsg;
                        setInterval(() => {
                            this.showMessage = false;
                        }, 5000);
                        this.getScheme();
                    }
                }
            }).catch((err) => {
                this.ShowErrormsg = true;
                this.message = "Record is not created";
                setInterval(() => {
                }, 5000);
            })
    }


    delete(data: any) {
        debugger;
        this.SchemeModel = data;
        debugger;
    }

    deleteScheme(data: any): void {
        this._globalURL.isLoading = true;
        debugger;
        let id = data.Id;
        let final = this._globalURL.DELETE_SCHEME + id;
        this._cmrcservice.post(final, "data", sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.status === 401) {
                    sessionStorage.clear();
                    this.showErrorMessage = true;
                    this.message = this._common_message.error401message;
                    setInterval(() => {
                        this.showErrorMessage = false;
                    }, 5000);
                    this._globalURL.isLoading = false;
                    this._router.navigate(['../login']);
                }
                if (res.Status == undefined)
                    if (res.status == 200) {
                        let data_ = JSON.parse(res._body);
                        if (data_.Status == true) {
                            this._globalURL.isLoading = false;
                            this.showMessage = true;
                            this.message = this._common_message.DeleteSchemeMsg;
                            setInterval(() => {
                                this.showMessage = false;
                            }, 5000);
                            this.getScheme();
                        }
                        else {
                            this._globalURL.isLoading = false;
                            this.message = this._common_message.ErrorDeleteSchemeMsg;
                            this.showErrorMessage = true;
                            setInterval(() => {
                                this.showErrorMessage = false;
                            }, 5000);
                        }
                    }
                    else {
                        this._globalURL.isLoading = false;
                        this.message = this._common_message.ErrorDeleteSchemeMsg;
                        this.showErrorMessage = true;
                        setInterval(() => {
                            this.showErrorMessage = false;
                        }, 5000);
                    }
                else {
                    if (res.status == 200) {
                        this._globalURL.isLoading = false;
                        this.showMessage = true;
                        this.message = this._common_message.DeleteSchemeMsg;
                        setInterval(() => {
                            this.showMessage = false;
                        }, 5000);
                        this.getScheme();
                    }
                    else {
                        this._globalURL.isLoading = false;
                        this.message = this._common_message.ErrorDeleteSchemeMsg;
                        this.showErrorMessage = true;
                        setInterval(() => {
                            this.showErrorMessage = false;
                        }, 5000);
                    }
                }
            }).catch((err) => {
                this._globalURL.isLoading = false;
                this.message = this._common_message.CatchBlockMsg;
                this.showErrorMessage = true;
                setInterval(() => {
                    this.showErrorMessage = false;
                }, 5000);
            })
    }

    reRenderDT() {
        this.dtElement.dtInstance.then((dtInstance: DataTables.Api) => {
            dtInstance.destroy();
            this.dtTrigger.next();
        });
    }
}













