import { Component, OnInit, ViewChild, AfterViewInit } from '@angular/core';
import { constructor } from 'events';
import { Router } from '@angular/router';
import { AccessToken } from '../../../environments/access_token';
import { GlobalAssets } from '../../../environments/Global';
import { CMRCService } from '../../../services/cmrc.services';
import { GlobalVariable } from '../../../environments/GlobalVeriables';
import { LangulageConf } from '../../../environments/language.config';
import { Subject } from 'rxjs';
import { DataTableDirective } from 'angular-datatables';
import { NgForm } from '@angular/forms';
import { CommonMessageComponent } from '../../../environments/CommanMssage';


@Component({
    templateUrl: '../fund_types/fund_types.component.html',
    styleUrls: ['../../../components/app.css'],
})


export class FundTypeComponent {
    showWarningMessage: boolean;
    showErrorMessage: boolean;
    @ViewChild(DataTableDirective)
    dtElement: DataTableDirective;
    dtInstance: DataTables.Api;
    public dtTrigger: Subject<any> = new Subject();
    public ShowErrormsg: boolean;
    public fundTypesModel: any;
    public fundModel: any;
    public message: string;
    public showMessage: boolean;
    public getAllFundTypes: any;
    constructor(public _cmrcservice: CMRCService,
        public _router: Router,
        public lang: LangulageConf,
        public _access_token: AccessToken,
        public _globalURL: GlobalAssets,
        public _globalVariable: GlobalVariable,
        public _common_message: CommonMessageComponent
    ) {
        _globalURL.isLoading = true;
        if (sessionStorage.getItem("access_token") == "") {
            _router.navigate(['../login']);
        }
    }
    ngOnInit() {
        this.getFundType();
    }
    ngAfterViewInit() {
        this.dtTrigger.next();      
        this.getFundType();
    }

    //GET ALL Fund Type 
    getFundType(): void {
        this._globalURL.isLoading = true;
        debugger;
        this._cmrcservice.get(this._globalURL.GET_FUNDTYPE, sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.status === 401) {
                    sessionStorage.clear();
                    this.showErrorMessage = true;
                    this.message = this._common_message.error401message;
                    setInterval(() => {
                        this.showErrorMessage = false;
                    }, 5000);
                    this._globalURL.isLoading = false;
                    this._router.navigate(['../login']);
                }
                if (res.Status == true) {
                    this.getAllFundTypes = res.Data;
                    this._globalURL.isLoading = false;
                    this.reRenderDT();
                }
                else {
                    this.showWarningMessage = true;
                    this.message = this._common_message.WarningMsg;
                    setInterval(() => {
                        this.showWarningMessage = false;
                    }, 5000);
                    this._globalURL.isLoading = false;
                }
            }).catch((res) => {
                this._globalURL.isLoading = false;
                this.message = this._common_message.CatchBlockMsg;
                this.showErrorMessage = true;
                setInterval(() => {
                    this.showErrorMessage = false;
                }, 5000);
            })
    }
    createFundTypes(Data: NgForm): void {
        this._globalURL.isLoading = true;
        let fundType = Data.value.Name
        debugger;
        this._cmrcservice.post((this._globalURL.CREATE_FUNDTYPE + fundType), Data.value, sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.status === 401) {
                    sessionStorage.clear();
                    this.showErrorMessage = true;
                    this.message = this._common_message.error401message;
                    setInterval(() => {
                        this.showErrorMessage = false;
                    }, 5000);
                    this._globalURL.isLoading = false;
                    this._router.navigate(['../login']);
                }
                if (res.Status == undefined)
                    if (res.status == 200) {
                        let data_ = JSON.parse(res._body);
                        if (data_.Status == true) {
                            Data.control.reset();
                            this._globalURL.isLoading = false;
                            this.showMessage = true;
                            this.message = this._common_message.FundTypeCreateMsg;
                            setInterval(() => {
                                this.showMessage = false;
                            }, 5000);
                            this.getFundType();
                        }
                        else {
                            this._globalURL.isLoading = false;
                            this.showErrorMessage = true;
                            this.message = this._common_message.ErrorFundTypeCreateMsg;
                            setInterval(() => {
                                this.showErrorMessage = false;
                            }, 5000);
                        }
                    }
                    else {
                        this._globalURL.isLoading = false;
                        this.showErrorMessage = true;
                        this.message = this._common_message.ErrorFundTypeCreateMsg;
                        setInterval(() => {
                            this.showErrorMessage = false;
                        }, 5000);
                    }
                else {
                    if (res.Status == 1) {
                        Data.control.reset();
                        this._globalURL.isLoading = false;
                        this.showMessage = true;
                        this.message = this._common_message.FundTypeCreateMsg;
                        setInterval(() => {
                            this.showMessage = false;
                        }, 5000);
                        this.getFundType();
                    }
                    else {
                        this._globalURL.isLoading = false;
                        this.showErrorMessage = true;
                        this.message = this._common_message.ErrorFundTypeCreateMsg;
                        setInterval(() => {
                            this.showErrorMessage = false;
                        }, 5000);
                    }
                }
            })
            .catch((err) => {
                this._globalURL.isLoading = false;
                this.message = this._common_message.CatchBlockMsg;
                this.showErrorMessage = true;
                setInterval(() => {
                    this.showErrorMessage = false;
                }, 5000);
            })
    }

    delete(data: any) {
        debugger;
        this.fundTypesModel = data;
        debugger;
    }
    deleteFundTypes(data: any): void {
        this._globalURL.isLoading = true;
        debugger;
        let id = data.Id;
        let final = this._globalURL.DELETE_FUNDTYPE + id;
        this._cmrcservice.post(this._globalURL.DELETE_FUNDTYPE + id, data, sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.status === 401) {
                    sessionStorage.clear();
                    this.showErrorMessage = true;
                    this.message = this._common_message.error401message;
                    setInterval(() => {
                        this.showErrorMessage = false;
                    }, 5000);
                    this._globalURL.isLoading = false;
                    this._router.navigate(['../login']);
                }
                if (res.Status == undefined) {
                    var data = JSON.parse(res._body);
                    if (data.Status == true) {
                        this._globalURL.isLoading = false;
                        this.showMessage = true;
                        this.message = this._common_message.FundTypeDeleteMsg;
                        setInterval(() => {
                            this.showMessage = false;
                        }, 5000);
                        this.getFundType();
                    }
                    else {
                        this._globalURL.isLoading = false;
                        this.showErrorMessage = true;
                        this.message = this._common_message.ErrorFundTypeDeleteMsg;
                        setInterval(() => {
                            this.showErrorMessage = false;
                        }, 5000);
                    }
                }
                else {
                    if (res.Status == true) {
                        this._globalURL.isLoading = false;
                        this.showMessage = true;
                        this.message = this._common_message.FundTypeDeleteMsg;
                        setInterval(() => {
                            this.showMessage = false;
                        }, 5000);
                        this.getFundType();
                    }
                    else {
                        this._globalURL.isLoading = false;
                        this.showErrorMessage = true;
                        this.message = this._common_message.ErrorFundTypeDeleteMsg;
                        setInterval(() => {
                            this.showErrorMessage = false;
                        }, 5000);
                    }
                }
            })
            .catch((err) => {
                this._globalURL.isLoading = false;
                this.message = this._common_message.CatchBlockMsg;
                this.showErrorMessage = true;
                setInterval(() => {
                    this.showErrorMessage = false;
                }, 5000);
            })
    }
    reRenderDT() {
        this.dtElement.dtInstance.then((dtInstance: DataTables.Api) => {
            dtInstance.destroy();
            this.dtTrigger.next();
        });
    }
}



