import { Component, OnInit, ViewChild, AfterViewInit } from '@angular/core';
import { constructor } from 'events';
import { Router } from '@angular/router';
import { AccessToken } from '../../../environments/access_token';
import { GlobalAssets } from '../../../environments/Global';
import { CMRCService } from '../../../services/cmrc.services';
import { GlobalVariable } from '../../../environments/GlobalVeriables';
import { LangulageConf } from '../../../environments/language.config';
import { DataTableDirective } from 'angular-datatables';
import { Subject } from 'rxjs/Subject';
import { NgForm, FormGroup, Form } from '@angular/forms';
import { puts } from 'util';
import { CommonMessageComponent } from '../../../environments/CommanMssage';

@Component({
    templateUrl: '../gender/gender.component.html',
    styleUrls: ['../../../components/app.css']
})

export class GenderComponent implements OnInit, AfterViewInit {
    showWarningMessage: boolean;
    showErrorMessage: boolean;
    public style_Conf: string = "none";
    public display: string;
    @ViewChild(DataTableDirective)
    dtElement: DataTableDirective;
    dtInstance: DataTables.Api;
    public dtTrigger: Subject<any> = new Subject();
    public red: string = "red";
    public ShowErrormsg: boolean;
    public genderModel: any;
    public message: string;
    public showMessage: boolean;
    public getGenders: any;
    public show: boolean;
    constructor(public _cmrcservice: CMRCService,
        public _router: Router,
        public lang: LangulageConf,
        public _access_token: AccessToken,
        public _globalURL: GlobalAssets,
        public _globalVariable: GlobalVariable,
        public _common_message: CommonMessageComponent) {
        _globalURL.isLoading = true;
        this.display = 'none';
        if (sessionStorage.getItem("access_token") == "") {
            _router.navigate(['../login']);
        }
    }

    ngOnInit() {
        this.getGender();
    }

    ngAfterViewInit(): void {
        this.dtTrigger.next();

    }
    //GET AREA AND ...
    getGender(): void {
        this.show = false;
        this._globalURL.isLoading = true;
        debugger;
        this._cmrcservice.get(this._globalURL.GET_GENDER_DETAILS, sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.status === 401) {
                    sessionStorage.clear();
                    this.showErrorMessage = true;
                    this.message = this._common_message.error401message;
                    setInterval(() => {
                        this.showErrorMessage = false;
                    }, 5000);
                    this._globalURL.isLoading = false;
                    this._router.navigate(['../login']);
                }
                if (res.Status == true) {
                    this.getGenders = res.Data;
                    this._globalURL.isLoading = false;
                }
                else {
                    this.showWarningMessage = true;
                    this.message = this._common_message.WarningMsg;
                    setInterval(() => {
                        this.showWarningMessage = false;
                    }, 5000);
                    this._globalURL.isLoading = false;
                }
                this.reRenderDT();
            }).catch((res) => {
                this._globalURL.isLoading = false;
                this.message = this._common_message.CatchBlockMsg;
                this.showErrorMessage = true;
                setInterval(() => {
                    this.showErrorMessage = false;
                }, 5000);
            })
    }

    public showModel() {
        this.show = true;
        //this.display = "block";
    }
    //public GenderForm: FormGroup;
    createGender(Data: NgForm): void {
        this._globalURL.isLoading = true;
        debugger;
        let gender = Data;
        this._cmrcservice.post((this._globalURL.CREATE_GENDER + Data.value.GenderName), Data.value.GenderName, sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.status === 401) {
                    sessionStorage.clear();
                    this.showErrorMessage = true;
                    this.message = this._common_message.error401message;
                    setInterval(() => {
                        this.showErrorMessage = false;
                    }, 5000);
                    this._globalURL.isLoading = false;
                    this._router.navigate(['../login']);
                }
                Data.control.reset();
                if (res.Status == undefined)
                    if (res.status == 200) {
                        let data_ = JSON.parse(res._body);
                        if (data_.Status == true) {
                            this._globalURL.isLoading = false;
                            this.showMessage = true;
                            this.message = this._common_message.AddGenderMsg;
                            setInterval(() => {
                                this.showMessage = false;
                            }, 5000);
                            Data.control.reset();
                            this.getGender();
                        }
                        else {
                            this._globalURL.isLoading = false;
                            this.message = this._common_message.ErrorAddGenderMsg;
                            this.showErrorMessage = true;
                            setInterval(() => {
                                this.showErrorMessage = false;
                            }, 5000);
                        }
                    }
                  
                else {
                    if (res.Status == true) {
                        this._globalURL.isLoading = false;
                        this.showMessage = true;
                        this.message = this._common_message.AddGenderMsg;
                        setInterval(() => {
                            this.showMessage = false;
                        }, 5000);
                        Data.control.reset();
                        this.getGender();
                    }
                    else {
                        this._globalURL.isLoading = false;
                        this.message = this._common_message.ErrorAddGenderMsg;
                        this.showErrorMessage = true;
                        setInterval(() => {
                            this.showErrorMessage = false;
                        }, 5000);
                    }
                }
            }).catch((err) => {
                this._globalURL.isLoading = false;
                this.message = this._common_message.CatchBlockMsg;
                this.showErrorMessage = true;
                setInterval(() => {
                    this.showErrorMessage = false;
                }, 5000);
            })
    }

    delete(data: any) {
        debugger;
        this.genderModel = data;
        debugger;
    }

    deleteGender(data: any): void {
        this._globalURL.isLoading = true;
        debugger;
        let id = data.Id;
        let final = this._globalURL.DELETE_GENDER + id;
        this._cmrcservice.post(final, "data", sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.status === 401) {
                    sessionStorage.clear();
                    this.showErrorMessage = true;
                    this.message = this._common_message.error401message;
                    setInterval(() => {
                        this.showErrorMessage = false;
                    }, 5000);
                    this._globalURL.isLoading = false;
                    this._router.navigate(['../login']);
                }
                if (res.Status == undefined)
                    if (res.status == 200) {
                        let data_ = JSON.parse(res._body);
                        if (data_.Status == true) {
                            this._globalURL.isLoading = false;
                            this.showMessage = true;
                            this.message = this._common_message.GenderDeleteMsg;
                            setInterval(() => {
                                this.showMessage = false;
                            }, 5000);
                            this.getGender();
                        }
                        else {
                            this._globalURL.isLoading = false;
                            this.message = this._common_message.ErrorGenderDeleteMsg;
                            this.showErrorMessage = true;
                            setInterval(() => {
                                this.showErrorMessage = false;
                            }, 5000);
                        }
                    }
                    else {
                        this._globalURL.isLoading = false;
                        this.message = this._common_message.ErrorGenderDeleteMsg;
                        this.showErrorMessage = true;
                        setInterval(() => {
                            this.showErrorMessage = false;
                        }, 5000);
                    }
                else {
                    if (res.status == 200) {
                        this._globalURL.isLoading = false;
                            this.showMessage = true;
                            this.message = this._common_message.GenderDeleteMsg;
                            setInterval(() => {
                                this.showMessage = false;
                            }, 5000);
                            this.getGender();
                    }
                    else {
                        this._globalURL.isLoading = false;
                        this.message = this._common_message.ErrorGenderDeleteMsg;
                        this.showErrorMessage = true;
                        setInterval(() => {
                            this.showErrorMessage = false;
                        }, 5000);
                    }
                }
            }).catch((err) => {
                this._globalURL.isLoading = false;
                this.message = this._common_message.CatchBlockMsg;
                this.showErrorMessage = true;
                setInterval(() => {
                    this.showErrorMessage = false;
                }, 5000);
            })
    }

    reRenderDT() {
        this.dtElement.dtInstance.then((dtInstance: DataTables.Api) => {
            dtInstance.destroy();
            this.dtTrigger.next();
        });
    }
}


