import { Component, OnInit } from '@angular/core';
import { constructor } from 'events';
import { Router } from '@angular/router';
import { AccessToken } from '../../../environments/access_token';
import { GlobalAssets } from '../../../environments/Global';
import { CMRCService } from '../../../services/cmrc.services';
import { CommonMessageComponent } from '../../../environments/CommanMssage';
import { GlobalVariable } from '../../../environments/GlobalVeriables';
import { ExportToPDF } from '../../../services/reports/exportpdf';
import { ExportToExcel } from '../../../services/reports/exportexcel';

@Component({         
    templateUrl: '../reports/scheme_wise_shg_component.html',
})

export class scheme_wise_shg_Component implements OnInit {
    schemes: any;
    public showErrorMessage: boolean;
    public message: any;
    public shgDetails: any;
    public AllCmrcVoMembers: any;
    constructor(public _cmrcservice: CMRCService,
        public _router: Router,
        public _access_token: AccessToken,
        public _common_message: CommonMessageComponent,
        public exportToExcel: ExportToExcel,
        public _globalVariable: GlobalVariable,
        public _globalURL: GlobalAssets,
        public exportToPDF: ExportToPDF) {
        _globalURL.isLoading = true;

        if (sessionStorage.getItem("access_token") == ""){
            _router.navigate(['../login']);
        }
    }
    ngOnInit() {
        this.getSchemeKVP();
        this.viewSHGDetail(0);
    }
    //Get Scheme value pair
    getSchemeKVP(): void {
        debugger;
        this._globalURL.isLoading = true;
        this._cmrcservice.get(this._globalURL.GET_SCHEME_KVP, sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.Status == true) {
                    this.schemes = res.Data;
                    this._globalURL.isLoading = false;
                }
                else {
                    this._globalURL.isLoading = false;
                }
            }).catch((err) => {

            });
    }

    viewSHGDetail(id: number) {
        debugger;
        this._globalURL.isLoading = true;
        this._cmrcservice.get(this._globalURL.GET_SHG_BY_SCHEME + id, sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.Status == true) {
                    this.shgDetails = res.Data;
                    this._globalURL.isLoading = false;
                }
                else {
                    this._globalURL.isLoading = false;
                    this.message = this._common_message.WarningMsg;
                    this.showErrorMessage = true;
                    setInterval(() => {
                        this.showErrorMessage = false;
                    }, 5000);
                }
            }).catch((err) => {
                this._globalURL.isLoading = false;
                this.message = this._common_message.CatchBlockMsg;
                this.showErrorMessage = true;
                setInterval(() => {
                    this.showErrorMessage = false;
                }, 5000);
            });
    }

    R8_AgeWiseSchemeDetailPDF() {
        this._globalURL.isLoading = true;
        this.exportToPDF.R8_AgeWiseSchemeDetails(this.shgDetails);
        this._globalURL.isLoading = false;

    }

    schemeWiseShgExcel(){
        this.exportToExcel.schemeWiseShgExcel(this.shgDetails);
    }
}


































































