import { Component, OnInit, ViewChild, AfterViewInit } from '@angular/core';
import { CurrencyPipe } from '@angular/common';
import { VOService } from '../../../services/vo.services';
import { Router } from '@angular/router';
import { AccessToken } from '../../../environments/access_token';
import { GlobalAssets } from '../../../environments/Global';
import { CMRCService } from '../../../services/cmrc.services';
import { CommonService } from '../../../services/common.service';
import { DataTableDirective } from 'angular-datatables';
//import { Subject } from 'rxjs';
import { GlobalVariable } from '../../../environments/GlobalVeriables';
import { Subject } from 'rxjs';
import { LangulageConf } from '../../../environments/language.config';
import { DatepickerOptions } from 'ng2-datepicker';
import * as frLocale from 'date-fns/locale/en';
import { ExportToPDF } from '../../../services/reports/exportpdf';
import { CommonMessageComponent } from '../../../environments/CommanMssage';

@Component({
    templateUrl: 'finance.component.html'
})

export class VOFinanceComponent implements OnInit {
    public message: any;
    public showWarningMessage: boolean = false;
    public showErrorMessage: boolean;
    public balanceSheetToDt: Date = new Date();
    public balanceSheetFromDt: Date = new Date();    
    options: DatepickerOptions = {
        minYear: 2016,
        maxYear: (new Date().getFullYear() + 1),
        displayFormat: 'MMM D[,] YYYY',
        barTitleFormat: 'MMMM YYYY',
        dayNamesFormat: 'dd',
        firstCalendarDay: 0, // 0 - Sunday, 1 - Monday
        locale: frLocale,
        //minDate: new Date(Date.now()), // Minimal selectable date
        minDate: new Date("2016-03-03"),
        // maxDate: new Date(Date.now()),  // Maximal selectable date
        barTitleIfEmpty: 'Click to select a date'
    };

    public balancesheet: any;
    public financeDetails: any;
    public lastFinanceYearOrFromDt: string;
    public todaysOrToDt: string;
    constructor(public _cmrcService: CMRCService,
        public _voservice: VOService,
        public _globalURL: GlobalAssets,
        public _access_token: AccessToken,
        public _router: Router,
        public lang: LangulageConf,
        public commonService: CommonService,
        public globalvariable: GlobalVariable,
        public exportToPDF: ExportToPDF,
        public _common_message: CommonMessageComponent,
        public router: Router
    ) {
        if (sessionStorage.getItem("access_token") == "")
            router.navigate(['../login']);
    }
    ngOnInit(): void {
        this.getBalanceSheet();
    }
   
    getBalanceSheet() {
        this.lastFinanceYearOrFromDt = "31 MAR " + (new Date().getFullYear() - 1);
        this.todaysOrToDt = this.commonService.getTodaysDate();
        this._globalURL.isLoading = true;
        let balFinalUrl = "";
        if (sessionStorage.getItem("roleId") == "3")
            balFinalUrl = this._globalURL.GET_BALANCESHEET + (sessionStorage.getItem("roleId") == null ? "3" : sessionStorage.getItem("roleId")) + "&frDt=" + this.lastFinanceYearOrFromDt + "&toDt=" + this.todaysOrToDt;
        else
            balFinalUrl = this._globalURL.GET_BALANCESHEET;
        this._voservice.get(balFinalUrl, sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.status === 401) {
                    sessionStorage.clear();
                    this.showErrorMessage = true;
                    this.message = this._common_message.error401message;
                    setInterval(() => {
                        this.showErrorMessage = false;
                    }, 5000);
                    this._globalURL.isLoading = false;
                    this.router.navigate(['../login']);
                }
                if (res.Status == undefined)
                    if (res.status == 200) {
                        this.balancesheet = JSON.parse(res._body).Data;
                        this.globalvariable.CashInHand = this.balancesheet.CashInHandAmount;
                        this.globalvariable.CashAtBank = this.balancesheet.cashAtBankAmount;
                        this.globalvariable.TotalAvailableAmount = this.balancesheet.CashInHandAmount + this.balancesheet.cashAtBankAmount;
                        this._globalURL.isLoading = false;
                    }
                    else {
                        this.message = this._common_message.WarningMsg;
                        this.showWarningMessage = true;
                        setInterval(() => {
                            this.showWarningMessage = false;
                        }, 5000);
                        this._globalURL.isLoading = false;
                    }
                else {
                    if (res.Status == true) {
                        this.balancesheet = res.Data;
                        this.globalvariable.CashInHand = this.balancesheet.CashInHandAmount;
                        this.globalvariable.CashAtBank = this.balancesheet.cashAtBankAmount;
                        this.globalvariable.TotalAvailableAmount = this.balancesheet.CashInHandAmount + this.balancesheet.cashAtBankAmount;
                        this._globalURL.isLoading = false;
                    }
                    else {
                        this.message = this._common_message.WarningMsg;
                        this.showWarningMessage = true;
                        setInterval(() => {
                            this.showWarningMessage = false;
                        }, 5000);
                        this._globalURL.isLoading = false;
                    }
                }
            }).
            catch((res) => {
                this.showErrorMessage = true;
                this.message = this._common_message.CatchBlockMsg;
                setInterval(() => {
                    this.showErrorMessage = false;
                }, 5000);
                this._globalURL.isLoading = false;
            })
    }

    loanBalancesheet(frDt: any, toDt: any) {
        debugger;
        this.lastFinanceYearOrFromDt = this.commonService.convertToDate(frDt);
        this.todaysOrToDt = this.commonService.convertToDate(toDt);
        let balFinalUrl = "";        
        if (sessionStorage.getItem("roleId") == "3")
            balFinalUrl = this._globalURL.GET_BALANCESHEET + (sessionStorage.getItem("roleId") == null ? "3" : sessionStorage.getItem("roleId")) + "&frDt=" + this.lastFinanceYearOrFromDt + "&toDt=" + this.todaysOrToDt
        else
            balFinalUrl = this._globalURL.GET_BALANCESHEET;
        this._voservice.get(balFinalUrl, sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.status === 401) {
                    sessionStorage.clear();
                    this.showErrorMessage = true;
                    this.message = this._common_message.error401message;
                    setInterval(() => {
                        this.showErrorMessage = false;
                    }, 5000);
                    this._globalURL.isLoading = false;
                    this.router.navigate(['../login']);
                }
                if (res.Status == undefined)
                    if (res.status == 200) {
                        frDt.control.reset();
                        toDt.control.reset();
                        this.balancesheet = JSON.parse(res._body).Data;
                        this.globalvariable.CashInHand = this.balancesheet.CashInHandAmount;
                        this.globalvariable.CashAtBank = this.balancesheet.cashAtBankAmount;
                        this.globalvariable.TotalAvailableAmount = this.balancesheet.CashInHandAmount + this.balancesheet.cashAtBankAmount;
                    }
                    else {
                        this.message = this._common_message.WarningMsg;
                        this.showWarningMessage = true;
                        setInterval(() => {
                            this.showWarningMessage = false;
                        }, 5000);
                        this._globalURL.isLoading = false;
                    }
                else {
                    if (res.Status == true) {
                        this.balancesheet = res.Data;
                        this.globalvariable.CashInHand = this.balancesheet.CashInHandAmount;
                        this.globalvariable.CashAtBank = this.balancesheet.cashAtBankAmount;
                        this.globalvariable.TotalAvailableAmount = this.balancesheet.CashInHandAmount + this.balancesheet.cashAtBankAmount;
                    }
                    else {
                        this.message = this._common_message.WarningMsg;
                        this.showWarningMessage = true;
                        setInterval(() => {
                            this.showWarningMessage = false;
                        }, 5000);
                        this._globalURL.isLoading = false;
                    }
                }
            }).catch((res) => {
                this.showErrorMessage = true;
                this.message = this._common_message.CatchBlockMsg;
                setInterval(() => {
                    this.showErrorMessage = false;
                }, 5000);
                this._globalURL.isLoading = false;
            })
    }


    ExportBalancesheetToPdf() {
        this.globalvariable.CashInHand = this.balancesheet.CashInHandAmount;
        this.globalvariable.CashAtBank = this.balancesheet.cashAtBankAmount;
        this.globalvariable.TotalAvailableAmount = this.balancesheet.CashInHandAmount + this.balancesheet.cashAtBankAmount;
        var reportData = {
            "balancesheet": this.balancesheet,
            "CashInHand": this.globalvariable.CashInHand,
            "CashAtBank": this.globalvariable.CashAtBank,
            "TotalAvailableAmount": this.globalvariable.TotalAvailableAmount
        };
        var headerData = {
            "CMRCName": this.globalvariable.CMRCName,
            "CMRCCode": this.globalvariable.cmrcBaseCode,
            "VOName": this.globalvariable.VOName,
            "VoCode": this.globalvariable.cmrcBaseCode,
            "frDt": this.lastFinanceYearOrFromDt,
            "toDt": this.todaysOrToDt
        };
        this.exportToPDF.RExportBalancesheetToPdf(reportData, headerData);
        this._globalURL.isLoading = false;
    }

    naviagateToAccTransaction(glTypeId: number) {
        this.globalvariable.selectedGLTypeId = glTypeId;
        this.router.navigate(['/vo/AccTransaction']);
    }
}


