import { Component, OnInit } from '@angular/core';
import { VOService } from '../../../services/vo.services';
import { AccessToken } from '../../../environments/access_token';
import { GlobalAssets } from '../../../environments/Global';
import { Router } from '@angular/router';
declare var jQuery: any;

@Component({
    templateUrl: '../dashboard/dashboard.component.html'
})
export class SCDashboardComponent implements OnInit {
    public shgLoanDetails: any;
    public memberLoanDetails: any;
    public SHGNameAndId: any;
    public selectedShgId: any;
    public membersNameAndId: any;
    public selectedMemberId: any;
    public memLoanRepaymentDetails: string;
    public Action: string;
    public LoanId: string;
    public repaymentDetails: string;
    public amount: any;
    public totalLoanAmt: any;
    public totalPrinciple; any;
    public totalPayable: any;
    constructor(public _voservice: VOService,
        public _access_token: AccessToken,
        public _globalURL: GlobalAssets,
        public router: Router) {
        _globalURL.isLoading = false;
        this.shgLoanDetails = null;
        this.memberLoanDetails = null;
        this.SHGNameAndId = null;
        this.selectedShgId = null;
        this.membersNameAndId = null;
        this.selectedMemberId = null;
        this.memLoanRepaymentDetails = null;
        this.LoanId = null;
        this.Action = null;
        this.amount = null;
        this.totalLoanAmt = 0;
        this.totalPayable = 0;
        this.totalPrinciple = 0;
        if (sessionStorage.getItem("access_token") == "")
            router.navigate(['../login']);
    }

    ngOnInit() {
        this._globalURL.isLoading = true;
        this.loanSHGLoanData()
    }
    // naviagetToRepayment(memid: any, loanAmt: any) {
    //     this._globalURL.selectedMemId = memid;
    //     this._globalURL.loanAmount = loanAmt;
    //     this.router.navigate(['/vo/loanrepayment']);
    // }
    loanSHGLoanData() {
        debugger;
        this._voservice.get((this._globalURL.MEMBER_LOAN_DETAILS + "" + 2), sessionStorage.getItem("access_token"))
            .then((res) => {
                debugger;
                // this.memberLoanDetails = res.Data;
                this.memLoanRepaymentDetails = null;
                this._globalURL.isLoading = false;
            })
            .catch((res) => {
            })
    }

    save(data: any) {
        debugger;
        this._globalURL.isLoading = true;
        // let fial = (this._globalURL.CALCULATE_REPAYMENT + "?amount=" + this.amount + 
        // "&interest=" + data.interest + "&tenure=" + data.tenure + "&effectiveDate=" + data.effectiveDate);
        this._voservice.get(
            (this._globalURL.CALCULATE_REPAYMENT + "?amount=" + this.amount +
                "&interest=" + data.interest + "&tenure=" + data.tenure + "&effectiveDate=" + data.effectiveDate),
            sessionStorage.getItem("access_token"))
            .then((res) => {
                this.memLoanRepaymentDetails = res.Data;
                for (var item in res.Data) {
                    this.totalLoanAmt = this.totalLoanAmt + res.Data[item].FixedPayableAmt;
                    this.totalPayable = this.totalPayable + res.Data[item].TotalPayableAmt;
                    this.totalPrinciple = this.totalPrinciple + res.Data[item].InterestAmt;
                    debugger;
                }
                this._globalURL.isLoading = false;
                //this.loadLoanRepayment();
            })
    }


    setLoanId(loanId: any, amount: any) {
        this.memLoanRepaymentDetails = null;
        this.LoanId = loanId;
        this.amount = amount;
        this._voservice.get((this._globalURL.GET_REPAYMENT + this.LoanId), sessionStorage.getItem("access_token"))
            .then((res) => {
                debugger;
                this.memLoanRepaymentDetails = res.Data;
            })
            .catch((res) => {

            })

    }

    UpdateStatus() {
        this.Action;
        this.LoanId;
        debugger;
        this._globalURL.isLoading = true;
        this._voservice.get((this._globalURL.UPDATE_STATUS + "?status=" + this.Action + "&loanId=" + this.LoanId), sessionStorage.getItem("access_token"))
            .then((res) => {
                this._globalURL.isLoading = true;
                //this._globalURL.isSaveSuccessMessage = true;
                this.loanSHGLoanData();
            })
    }
}


