import { Component, OnInit, ViewChild, AfterViewInit } from '@angular/core';
import { constructor } from 'events';
import { Router } from '@angular/router';
import { AccessToken } from '../../../environments/access_token';
import { GlobalAssets } from '../../../environments/Global';
import { CMRCService } from '../../../services/cmrc.services';
import { GlobalVariable } from '../../../environments/GlobalVeriables';
import { LangulageConf } from '../../../environments/language.config';
import { DataTableDirective } from 'angular-datatables';
import { Subject } from 'rxjs';
import { CommonMessageComponent } from '../../../environments/CommanMssage';
//import { setInterval } from 'timers';

@Component({
    templateUrl: '../taluka/taluka.component.html',
    styleUrls: ['../../../components/app.css']
})

export class TalukaComponent implements OnInit {
    showErrorMessage: boolean;
    showWarningMessage: boolean;
    @ViewChild(DataTableDirective)
    dtElement: DataTableDirective;
    dtInstance: DataTables.Api;
    public dtTrigger: Subject<any> = new Subject();
    public talukaByDistrictModel: any;
    public TalukasByDistrict: any;
    public ShowErrormsg: boolean;
    public distId: string;
    public getTalukas: any;
    public districtGroups: any;
    public talukaModel: any;
    public getMasterTalukas: any;
    public districtModel: any;
    public getDistricts: any;
    public message: string;
    public showMessage: boolean;
    public ShowTaluka: boolean;
    public ShowTalukaByDistrict: boolean;
    constructor(public _cmrcservice: CMRCService,
        public _router: Router,
        public _access_token: AccessToken,
        public _globalURL: GlobalAssets,
        public lang: LangulageConf,
        public _globalVariable: GlobalVariable,
        public _common_message: CommonMessageComponent
    ) {
        _globalURL.isLoading = true;
        if (sessionStorage.getItem("access_token") == "") {
            _router.navigate(['../login']);
        }
        this.ShowTaluka = true;
        this.ShowTalukaByDistrict = false;
    }

    ngOnInit() {
        this.getTaluka();
        this.getCmrcDistrict();
    }
    ngAfterViewInit(): void {
        this.dtTrigger.next();
        this.getTaluka();
    }
 
    //get Taluka
    getTaluka(): void {
        this.ShowTaluka = true;
        this.ShowTalukaByDistrict = false;
        this._globalURL.isLoading = true;
        debugger;
        this._cmrcservice.get(this._globalURL.GET_TALUKA, sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.Status == true) {
                    this.getTalukas = res.Data;
                    this._globalURL.isLoading = false;
                }
                else {
                    this.showWarningMessage = true;
                    this.message = this._common_message.WarningMsg;
                    setInterval(() => {
                        this.showWarningMessage = false;
                    }, 5000);
                    this._globalURL.isLoading = false;
                }
                 this.reRenderDT();
            }).catch((err) => {
                this._globalURL.isLoading = false;
                this.message = this._common_message.CatchBlockMsg;
                this.showErrorMessage = true;
                setInterval(() => {
                    this.showErrorMessage = false;
                }, 5000);
            })
    }

    //get taluka By District Id
    getTalukaByDistrict(data: any): void {
        this.ShowTaluka = false;
        this.ShowTalukaByDistrict = true;
        this._globalURL.isLoading = true;
        debugger;
        this._cmrcservice.get(this._globalURL.GET_TALUKA_BY_DISTRICT + data, sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.Status == true) {
                    this.TalukasByDistrict = res.Data;
                    this._globalURL.isLoading = false;
                }
                else {
                    this.showWarningMessage = true;
                    this.message = this._common_message.WarningMsg;
                    setInterval(() => {
                        this.showWarningMessage = false;
                    }, 5000);
                    this._globalURL.isLoading = false;
                }
            }).catch((err) => {
                this._globalURL.isLoading = false;
                this.message = this._common_message.CatchBlockMsg;
                this.showErrorMessage = true;
                setInterval(() => {
                    this.showErrorMessage = false;
                }, 5000);
            })
    }

    //get all District...
    getCmrcDistrict(): void {
        this._globalURL.isLoading = true;
        debugger;
        this._cmrcservice.get(this._globalURL.GET_DISTRICT, sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.Status == true) {
                    this.districtGroups = res.Data;
                    this._globalURL.isLoading = false;
                }
                else {
                    this.showWarningMessage = true;
                    this.message = this._common_message.WarningMsg;
                    setInterval(() => {
                        this.showWarningMessage = false;
                    }, 5000);
                    this._globalURL.isLoading = false;
                }
            }).catch((err) => {
                this._globalURL.isLoading = false;
                this.message = this._common_message.CatchBlockMsg;
                this.showErrorMessage = true;
                setInterval(() => {
                    this.showErrorMessage = false;
                }, 5000);
            })
    }
    //Create Taluka...
    createTaluka(data: any): void {
        debugger;
        this._globalURL.isLoading = true;
        let finalUrl = this._globalURL.CREATE_NEW_TALUKA + data.TalukaName + "&disId=" + data.DistrictId;
        this._cmrcservice.post(finalUrl, null, sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.Status == undefined)
                    if (res.status == 200) {
                        let data_ = JSON.parse(res._body);
                        if (data_.Status == true) {
                            this._globalURL.isLoading = false;
                            this.showMessage = true;
                            this.message = this._common_message.AddTalukaMsg;
                            setInterval(() => {
                                this.showMessage = false;
                            }, 5000);
                            this.getTaluka();
                        }
                        else {
                            this._globalURL.isLoading = false;
                            this.showErrorMessage = true;
                            this.message = this._common_message.ErrorAddTalukaMsg;
                            setInterval(() => {
                                this.showErrorMessage = false;
                            }, 5000);
                        }
                    }
                    else {
                        this._globalURL.isLoading = false;
                        this.showErrorMessage = true;
                        this.message = this._common_message.ErrorAddTalukaMsg;
                        setInterval(() => {
                            this.showErrorMessage = false;
                        }, 5000);
                    }
                else {
                    if (res.Status == 1) {
                        this._globalURL.isLoading = false;
                        this.showMessage = true;
                        this.message = this._common_message.AddTalukaMsg;
                        setInterval(() => {
                            this.showMessage = false;
                        }, 5000);
                        this.getTaluka();
                    }
                    else {
                        this._globalURL.isLoading = false;
                        this.showErrorMessage = true;
                        this.message = this._common_message.ErrorAddTalukaMsg;
                        setInterval(() => {
                            this.showErrorMessage = false;
                        }, 5000);
                    }
                }
            }).catch((err) => {
                this._globalURL.isLoading = false;
                this.message = this._common_message.CatchBlockMsg;
                this.showErrorMessage = true;
                setInterval(() => {
                    this.showErrorMessage = false;
                }, 5000);
            })
    }

    //delete Taluka
    delete(data: any) {
        debugger;
        this.talukaModel = data;
        debugger;
    }
    deleteTaluka(data: any): void {
        this._globalURL.isLoading = true;
        debugger;
        let id = data.Id;
        let final = this._globalURL.DELETE_TALUKA + id;
        this._cmrcservice.post(final, "data", sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.Status == undefined) {
                    if (res.status == 200) {
                        this._globalURL.isLoading = false;
                        this.showMessage = true;
                        this.message = "Record is Delete succesfully";
                        setInterval(() => {
                            this.showMessage = false;
                        }, 5000);
                        this.getTaluka();
                    }
                    else {
                        this.ShowErrormsg = true;
                        this.message = "Record is not delete";
                        setInterval(() => {
                            this._globalURL.isSaveSuccessMessage = false;
                            this.ShowErrormsg = false;
                        }, 5000);
                    }
                }
                else {
                    if (res.Status == true) {
                        this._globalURL.isLoading = false;
                        this.showMessage = true;
                        this.message = "Record is Delete succesfully";
                        setInterval(() => {
                            this.showMessage = false;
                        }, 5000);
                        this.getTaluka();
                    }
                    else {
                        this.ShowErrormsg = true;
                        this.message = "Record is not delete";
                        setInterval(() => {
                            this._globalURL.isSaveSuccessMessage = false;
                            this.ShowErrormsg = false;
                        }, 5000);
                    }
                }
            }).catch((err) => {
                this._globalURL.isLoading = false;
                this.message = this._common_message.CatchBlockMsg;
                this.showErrorMessage = true;
                setInterval(() => {
                    this.showErrorMessage = false;
                }, 5000);
            })
    }

    //Delete Taluka by selected district
    deleteTalukaByDistrict(data: any) {
        debugger;
        this.talukaByDistrictModel = data;
        debugger;
    }
    deleteTalukasByDistrict(data: any): void {
        this._globalURL.isLoading = true;
        debugger;
        let id = data.Key;
        let final = this._globalURL.DELETE_TALUKA + id;
        this._cmrcservice.post(final, "data", sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.Status == undefined) {
                    if (res.status == 200) {
                        this._globalURL.isLoading = false;
                        this.showMessage = true;
                        this.message = "Record is Delete succesfully";
                        setInterval(() => {
                            this.showMessage = false;
                        }, 5000);
                        this.getTaluka();
                    }
                    else {
                        this.ShowErrormsg = true;
                        this.message = "Record is not delete";
                        setInterval(() => {
                            this._globalURL.isSaveSuccessMessage = false;
                            this.ShowErrormsg = false;
                        }, 5000);
                    }
                }
                else {
                    if (res.Status == true) {
                        this._globalURL.isLoading = false;
                        this.showMessage = true;
                        this.message = "Record is Delete succesfully";
                        setInterval(() => {
                            this.showMessage = false;
                        }, 5000);
                        this.getTaluka();
                    }
                    else {
                        this.ShowErrormsg = true;
                        this.message = "Record is not delete";
                        setInterval(() => {
                            this._globalURL.isSaveSuccessMessage = false;
                            this.ShowErrormsg = false;
                        }, 5000);
                    }
                }
            })
            .catch((err) => {
                // alert("sooory");
                setInterval(() => {
                }, 5000);
            })
    }
    reRenderDT() {
        this.dtElement.dtInstance.then((dtInstance: DataTables.Api) => {
            dtInstance.destroy();
            this.dtTrigger.next();
        });
    }
}













