import { Component, OnInit } from '@angular/core';
import { VOService } from '../../../services/vo.services';
import { GlobalAssets } from '../../../environments/Global';
import { AccessToken } from '../../../environments/access_token';
import { Router } from '@angular/router';
import { LangulageConf } from '../../../environments/language.config';


@Component({
    templateUrl: '../sc_member/sc_member.component.html'
})

export class SCMemberComponent implements OnInit {
    public scMembers: any;
    public userName: any;
    public password: any;
    public voSCMemberNameAndId_: any;
    public showMessage: boolean;
    public showErrorMessage: any;
    public message: any;
    constructor(public _voService: VOService,
        //public memberModel: MemberModel,
        public _globalURL: GlobalAssets,
        public lang: LangulageConf,
        public _access_token: AccessToken,
        public _router: Router,
    ) {
        if (sessionStorage.getItem("access_token") == ""){
            _router.navigate(['../login']);
        }

    }
    ngOnInit() {
        this.getAllSCMemberDetails();

    }

    // public scMembers: any;
    getAllSCMemberDetails(): void {
        debugger;
        this._globalURL.isLoading = true;
        this._voService.get(this._globalURL.GET_SC_MEMBERS, sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.Status == true)
                    this.scMembers = res.Data;
                this._globalURL.isLoading = false;
            })
            .catch((err) => {
                this.showErrorMessage = true;
                this.message = "Record Does Not Get succesfully";
                setInterval(() => {
                    this.showErrorMessage = false;
                }, 5000);
            })
    }
    getSCMemberKeyValuePairDetails(): void {
        debugger;
        this._globalURL.isLoading = true;
        this._voService.get(this._globalURL.GET_VO_MEMBERS, sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.Status == true)
                    this.voSCMemberNameAndId_ = res.Data;
                this._globalURL.isLoading = false;

            })
            .catch((err) => {
                this.showErrorMessage = true;
                this.message = "Record Does Not Get succesfully";
                setInterval(() => {
                    this.showErrorMessage = false;
                }, 5000);
            })
    }

    public arr = new Array({ key: 0, value: 0 });
    add(key_: any, value_: any) {
        debugger
        this.arr.push({ key: key_.value, value: value_.name });
    }

    createSCMember(data: any) {
        //  let _data = {
        //    'userName': data.userName , 'password': data.passwor &userName=" + "?passoword="
        //  }
        debugger;
        var finalUrl = this._globalURL.CREATE_SC_MEMBER_KEY_VALUE_PAIR + "?userName=" + data.userName + "&password=" + data.password;
        this._voService.post(finalUrl, this.arr, sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.Status == true)
                    this._globalURL.isLoading = false;
                this.getAllSCMemberDetails();
                this.showMessage = true;
                this.message = "Record is Create succesfully";
                setInterval(() => {
                    this.showMessage = false;
                }, 5000);
            })
            .catch((err) => {
                this.showErrorMessage = true;
                this.message = "Record Does Not Create succesfully";
                setInterval(() => {
                    // this._globalURL.isSaveSuccessMessage = false;
                    this.showErrorMessage = false;
                }, 5000);
            })
    }
}


