import { DISTRICTService } from "../../../services/district.services";
import { GlobalAssets } from "../../../environments/Global";
import { CommonService } from "../../../services/common.service";
import { AccessToken } from "../../../environments/access_token";
import { CommonMessageComponent } from "../../../environments/CommanMssage";
import { Router } from "@angular/router";
import { LangulageConf } from "../../../environments/language.config";
import { DistrictMemberModel } from "./member.model.component";
import { Component } from "@angular/core";
import { NgForm } from "@angular/forms";


@Component({
    templateUrl : '../member/member.component.html',
    styleUrls: ['../../../components/app.css']
})

export class DistrictMemberComponent{

    public showWarningMessage: boolean;
    public showMessage: boolean;
    public message: any;
    public showErrorMessage: boolean;
    public selectedShgId: string;
    public memDetails: any;
    public shgs: any;
    public Vos: any;
    constructor(public _districtService: DISTRICTService,
        public _globalURL: GlobalAssets,
        public _commonService: CommonService,
        public _access_token: AccessToken,
        public _commonMessage: CommonMessageComponent,
        public _router: Router,
        public lang: LangulageConf,
        public districtMemberModel: DistrictMemberModel
    ){
    
    }

    ngOnInit() {
        this.getVoIdAndName();
    }


    getVoIdAndName(): void {
        this._globalURL.isLoading = true;
        debugger;
        this._districtService.get(this._globalURL.GET_VOID_NAME_FOR_DISTRICT, sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.Status == true) {
                    this.Vos = res.Data;
                    this._globalURL.isLoading = false;
                }
                else {
                    //  this.ShowErrormsg = true;
                    // this.message = "Vo is not get";
                    setInterval(() => {
                        this._globalURL.isSaveSuccessMessage = false;
                        //  this.ShowErrormsg = false;
                    }, 5000);
                }
            });
    }


    getShgNameAndId(voID: any): void {
        //  let id = data.voId;
        this._globalURL.isLoading = true;
        debugger;
        // let finalUrl = this._globalURL.GET_SHGNAME_ID_VOID + " " + id
        this._districtService.get((this._globalURL.GET_SHGNAME_ID_VOID + "" + voID), sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.Status == true) {
                    this.shgs = res.Data;
                    this._globalURL.isLoading = false;
                }
                else {
                    this._globalURL.isLoading = false;
                }
            });
    }


    getMemberDetails(): void {
        this._globalURL.isLoading = true;
        debugger;
        this._districtService.get((this._globalURL.GET_ALL_MEMBER_DETAILS_BY_SHGID + this.selectedShgId + "&Status="+ 2 ), sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.Status == true) {
                    this.memDetails = res.Data;
                    this._globalURL.isLoading = false;
                }
                else {
                    // this.ShowErrormsg = true;
                    // this.message = "member details is not get";
                    setInterval(() => {
                        this._globalURL.isSaveSuccessMessage = false;
                        // this.ShowErrormsg = false;
                    }, 5000);
                }
                //this.reRenderDT();
            });
    }


    
    setUpdatedValues(data: any) {
        debugger;
        this.districtMemberModel = data;
    }

    updateMember(UpdateMember:NgForm) {
        debugger;
        this._districtService.post(this._globalURL.UPDATE_MEMBER_MASTER, UpdateMember.value, sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.status === 401) {
                    sessionStorage.clear();
                    this.showErrorMessage = true;
                    this.message = this._commonMessage.error401message;
                    setInterval(() => {
                        this.showErrorMessage = false;
                    }, 5000);
                    this._globalURL.isLoading = false;
                    this._router.navigate(['../login']);
                }
                if (res.Status == undefined) {
                    var data = JSON.parse(res._body);
                    if (data.Status == true) {
                        this._globalURL.isLoading = false;
                        this.showMessage = true;
                        this.message = this._commonMessage.MemberUpdateMsg;
                        setInterval(() => {
                            this.showMessage = false;
                        }, 5000);
                       // this.getMemberDetails(this.globalVariable.SelectedSHGID);
                    }
                    else {
                        this.showWarningMessage = true;
                        this.message = this._commonMessage.WarningMsg;
                        setInterval(() => {
                            this.showWarningMessage = false;
                        }, 5000);
                        this._globalURL.isLoading = false;
                    }
                }
                else {
                    if (res.Status == true) {

                    }
                    else {
                        this.showWarningMessage = true;
                        this.message = this._commonMessage.WarningMsg;
                        setInterval(() => {
                            this.showWarningMessage = false;
                        }, 5000);
                        this._globalURL.isLoading = false;
                    }
                }
            }).catch((err) => {
                this._globalURL.isLoading = false;
                this.message = this._commonMessage.CatchBlockMsg;
                this.showErrorMessage = true;
                setInterval(() => {
                    this.showErrorMessage = false;
                }, 5000);
            })
    }




}


