import { Component, ViewChild, OnInit, AfterViewInit, } from '@angular/core';
import { Router } from '@angular/router';
import { AccessToken } from '../../../environments/access_token';
import { GlobalAssets } from '../../../environments/Global';
import { VOService } from '../../../services/vo.services';
import { ExportToPDF } from '../../../services/reports/exportpdf';
import { LangulageConf } from '../../../environments/language.config';
import { ExportToExcel } from '../../../services/reports/exportexcel';
import { DataTableDirective } from 'angular-datatables';
import { Subject } from 'rxjs';
import { CommonMessageComponent } from '../../../environments/CommanMssage';
import { NgForm } from '@angular/forms';
//import { User } from './vo_member.interface';

@Component({
    templateUrl: '../vo_members/vo_members.component.html',
    styleUrls: ['../../../components/app.css']
})

export class VOCMembersComponent implements OnInit, AfterViewInit {
    public checkValidUserName: boolean = true;
    public showSveButton: boolean = true;
    public showWarningMessage: boolean;
    public showMessage: boolean;
    // @ViewChild(DataTableDirective)
    // dtElement: DataTableDirective;
    // dtInstance: DataTables.Api;
    // public dtTrigger: Subject<any> = new Subject();
    public deleterecord: any;
    public validusername: boolean = false;
    public showErrorMessage: boolean = false;
    public vomemberNameAndId_: any;
    public message: any;
    public designations: any;
    public members: any;
    //public user: User;
    public selectedMember: number = 0;
    public selectedDesignation: number = 0;

    constructor(public _voService: VOService,
        //public memberModel: MemberModel,
        public _globalURL: GlobalAssets,
        public _access_token: AccessToken,
        public exportToPDF: ExportToPDF,
        public exportToExcel: ExportToExcel,
        public lang: LangulageConf,
        public _router: Router,
        public _common_message: CommonMessageComponent,

    ) {
        if (sessionStorage.getItem("access_token") == "") {
            _router.navigate(['../login']);
        }

    }
    ngOnInit() {
        this.designations = null;
        this.members = null;
        this.getAllMembersVoDetails();
        this.getAllMembers();
    }
    ngAfterViewInit(): void {
        //this.dtTrigger.next();
        this.getAllMembersVoDetails();
    }

    public getAllMembers() {
        this._voService.get(this._globalURL.GET_VO_MEMBERS, sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.status === 401) {
                    sessionStorage.clear();
                    this.showErrorMessage = true;
                    this.message = this._common_message.error401message;
                    setInterval(() => {
                        this.showErrorMessage = false;
                    }, 5000);
                    this._globalURL.isLoading = false;
                    this._router.navigate(['../login']);
                }
                if (res.Status == undefined)
                    if (res.status == 200) {
                        this._globalURL.isLoading = false;
                        this.members = JSON.parse(res._body).Data;
                    }
                    else {
                        this.showWarningMessage = true;
                        this.message = this._common_message.WarningMsg;
                        setInterval(() => {
                            this.showWarningMessage = false;
                        }, 5000);
                        this._globalURL.isLoading = false;

                    }
                else {
                    if (res.Status == true) {
                        this.members = res.Data;
                        this._globalURL.isLoading = false;
                    }
                    else {
                        this.showWarningMessage = true;
                        this.message = this._common_message.WarningMsg;
                        setInterval(() => {
                            this.showWarningMessage = false;
                        }, 5000);
                        this._globalURL.isLoading = false;
                    }
                }
            }).catch((err) => {
                this._globalURL.isLoading = false;
                this.message = "Unfortunatelly there is a technical problem while getting the details.";
                this.showErrorMessage = true;
                setInterval(() => {
                    this.showErrorMessage = false;
                }, 5000);
            })
    }
    public getDesignations() {
        this._globalURL.isLoading = true;
        this._voService.get(this._globalURL.GET_DESIGNATION_DETAILS + "?forGroup=VO", sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.status === 401) {
                    sessionStorage.clear();
                    this.showErrorMessage = true;
                    this.message = this._common_message.error401message;
                    setInterval(() => {
                        this.showErrorMessage = false;
                    }, 5000);
                    this._globalURL.isLoading = false;
                    this._router.navigate(['../login']);
                }
                if (res.Status == undefined)
                    if (res.status == 200) {
                        this._globalURL.isLoading = false;
                        this.designations = JSON.parse(res._body).Data;
                    }
                    else {
                        this.showWarningMessage = true;
                        this.message = this._common_message.WarningMsg;
                        setInterval(() => {
                            this.showWarningMessage = false;
                        }, 5000);
                        this._globalURL.isLoading = false;
                    }
                else {
                    if (res.Status == true) {
                        this.designations = res.Data;
                        this._globalURL.isLoading = false;
                    }
                    else {
                        this.showWarningMessage = true;
                        this.message = this._common_message.WarningMsg;
                        setInterval(() => {
                            this.showWarningMessage = false;
                        }, 5000);
                        this._globalURL.isLoading = false;
                    }
                }
            }).catch((err) => {
                this._globalURL.isLoading = false;
                this.message = "Unfortunatelly there is a technical problem while getting the details.";
                this.showErrorMessage = true;
                setInterval(() => {
                    this.showErrorMessage = false;
                }, 5000);
            })
    }

    public voMembers: any = null;
    getAllMembersVoDetails(): void {
        debugger;
        this._globalURL.isLoading = true;
        this._voService.get(this._globalURL.GET_ALL_VO_SC_MEMEBER, sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.status === 401) {
                    sessionStorage.clear();
                    this.showErrorMessage = true;
                    this.message = this._common_message.error401message;
                    setInterval(() => {
                        this.showErrorMessage = false;
                    }, 5000);
                    this._globalURL.isLoading = false;
                    this._router.navigate(['../login']);
                }
                if (res.Status == true) {
                    //this.reRenderDT();
                    this.voMembers = res.Data;
                    this._globalURL.isLoading = false;
                }
                else {
                    this.showWarningMessage = true;
                    this.message = this._common_message.WarningMsg;
                    setInterval(() => {
                        this.showWarningMessage = false;
                    }, 5000);
                    this._globalURL.isLoading = false;
                }

            })
            .catch((err) => {
                this._globalURL.isLoading = false;
                this.message = "Unfortunatelly there is a technical problem while getting the details.";
                this.showErrorMessage = true;
                setInterval(() => {
                    this.showErrorMessage = false;
                }, 5000);
            })
    }

    public MemberAndDesignationList: any[] = [];
    public MemberAndDesignationNamesList: any[] = [];

    public data: any = {
        "Key": 0, "Value": 0, "MemberName": "", "DesignationName": ""
    };
    //Key Member Id, and Value = Designation Id
    public _username: string = "";
    public _password: string = "";
    public addMember() {
        debugger;
        this.showSveButton = true;
        this._username = this.userName;
        this._password = this.password;
        this.data = {};
        this.data.Key = this.selectedMember;
        this.data.Value = this.selectedDesignation;
        for (var item in this.members) {
            if (this.selectedMember == this.members[item].Key)
                this.data.MemberName = this.members[item].Value;
        }
        for (var desItem in this.designations) {
            if (this.selectedDesignation == this.designations[desItem].Id)
                this.data.DesignationName = this.designations[desItem].ForGroup + " " + this.designations[desItem].Name;
        }
        this.MemberAndDesignationList.push(this.data);
    }

    recreateMemAndDesArray() {
        debugger;

        for (var item in this.MemberAndDesignationList)
            if (this.members[item].Key == this.MemberAndDesignationList[item].Key)
                this.members.splice(this.MemberAndDesignationList[item], 1);
    }
    removeItem(id: number) {
        this.MemberAndDesignationList.splice(id, 1);
        debugger;
    }

    public arr = new Array({ key: 0, value: 0 });
    add(key_: any, value_: any) {
        debugger
        this.arr.push({ key: key_.value, value: value_.name });
    }

    public userName: string = "";
    public password: string = "";

    createVOMember(ListData: NgForm) {
        debugger;
        if (this.selectedDesignation <= 15 && this.selectedDesignation >= 18) {
            this._username = "";
            this._password = "";
        }
        else {
        }
        var finalUrl = this._globalURL.POST_VO_MEMBER + "?userName=" + this._username + "&password=" + this._password;
        this._voService.post(finalUrl, this.MemberAndDesignationList, sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.status === 401) {
                    sessionStorage.clear();
                    this.showErrorMessage = true;
                    this.message = this._common_message.error401message;
                    setInterval(() => {
                        this.showErrorMessage = false;
                    }, 5000);
                    this._globalURL.isLoading = false;
                    this._router.navigate(['../login']);
                }
                if (res.status == 200) {
                    this._globalURL.isLoading = false;
                    this.getAllMembersVoDetails();
                    this.showMessage = true;
                    this.message = this._common_message.VOBodyCreateMsg;
                    setInterval(() => {
                        this.showMessage = false;
                    }, 5000);
                    ListData.control.reset();
                    this.selectedDesignation = 0;
                    this.selectedMember = 0;
                    this.MemberAndDesignationList = [];
                    this.getAllMembersVoDetails();
                    this.getAllMembers();
                }
                else {
                    this.showErrorMessage = true;
                    this.message = this._common_message.ErrorVOBodyCreateMsg;
                    setInterval(() => {
                        this.showErrorMessage = false;
                    }, 5000);
                    this._globalURL.isLoading = false;
                    this.getAllMembersVoDetails();
                    this.getAllMembers();
                }
            }).catch((err) => {
                this._globalURL.isLoading = false;
                this.message = "Unfortunatelly there is a technical problem while getting the details.";
                this.showErrorMessage = true;
                setInterval(() => {
                    this.showErrorMessage = false;
                }, 5000);
                this.getAllMembersVoDetails();
                this.getAllMembers();
            })
    }
    /***** */
    getMemberVoDetails(): void {
        debugger;
        this._globalURL.isLoading = true;
        this._voService.get(this._globalURL.GET_VO_MEMBERS, sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.status === 401) {
                    sessionStorage.clear();
                    this.showErrorMessage = true;
                    this.message = this._common_message.error401message;
                    setInterval(() => {
                        this.showErrorMessage = false;
                    }, 5000);
                    this._globalURL.isLoading = false;
                    this._router.navigate(['../login']);
                }
                if (res.Status == true) {
                    this.vomemberNameAndId_ = res.Data;
                    this._globalURL.isLoading = false;
                }
                else {
                    this.showWarningMessage = true;
                    this.message = this._common_message.WarningMsg;
                    setInterval(() => {
                        this.showWarningMessage = false;
                    }, 5000);
                    this._globalURL.isLoading = false;
                }
            })
            .catch((err) => {
                this._globalURL.isLoading = false;
                this.message = this._common_message.CatchBlockMsg;
                this.showErrorMessage = true;
                setInterval(() => {
                    this.showErrorMessage = false;
                }, 5000);
            });
    }
    public checkpassword: boolean = true;
    checkConfirmPassword(password: any, confirmpassword: any) {
        debugger;
        if (confirmpassword == password) {
            this.checkpassword = false;
        }
        else {
            this.checkpassword = true;
        }
        //console.log(searchValue);
    }

    checkUserName(userName: any) {
        debugger;
        this._voService.get(this._globalURL.GET_CHECK_USERNAME + userName, sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.status === 401) {
                    sessionStorage.clear();
                    this.showErrorMessage = true;
                    this.message = this._common_message.error401message;
                    setInterval(() => {
                        this.showErrorMessage = false;
                    }, 5000);
                    this._globalURL.isLoading = false;
                    this._router.navigate(['../login']);
                }
                if (res.Status == true) {
                    this.validusername = true;
                    this.checkValidUserName = false;
                }
                else {
                    this.validusername = false;
                    this.checkValidUserName = true;
                }
            }).catch((err) => {
                this._globalURL.isLoading = false;
                this.message = this._common_message.CatchBlockMsg;
                this.showErrorMessage = true;
                setInterval(() => {
                    this.showErrorMessage = false;
                }, 5000);
            })
    }

    fordelete(data: any) {
        debugger;
        this.deleterecord = data;
    }
    removeVOMember(id: number) {
        debugger;
        this._globalURL.isLoading = true;
        this._voService.get(this._globalURL.REMOVE_SELECTED_VO_MEMBER + id, sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.status === 401) {
                    sessionStorage.clear();
                    this.showErrorMessage = true;
                    this.message = this._common_message.error401message;
                    setInterval(() => {
                        this.showErrorMessage = false;
                    }, 5000);
                    this._globalURL.isLoading = false;
                    this._router.navigate(['../login']);
                }
                if (res.Status == undefined)
                    if (res.status == 200) {
                        this._globalURL.isLoading = false;
                        this.message = this._common_message.RemoveMemberMsg;
                        this.showMessage = true;
                        setInterval(() => {
                            this.showMessage = false;
                        }, 5000);
                    }
                    else {
                        this.showWarningMessage = true;
                        this.message = this._common_message.ErrorRemoveMemberMsg;
                        setInterval(() => {
                            this.showWarningMessage = false;
                        }, 5000);
                        this._globalURL.isLoading = false;
                    }
                if (res.Status == true) {
                    this._globalURL.isLoading = false;
                    this.message = this._common_message.RemoveMemberMsg;
                    this.showMessage = true;
                    setInterval(() => {
                        this.showMessage = false;
                    }, 5000);
                }
                else {
                    this.showErrorMessage = true;
                    this.message = this._common_message.ErrorRemoveMemberMsg;
                    setInterval(() => {
                        this.showErrorMessage = false;
                    }, 5000);
                    this._globalURL.isLoading = false;
                }
                this.getAllMembersVoDetails();
            })
    }

    R38_voMemberSCExportTopdf() {
        this._globalURL.isLoading = true;
        debugger;
        this.exportToPDF.R38_voMemberSCExportTopdf(this.voMembers);
        this._globalURL.isLoading = false;
    }

    voMemberExcel() {
        debugger
        this.exportToExcel.voMembersExcel(this.voMembers);
    }

    selectdesignation() {
        this.showSveButton = false;
    }

    // reRenderDT() {
    //     this.dtElement.dtInstance.then((dtInstance: DataTables.Api) => {
    //         dtInstance.destroy();
    //         this.dtTrigger.next();
    //     });
    // }
}


