import { Component, OnInit } from '@angular/core';
import { constructor } from 'events';
import { Router } from '@angular/router';
import { AccessToken } from '../../../environments/access_token';
import { GlobalAssets } from '../../../environments/Global';
import { CMRCService } from '../../../services/cmrc.services';
import { ExportToPDF } from '../../../services/reports/exportpdf';
import { ExportToExcel } from '../../../services/reports/exportexcel';
//import { AllCmrcActiveMemberModel } from './all_active_members.model.component';

@Component({
    templateUrl: '../reports/members_report.component.html'
})

export class ActiveAndWithdrawlMembersComponent implements OnInit {
    public allActiveCmrcMembers: any;
    public AllCmrcActiveMemberModel: any;
    public showActiveMembers: boolean;
    public showWithdrawlMembers: boolean;
    public status: number;
    constructor(public _cmrcservice: CMRCService,
        public _router: Router,
        public _access_token: AccessToken,
        public _globalURL: GlobalAssets,
        public exportToExcel: ExportToExcel,
        public exportToPDF: ExportToPDF
    ) {
        _globalURL.isLoading = true;

        if (sessionStorage.getItem("access_token") == ""){
            _router.navigate(['../login']);
        }
        //this.showActiveMembers = true;
        // this.showWithdrawlMembers = false;

    }
    ngOnInit() {
        this.status = 1;
        this.getAllActiveCmrcMembers(this.status);
    }

    //GET ALL CMRC MEMBERS....//
    getAllActiveCmrcMembers(status: number): void {
        debugger;
        this.status = status;
        //  this.showActiveMembers = true;        
        this._globalURL.isLoading = true;
        this.allActiveCmrcMembers = null;
        this._cmrcservice.get(this._globalURL.GET_ALL_MEMBERS_BY_CMRCID + status, sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.Status == true) {
                    this.allActiveCmrcMembers = res.Data;
                    this._globalURL.isLoading = false;
                }
                else {
                    this._globalURL.isLoading = false;
                }
            });
    }

    public viewData: any = null;
    viewSHGDetail(data: any): void {
        debugger;
        this.viewData = data;
    }

    R16_MemberProfileAtCmrcLevel() {
        this._globalURL.isLoading = true;
        this.exportToPDF.R16_MemberProfileAtCmrcLevel(this.allActiveCmrcMembers, this.status == 1 ? "ACTIVE" : "WITHDRAWAL");
        this._globalURL.isLoading = false;
    }

    ActivrOrWithdrolMemExcel() {
        debugger;
        this._globalURL.isLoading = true;
        this.exportToExcel.ActivrOrWithdrolMemExcel(this.allActiveCmrcMembers, this.status == 1 ? "ACTIVE" : "WITHDRAWAL");
        this._globalURL.isLoading = false;
    }

}
























